import Vue from 'vue'

const days = 86400,
	  hours = 3600,
	  trx = 1e6,
	  usd = 1e6,
	  mc = 1e6;

// 订单创建以后，需要等待进入下阶段的时间
Vue.prototype.WaitTime = 3 * days;    // 订单尾款支付截止的周期
Vue.prototype.PaymentCountDownSec = 72 * hours;

// 冻结期时间最小周期
Vue.prototype.ForzenTimesMin = 5 * days;

// 冻结期时间最大周期
Vue.prototype.ForzenTimesMax = 25 * days;

// P1阶段每日收益率
Vue.prototype.ProfitPropP1 = 0.01;

// P2阶段总收益
Vue.prototype.ProfitPropTotalP2 = 0.08;

// 订单创建时间间隔
Vue.prototype.OrderCreateInterval = 72 * hours;

// 每次参与后额度增量
Vue.prototype.OrderAmountAppendQuota = 2000 * usd;

// 订单最小额度限制(USDT mwei)
Vue.prototype.OrderAmountMinLimit = 100 * usd;

// 订单最大额度限制(USDT mwei)
Vue.prototype.OrderAmountMaxLimit = 10000 * usd;

// 金额是几的倍数
Vue.prototype.OrderAmountGranularity = 10 * usd;

// 订单最小支付比例 0.5 * 10 ** 12 表示 50%
Vue.prototype.OrderPaymentedMinPart = 0.6;

// 从合约转出USDT时，需要等值的DT的手续费比例
Vue.prototype.WithdrawCostProp = 0.022;

// 手续费比例关系, 1 USDT : 20 Mc
//      1e6(wei) : 20e6(wei)
//    = 1e6 : 2e7
//    = 1 : 2e1
//    = 20e13
// Vue.prototype.USDTToDTProp = 50;

// 每日投入上限
// Vue.prototype.DepositedUSDMaxLimit = 1000000 * usd;

// 黑名单解除需要消耗的DT数量
Vue.prototype.ResolveBreakerDTAmount = 1000 * mc;