<template>
	<div class="box friendsListBox">
		<div class="myInviter">
			<span>{{$t('MyInviter')}}：</span>
			<span>{{GetIntroducerCode}}</span>
		</div>
		<div class="header">
			<div
			  v-for="(item, index) in history"
			  :key="index"
			  :class="{active: active == item.name}"
			  @click="viewHistory(index,item.name)"
			><span>{{ item.name }}</span></div>
		</div>
		<div class="content">
			<div
			  v-for="(item, index) in list"
			  :key="index"
			  @click="addHistory(item.name, item.address)"
			>
				<div>
					<div v-if="item.grade > 0">
						<span>{{ item.name }}</span>
						<img v-lazy="require(`@/assets/img/upLv0${item.grade}.png`)">
						<span>(V{{ item.grade }})</span>
					</div>
					<div v-else>
						<span>{{ item.name }}</span>
						<img v-lazy="require('@/assets/img/upLv00.png')">
					</div>
					<span>{{ item.code }}</span>
				</div>
				<div>
					<span>{{ item.amount }} <span>USDT</span></span>
					<img v-lazy="require('@/assets/img/next.png')">
				</div>
			</div>
			<not-record v-if="!list.length" :text="NoFriendsRecord"></not-record>
		</div>
	</div>
</template>

<script>
	import notRecord from '@/components/pubilc/not-record'
	
	export default {
		components: { notRecord },
		data() {
			return {
				title: this.$t('FriendsList'),
				list: [
					// { name: '001', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '002', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '003', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '004', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '005', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '006', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '007', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '008', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '009', code: '123456', grade: 1, amount: '0.12', address: '123' },
				],
				history: [
					
				],
				NoFriendsRecord: this.$t('NoFriendsRecord'),
				active: '',
				GetIntroducerCode: '',
			}
		},
		created() {
			this.$tronWeb && this.init()
		},
		methods: {
			init() {
				this.getData()
				
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'getIntroducer(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(async result=>{
					const res = result.constant_result[0].decode(['address'])
				
					let GetIntroducerCode = (await this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'addressToShortCode(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: res }]
					}).then()).constant_result[0].decode(['bytes6']);
				
					if(parseInt(GetIntroducerCode,16)){
						this.GetIntroducerCode = GetIntroducerCode.hexToString()
					}else{
						this.GetIntroducerCode = ''
					}
				})
			},
			getData() {
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'addressToNickName(address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: this.$address }]
				}).then( result => {
					const res = result.constant_result[0].decode(['bytes16']);
					let name = res.hexToString(true) || 'BOBO';
					
					this.active = name
					this.history.push({
						name: name,
						address: this.$address
					})
				})
				this.getList(this.$address)
			},
			getList(address) {
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'recommendList(address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: address }]
				}).then(result => {
					let res = result.constant_result[0].decode(['address[]']),
						list = [];
					res.forEach(item => {
						Promise.all([
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'addressToNickName(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'addressToShortCode(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.OrderManager.address,
								method: 'userInfoMapping(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: item }]
							}),
							this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'levelOf(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: item }]
							})
						]).then(([ name, code , userInfoMapping , lv ]) => {
							let nameDecode = name.constant_result[0].decode(['bytes16']),
								codeDecode = code.constant_result[0].decode(['bytes6']);
								// infoDecode = info.constant_result[0].decode(['bool','uint256','uint256','uint256'],['isvaild','vaildMemberTotal','selfAchievements','dlevel']);
							
							const { totalIn, totalOut, awardQuota , awardWithdrawableTotal } = userInfoMapping.constant_result[0].decode(
								['uint256', 'uint256', 'uint256', 'uint256'],
								['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal']
							);
							
							list.push({
								name:  nameDecode.hexToString().replace(/[^\u4e00-\u9fa5a-zA-Z\d,\.，。]+/,''),
								code: codeDecode.hexToString(),
								grade: lv.constant_result[0].decode(['uint256']).toNumber(),
								amount: totalIn.toNumber().fromSun(),
								address: item
							})
						})
					})

					this.list = list
				})
			},
			viewHistory(index , name) {
				this.getList(this.history[index].address)
				this.history = this.history.slice(0, index + 1)
				this.active = name
			},
			addHistory(name, address) {
				this.getList(address)
				this.history.push({ name, address })
				this.active = name
				setTimeout(() => {
					document.querySelector('.header>div:last-child').scrollIntoView({
						behavior: 'smooth'
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		// padding-top: 40px;
		background: transparent;
		overflow: hidden;
	}
	.myInviter{
		width: 100%;
		height: 104px;
		background-color: #65B7B4;
		color: #FFFFFF;
		font-size: 32px;
		padding-left: 40px;
		display: flex;
		align-items: center;
	}
	.content {
		width: 100vw;
		height: calc(100vh - 80px - 104px);
		overflow-y: auto;
		position: relative;
		padding: 20px 0 0 30px;
		background-color: #fff;
		margin-bottom: 0;
		&>div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-right: 40px;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 2px solid #AFAFAF;
			&>div {
				display: flex;
				&:first-child {
					color: #AFAFAF;
					font-size: 28px;
					flex-direction: column;
					align-items: flex-start;
					&>div {
						display: flex;
						align-items: center;
						margin-bottom: 20px;
						&>span {
							&:first-child {
								color: #656363;
								font-size: 32px;
							}
							&:last-child {
								color: #AFAFAF;
							}
						}
						&>img {
							width: 38px;
							height: 38px;
							margin: 0 19px;
						}
					}
				}
				&:last-child {
					align-items: center;
					&>span {
						color: #AFAFAF;
						font-size: 32px;
						&>span {
							font-size: 20px;
							margin-right: 18px;
						}
					}
					&>img {
						width: 15px;
						height: 30px;
					}
				}
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}
	.header {
		width: 100vw;
		height: 80px;
		font-size: 0;
		text-align: left;
		line-height: 80px;
		white-space: nowrap;
		margin-bottom: 20px;
		overflow: auto hidden;
		background-color: #fff;
		&::-webkit-scrollbar {
			display: none;
		}
		&>div {
			display: inline-block;
			width: 200px;
			height: 80px;
			color: #4E4E4E;
			font-size: 28px;
			margin-right: 5px;
			text-align: center;
			position: relative;
			background-color: #EBEAEA;
			&>span{
				position: absolute;
				z-index: 2;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
			}
			&::before, &::after {
				content: '';
				width: 80px;
				height: 80px;
				position: absolute;
				top: 0px;
				transform: rotate(45deg);
			}
			&::before {
				left: -21px;
				background-color: #EBEAEA;
			}
			&::after {
				z-index: 1;
				right: -30px;
				background-color: #EBEAEA;
			}
			
			&:first-child::before {
				display: none;
			}
		}
		&>div:not(:nth-child(1)){
			&>span{
				left: 60%;
				top: 50%;
				transform: translate(-60%,-50%);
			}
		}
		&>.active {
			background-color: #65B7B4;
			color: #fff;
			
			&::before {
				background-color: #65B7B4;
			}
			&::after {
				background-color: #65B7B4;
			}
		}
	}
</style>
