on authorizeOperator(address operator) external;\n    function revokeOperator(address operator) external;\n\n    function send(address to, uint amount, bytes calldata data) external;\n    function operatorSend(address from, address to, uint amount, bytes calldata data, bytes calldata operatorData) external;\n\n    function burn(uint amount, bytes calldata data) external;\n    function operatorBurn(address from, uint amount, bytes calldata data, bytes calldata operatorData) external;\n\n    event Sent(address indexed operator, address indexed from, address indexed to, uint amount, bytes data, bytes operatorData);\n    event Minted(address indexed operator, address indexed to, uint amount, bytes data, bytes operatorData);\n    event Burned(address indexed operator, address indexed from, uint amount, bytes data, bytes operatorData);\n    event AuthorizedOperator(address indexed operator, address indexed holder);\n    event RevokedOperator(address indexed operator, address indexed holder);\n}\n\ncontract NoderStorage is KStorage {\n\n    struct Info {\n        uint totalWithdrawable;\n        uint quota;\n        uint latestBoundTime;\n    }\n\n    mapping(address => Info) public infomations;\n\n    uint public latestBonusTime;\n\n    Pool internal _poolInterface;\n    iERC20 internal _usdtInterface;\n    Relations internal _rlsInterface;\n\n    event Log_Bounds(uint indexed time, uint indexed targetLv, uint amount);\n\n    constructor(Pool poolInc, iERC20 usdtInc, Relations rlsInc) public {\n        _poolInterface = poolInc;\n        _usdtInterface = usdtInc;\n        _rlsInterface = rlsInc;\n        latestBonusTime = now;\n    }\n}\n\ncontract Noder is NoderStorage {\n\n    constructor() public NoderStorage(Pool(0), iERC20(0), Relations(0)) {\n\n    }\n\n    function withdrawAward() external {\n        _usdtInterface.transfer(msg.sender, infomations[msg.sender].quota);\n        infomations[msg.sender].totalWithdrawable += infomations[msg.sender].quota;\n        infomations[msg.sender].quota = 0;\n    }\n\n    function doBounds() external KOwnerOnly returns (uint) {\n\n        uint totalRward = _poolInterface.allowance(address(this));\n\n        _poolInterface.operatorSend(address(this), totalRward);\n\n        for (uint i = 2; i <= 5; i++) {\n\n            address[] memory lvAddresses = _rlsInterface.levelListOf(i);\n\n            if ( lvAddresses.length <= 0 ) {\n                break;\n            }\n\n            uint lvAward = totalRward / 4 / lvAddresses.length;\n\n            for ( uint j = 0; j < lvAddresses.length; j++ ) {\n\n                Info storage info = infomations[lvAddresses[j]];\n\n                if (info.latestBoundTime != now) {\n                    info.quota = lvAward;\n                    info.latestBoundTime = now;\n                } else {\n                    info.quota += lvAward;\n                }\n\n            }\n\n            emit Log_Bounds(timestemp() / 1 days * 1 days, i, lvAward);\n        }\n\n        latestBonusTime = now / 1 days * 1 days;\n    }\n\n}\n\n\ncontract ERC777_1TokenStorage is KStorage {\n\n    using SafeMath for uint;\n\n    address[] internal _defaultOperators;\n\n    mapping (address => uint) internal _balances;\n    mapping (address => mapping(address => uint)) internal _allowances;\n    mapping (address => mapping(address => bool)) internal _authorized;\n\n    string public name;\n    string public symbol;\n    uint8 public decimals = 18;\n    uint public totalSupply;\n    uint public granularity = 1;\n\n    constructor(\n        string memory _name,\n        string memory _symbol,\n        uint8 _decimals,\n        uint _totalSupply\n    ) public {\n        name = _name;\n        symbol = _symbol;\n        decimals = _decimals;\n        totalSupply = _totalSupply;\n\n        _balances[address(this)] = _totalSupply;\n        _defaultOperators.push(msg.sender);\n    }\n}\n\ncontract ERC777_1Token is iERC777_1, ERC777_1TokenStorage {\n\n    constructor() public ERC777_1TokenStorage(\"\", \"\", 0, 0) {\n\n    }\n\n    function balanceOf(address account) external view returns (uint) {\n        return _balances[account];\n    }\n\n    function allowance(address owner, address spender) external view returns (uint) {\n        return _allowances[owner][spender];\n    }\n\n    function transfer(address recipient, uint amount) external KWhenNotPaused returns (bool) {\n        _send(msg.sender, recipient, amount, \"\", msg.sender, \"\");\n        return true;\n    }\n\n    function approve(address spender, uint value) external KWhenNotPaused returns (bool) {\n        _approve(msg.sender, spender, value);\n        return true;\n    }\n\n    function transferFrom(address sender, address recipient, uint amount) external KWhenNotPaused returns (bool) {\n        require(amount <= _allowances[sender][msg.sender]);\n        _allowances[sender][msg.sender] = _allowances[sender][msg.sender].sub(amount);\n        _send(sender, recipient, amount, \"\", msg.sender, \"\");\n        return true;\n    }\n\n    function increaseAllowance(address spender, uint addedValue) external KWhenNotPaused returns (bool) {\n        _approve(msg.sender, spender, _allowances[msg.sender][spender].add(addedValue));\n        return true;\n    }\n\n    function decreaseAllowance(address spender, uint subtractedValue) external KWhenNotPaused returns (bool) {\n        _approve(msg.sender, spender, _allowances[msg.sender][spender].sub(subtractedValue, \"ERC20: decreased allowance below zero\"));\n        return true;\n    }\n\n    function addDefaultOperators(address owner) external KOwnerOnly returns (bool) {\n        _defaultOperators.push(owner);\n    }\n\n    function removeDefaultOperators(address owner) external KOwnerOnly returns (bool) {\n        for (uint i = 0; i < _defaultOperators.length; i++) {\n            if ( _defaultOperators[i] == owner ) {\n                for (uint j = i; j < _defaultOperators.length - 1; j++) {\n                    _defaultOperators[j] = _defaultOperators[j+1];\n                }\n                delete _defaultOperators[_defaultOperators.length - 1];\n                _defaultOperators.length --;\n                return true;\n            }\n        }\n        return false;\n    }\n\n    function defaultOperators() external view returns (address[] memory) {\n        return _defaultOperators;\n    }\n\n    function authorizeOperator(address _operator) external {\n        require(_operator != msg.sender);\n        _authorized[_operator][msg.sender] = true;\n        emit AuthorizedOperator(_operator, msg.sender);\n    }\n\n    function revokeOperator(address _operator) external {\n        require(_operator != msg.sender);\n        _authorized[_operator][msg.sender] = false;\n        emit RevokedOperator(_operator, msg.sender);\n    }\n\n    function send(address _to, uint _amount, bytes calldata _userData) external {\n        _send(msg.sender, _to, _amount, _userData, msg.sender, \"\");\n    }\n\n    function isOperatorFor(address _operator, address _tokenHolder) public view returns (bool) {\n        for (uint i = 0; i < _defaultOperators.length; i++) {\n            if ( _defaultOperators[i] == _operator )  {\n                return true;\n            }\n        }\n        return _operator == _tokenHolder || _authorized[_operator][_tokenHolder];\n    }\n\n    function operatorSend(address _from, address _to, uint _amount, bytes calldata _userData, bytes calldata _operatorData) external {\n        require( isOperatorFor(msg.sender, _from), \"NotAuthorized\" );\n        _send(_from, _to, _amount, _userData, msg.sender, _operatorData);\n    }\n\n    function mint(address _tokenHolder, uint _amount, bytes calldata _operatorData) external KOwnerOnly {\n        totalSupply = totalSupply.add(_amount);\n        _balances[_tokenHolder] = _balances[_tokenHolder].add(_amount);\n        emit Minted(msg.sender, _tokenHolder, _amount, \"\", _operatorData);\n    }\n\n    function burn(uint _amount, bytes calldata _data) external {\n        _send(msg.sender, address(0x0), _amount, _data, msg.sender, \"\");\n    }\n\n    function operatorBurn(\n        address _from,\n        uint _amount,\n        bytes calldata _data,\n        bytes calldata _operatorData\n    ) external {\n        require(isOperatorFor(msg.sender, _from), \"NotAuthorized\");\n\n        if ( totalSupply <= 10000000e6 ) {\n            _send(_from, KContractOwners[0], _amount, _data, msg.sender, _operatorData);\n        } else {\n            _send(_from, address(0x0), _amount, _data, msg.sender, _operatorData);\n        }\n    }\n\n    function _send(\n        address _from,\n        address _to,\n        uint _amount,\n        bytes memory _userData,\n        address _operator,\n        bytes memory _operatorData\n    ) internal {\n\n\n        _balances[_from] = _balances[_from].sub(_amount);\n        _balances[_to] = _balances[_to].add(_amount);\n\n        if ( _to == address(0) ) {\n            totalSupply -= _amount;\n            emit Burned(_operator, _from, _amount, _userData, _operatorData);\n        } else {\n            emit Sent(_operator, _from, _to, _amount, _userData, _operatorData);\n        }\n\n        emit Transfer(_from, _to, _amount);\n    }\n\n    function _approve(address owner, address spender, uint value) internal {\n        require(owner != address(0), \"ERC20: approve from the zero address\");\n        require(spender != address(0), \"ERC20: approve to the zero address\");\n\n        _allowances[owner][spender] = value;\n        emit Approval(owner, spender, value);\n    }\n}\n\n\ncontract CompensateStorage is KStorage {\n\n    struct Compensate {\n        uint total;\n        uint currentWithdraw;\n        uint latestWithdrawTime;\n    }\n    mapping(address => Compensate) public compensateMapping;\n    iERC777_1 internal _mctInterface;\n\n    constructor(\n        iERC777_1 erc777Inc\n    ) public {\n        _mctInterface = erc777Inc;\n    }\n}\n\ncontract Compensate is CompensateStorage {\n\n    constructor() public CompensateStorage(iERC777_1(0)) {}\n\n    event Log_CompensateCreated(address indexed owner, uint when, uint compensateAmount);\n\n    event Log_CompensateRelase(address indexed owner, uint when, uint relaseAmount);\n\n    function increaseCompensateAmountDelegate(address owner, uint amount) external KDelegateMethod {\n        compensateMapping[owner].total += amount;\n\n        if ( compensateMapping[owner].latestWithdrawTime == 0 ) {\n            compensateMapping[owner].latestWithdrawTime = timestemp() / 1 days * 1 days;\n        }\n\n        emit Log_CompensateCreated(msg.sender, timestemp(), amount);\n    }\n\n    function withdrawCompensate() external returns (uint amount) {\n\n        Compensate storage c = compensateMapping[msg.sender];\n\n        if ( c.total == 0 || c.currentWithdraw >= c.total ) {\n            return 0;\n        }\n        uint deltaDay = (timestemp() - c.latestWithdrawTime) / 1 days;\n        if ( deltaDay > 0 ) {\n            amount = (c.total * 0.01e12 / 1e12) * deltaDay;\n        } else {\n            return 0;\n        }\n\n        if ( (amount + c.currentWithdraw) > c.total ) {\n            amount = c.total - c.currentWithdraw;\n        }\n\n        if ( amount > 0 ) {\n            c.currentWithdraw += amount;\n            c.latestWithdrawTime = timestemp() / 1 days * 1 days;\n\n            _mctInterface.operatorSend(\n                address(_mctInterface),\n                msg.sender,\n                amount,\n                \"\",\n                \"CompensateRelease\"\n            );\n\n            emit Log_CompensateRelase(msg.sender, timestemp(), amount);\n        }\n\n    }\n\n}\n\n\ncontract RelationsStorage is KStorage {\n\n    enum AddRelationError {\n        NoError,\n        CannotBindYourSelf,\n        AlreadyBinded,\n        ParentUnbinded,\n        ShortCodeExisted\n    }\n\n    address public rootAddress = address(0xdead);\n\n    uint public totalAddresses;\n\n    mapping (address => address) internal _recommerMapping;\n\n    mapping (address => address[]) internal _recommerList;\n\n    mapping (bytes6 => address) internal _shortCodeMapping;\n\n    mapping (address => bytes6) internal _addressShotCodeMapping;\n\n    mapping (address => bytes16) internal _nickenameMapping;\n\n    mapping (address => uint) internal _depthMapping;\n\n    mapping (address => uint8) internal _levelMapping;\n\n    mapping (address => uint8) internal  _chilrenLevelMaxMapping;\n\n    mapping (uint => address[]) internal _levelsList;\n\n    constructor() public {\n        _shortCodeMapping[0x305844454144] = rootAddress;\n        _addressShotCodeMapping[rootAddress] = 0x305844454144;\n\n        _recommerMapping[rootAddress] = address(0xdeaddead);\n    }\n}\n\ncontract Relations is RelationsStorage {\n\n\n    function levelOf(address owner) external view returns (uint) {\n        return _levelMapping[owner];\n    }\n\n    function levelDistribution(address owner, uint maxLimit) external view returns (uint[] memory distribution) {\n\n        address[] storage directAddresses = _recommerList[owner];\n\n        distribution = new uint[](maxLimit + 1);\n\n        for ( uint i = 0; i < directAddresses.length; i++) {\n            uint lv = _chilrenLevelMaxMapping[directAddresses[i]];\n            if ( lv <= maxLimit ) {\n                distribution[lv]++;\n            }\n        }\n    }\n\n    function getIntroducer(address owner) external view returns (address) {\n        return _recommerMapping[owner];\n    }\n\n    function getForefathers2(address owner, uint depth, uint endLevel) external view returns (uint[] memory seq, address[] memory fathers) {\n\n        seq = new uint[](endLevel + 1);\n        fathers = new address[](endLevel + 1);\n        uint seqOffset = 0;\n        address parent = _recommerMapping[owner];\n\n        for (\n            uint i = 0;\n            ( i < depth && parent != address(0x0) && parent != rootAddress );\n            ( i++, parent = _recommerMapping[parent] )\n        ) {\n            uint lv = uint(_levelMapping[parent]);\n\n            if ( fathers[lv] == address(0) ) {\n                fathers[lv] = parent;\n                seq[seqOffset++] = uint(lv);\n            }\n\n            if ( lv >= endLevel + 1 ) {\n                break;\n            }\n        }\n\n    }\n\n    function getForefathers(address owner, uint depth) external view returns (address[] memory, uint8[] memory) {\n\n        address[] memory forefathers = new address[](depth);\n        uint8[] memory levels = new uint8[](depth);\n\n        for (\n            (address parent, uint i) = (_recommerMapping[owner], 0);\n            i < depth && parent != address(0) && parent != rootAddress;\n            (i++, parent = _recommerMapping[parent])\n        ) {\n            forefathers[i] = parent;\n            levels[i] = _levelMapping[parent];\n        }\n\n        return (forefathers, levels);\n    }\n\n    function recommendList(address owner) external view returns (address[] memory) {\n        return (_recommerList[owner]);\n    }\n\n    function shortCodeToAddress(bytes6 shortCode) external view returns (address) {\n        return _shortCodeMapping[shortCode];\n    }\n\n    function addressToShortCode(address addr) external view returns (bytes6) {\n        return _addressShotCodeMapping[addr];\n    }\n\n    function addressToNickName(address addr) external view returns (bytes16) {\n        return _nickenameMapping[addr];\n    }\n\n    function depth(address addr) external view returns (uint) {\n        return _depthMapping[addr];\n    }\n\n    function registerShortCode(bytes6 shortCode) external returns (bool) {\n\n        if ( _shortCodeMapping[shortCode] != address(0x0) ) {\n            return false;\n        }\n\n        if ( _addressShotCodeMapping[msg.sender] != bytes6(0x0) ) {\n            return false;\n        }\n\n        _shortCodeMapping[shortCode] = msg.sender;\n        _addressShotCodeMapping[msg.sender] = shortCode;\n\n        return true;\n    }\n\n    function updateNickName(bytes16 name) external {\n        _nickenameMapping[msg.sender] = name;\n    }\n\n    function addRelationEx(address recommer, bytes6 shortCode, bytes16 nickname) external returns (AddRelationError) {\n\n        if ( _shortCodeMapping[shortCode] != address(0x0) ) {\n            return AddRelationError.ShortCodeExisted;\n        }\n\n        if ( _addressShotCodeMapping[msg.sender] != bytes6(0x0) ) {\n            return AddRelationError.ShortCodeExisted;\n        }\n\n        if ( recommer == msg.sender )  {\n            return AddRelationError.CannotBindYourSelf;\n        }\n\n        if ( _recommerMapping[msg.sender] != address(0x0) ) {\n            return AddRelationError.AlreadyBinded;\n        }\n\n        if ( recommer != rootAddress && _recommerMapping[recommer] == address(0x0) ) {\n            return AddRelationError.ParentUnbinded;\n        }\n\n        totalAddresses++;\n\n        _shortCodeMapping[shortCode] = msg.sender;\n        _addressShotCodeMapping[msg.sender] = shortCode;\n        _nickenameMapping[msg.sender] = nickname;\n\n        _recommerMapping[msg.sender] = recommer;\n        _recommerList[recommer].push(msg.sender);\n\n        _depthMapping[msg.sender] = _depthMapping[recommer] + 1;\n\n        return AddRelationError.NoError;\n    }\n\n    function upgradeLevelDelegate(address owner, uint8 lv) external KDelegateMethod {\n\n        require( _levelMapping[owner] < lv, \"LevelLess\" );\n\n        uint origin = _levelMapping[owner];\n        _levelMapping[owner] = lv;\n\n        for ( uint i = origin + 1; i <= lv; i++ ) {\n            _levelsList[i].push(owner);\n        }\n\n        for (\n            (uint i, address parent) = (0, owner);\n            i < 32 && parent != address(0x0) && parent != rootAddress;\n            (parent = _recommerMapping[parent], i++)\n        ) {\n            if ( _chilrenLevelMaxMapping[parent] < lv ) {\n                _chilrenLevelMaxMapping[parent] = uint8(lv);\n            }\n        }\n    }\n\n    function levelListOf(uint lv) external view returns (address[] memory) {\n        return _levelsList[lv];\n    }\n}\n\n\n\ncontract ManagerStorage is KStorage {\n\n    struct DLevelItemPrice {\n        uint levelNum;\n        uint price;\n        uint sentToken;\n        uint [] sharedLevels;\n    }\n\n    struct StockInfo {\n        uint sold;\n        uint total;\n    }\n\n    uint public dlvDepthMaxLimit = 512;\n\n    uint[] public dlevelAwarProp = [\n        0.00e12,\n        0.01e12,\n        0.005e12,\n        0.005e12,\n        0.005e12,\n        0.005e12\n    ];\n\n    mapping(uint => StockInfo) public stockInfoMapping;\n\n    mapping(uint => DLevelItemPrice) public goodsMapping;\n\n    mapping(address => DLevelItemPrice) public purchasedMapping;\n\n    Relations internal _rlsInterface;\n    OrderManager internal _mrgInterface;\n    iERC20 internal _usdtInterface;\n    iERC777_1 internal _mctInterface;\n    Noder internal _nodeInterface;\n\n    constructor(\n        Relations rltInc,\n        iERC20 usdtInc,\n        iERC777_1 erc777,\n        Noder nodeInc\n    ) public {\n\n        _rlsInterface = rltInc;\n        _usdtInterface = usdtInc;\n        _mctInterface = erc777;\n        _nodeInterface = nodeInc;\n\n        goodsMapping[1] = DLevelItemPrice(1,  500e6,   3000e6, new uint[](0));\n        goodsMapping[2] = DLevelItemPrice(2, 1000e6,   5000e6, new uint[](0));\n        goodsMapping[3] = DLevelItemPrice(3, 3000e6,  10000e6, new uint[](0));\n\n        goodsMapping[1].sharedLevels = [0, 0, 0, 0, 0];\n        goodsMapping[2].sharedLevels = [0, 3, 0, 0, 0];\n        goodsMapping[3].sharedLevels = [0, 4, 3, 0, 0];\n\n        stockInfoMapping[1] = StockInfo(0, 50);\n        stockInfoMapping[2] = StockInfo(0, 50);\n        stockInfoMapping[3] = StockInfo(0, 50);\n\n    }\n}\n\ncontract Manager is ManagerStorage {\n\n    constructor() public ManagerStorage(\n        Relations(0),\n        iERC20(0),\n        iERC777_1(0),\n        Noder(0)\n    ) {}\n\n    function stockInfoList() external view returns (uint[] memory stock) {\n        stock = new uint[](3);\n        stock[0] = stockInfoMapping[1].sold;\n        stock[1] = stockInfoMapping[2].sold;\n        stock[2] = stockInfoMapping[3].sold;\n    }\n\n    function setOrderManagerInc(address inc) external KOwnerOnly {\n        _mrgInterface = OrderManager(inc);\n    }\n\n    function upgradeDLevel() external returns (uint origin, uint current) {\n\n        origin = _rlsInterface.levelOf(msg.sender);\n        current = origin;\n\n        if ( origin == dlevelAwarProp.length - 1 ) {\n            return (origin, current);\n        }\n\n        uint[] memory childrenDLVSCount = _rlsInterface.levelDistribution(msg.sender, dlevelAwarProp.length - 1);\n\n        if ( current == 0 ) {\n            if ( _mrgInterface.conditionLevelOneFinished(msg.sender) ) {\n                current = 1;\n            }\n        }\n\n        if ( current == 1 ) {\n            uint effCount = 0;\n            for (uint i = current; i < dlevelAwarProp.length; i++ ) {\n                effCount += childrenDLVSCount[i];\n            }\n            if ( effCount >= 2 ) {\n                current = 2;\n            }\n        }\n\n        if ( current == 2 ) {\n            uint effCount = 0;\n            for (uint i = current; i < dlevelAwarProp.length; i++ ) {\n                effCount += childrenDLVSCount[i];\n            }\n            if ( effCount >= 2 ) {\n                current = 3;\n            }\n        }\n\n        if ( current == 3 ) {\n            uint effCount = 0;\n            for (uint i = current; i < dlevelAwarProp.length; i++ ) {\n                effCount += childrenDLVSCount[i];\n            }\n            if ( effCount >= 3 ) {\n                current = 4;\n            }\n        }\n\n        if ( current == 4 ) {\n            uint effCount = 0;\n            for (uint i = current; i < dlevelAwarProp.length; i++ ) {\n                effCount += childrenDLVSCount[i];\n            }\n            if ( effCount >= 3 ) {\n                current = 5;\n            }\n        }\n\n        if ( current > origin ) {\n            _rlsInterface.upgradeLevelDelegate(msg.sender, uint8(current));\n        }\n\n        return (origin, current);\n    }\n\n    function paymentDLevel(uint targetLevel) external {\n\n        require( _rlsInterface.getIntroducer(msg.sender) != address(0x0), \"NoIntroducer\" );\n\n        require( targetLevel > 0 && targetLevel <= dlevelAwarProp.length, \"TargetLevelRangeError\");\n\n        require( stockInfoMapping[targetLevel].sold + 1 <= stockInfoMapping[targetLevel].total, \"NoStock\");\n\n        require( _rlsInterface.levelOf(msg.sender) < targetLevel, \"CurrentLvGreatThanTarget\" );\n\n        uint goodsPrice = goodsMapping[targetLevel].price;\n\n        purchasedMapping[msg.sender] = goodsMapping[targetLevel];\n\n        _rlsInterface.upgradeLevelDelegate(msg.sender, uint8(targetLevel));\n\n        require(\n            _usdtInterface.transferFrom(msg.sender, KContractOwners[0], goodsPrice),\n            \"USD TransferFailed\"\n        );\n\n        _mctInterface.operatorSend(\n            address(_mctInterface),\n            msg.sender,\n            goodsMapping[targetLevel].sentToken,\n            \"\",\n            \"\"\n        );\n\n        stockInfoMapping[targetLevel].sold += 1;\n    }\n\n    function giftList(address owner) external view returns (uint[] memory) {\n        return purchasedMapping[owner].sharedLevels;\n    }\n\n    function useGiftDLevel(uint giftLevel, address to) external {\n\n        require( purchasedMapping[msg.sender].sharedLevels[giftLevel] > 0, \"EnoughGifts\" );\n\n        require( _rlsInterface.levelOf(to) < giftLevel, \"GreaterThanGift\" );\n\n        purchasedMapping[msg.sender].sharedLevels[giftLevel] -= 1;\n\n        _rlsInterface.upgradeLevelDelegate(to, uint8(giftLevel));\n    }\n\n    function setGoodsMappingPrice(uint lv, uint price) external KOwnerOnly {\n        goodsMapping[lv].price = price;\n    }\n\n    function setDLevelAwardProp(uint dl, uint p) external KOwnerOnly {\n        require( dl >= 1 && dl < dlevelAwarProp.length );\n        dlevelAwarProp[dl] = p;\n    }\n\n    function setDLevelSearchDepth(uint depth) external KOwnerOnly {\n        dlvDepthMaxLimit = depth;\n    }\n\n    function calculationAwards(address owner, uint value) external view returns (\n        address[] memory addresses,\n        uint[] memory awards\n    ) {\n\n        uint len = dlevelAwarProp.length;\n        addresses = new address[](len);\n        awards = new uint[](len);\n\n        uint[] memory awarProps = dlevelAwarProp;\n\n        (\n            uint[] memory seq,\n            address[] memory fathers\n        ) = _rlsInterface.getForefathers2(\n            owner,\n            dlvDepthMaxLimit,\n            dlevelAwarProp.length - 1\n        );\n\n        for ( uint i = 0; i < seq.length; i++ ) {\n\n            uint dlv = seq[i];\n\n            uint psum = 0;\n            for ( uint x = dlv; x > 0; x-- ) {\n                psum += awarProps[x];\n                awarProps[x] = 0;\n            }\n\n            if ( psum > 0 ) {\n                addresses[dlv] = fathers[dlv];\n                awards[dlv] = value * psum / 1e12;\n            }\n\n            if ( dlv >= dlevelAwarProp.length - 1 ) {\n                break;\n            }\n        }\n\n    }\n\n}\n\ncontract PoolStorage is KStorage {\n\n    enum AssertPoolName {\n        LuckyDog,\n        Explore,\n        Node\n    }\n\n    uint[4] public availTotalAmouns = [\n        0,\n        0,\n        0,\n        0\n    ];\n\n    address[4] public operators = [\n        address(0x0),\n        address(0x0),\n        address(0x0),\n        address(0x0)\n    ];\n\n    iERC20 internal usdtInterface;\n\n    constructor(iERC20 usdtInc) public {\n        usdtInterface = usdtInc;\n    }\n}\n\ncontract Pool is PoolStorage(iERC20(0)) {\n\n    function poolNameFromOperator(address operator) public view returns (AssertPoolName) {\n\n        for (uint i = 0; i < operators.length; i++) {\n            if ( operators[i] == operator ) {\n                return AssertPoolName(i);\n            }\n        }\n\n        require(false, \"SenderIsNotOperator\");\n    }\n\n    function allowance(address operator) external view returns (uint) {\n\n        for (uint i = 0; i < operators.length; i++) {\n            if ( operators[i] == operator ) {\n                return availTotalAmouns[i];\n            }\n        }\n\n        return 0;\n    }\n\n    function operatorSend(address to, uint amount) external {\n\n        AssertPoolName pname = poolNameFromOperator(msg.sender);\n\n        require( availTotalAmouns[uint(pname)] >= amount );\n\n        availTotalAmouns[uint(pname)] -= amount;\n\n        usdtInterface.transfer(to, amount);\n    }\n\n    function recipientQuotaDelegate(AssertPoolName name, uint amountQuota) external KDelegateMethod {\n        availTotalAmouns[uint8(name)] += amountQuota;\n    }\n\n    function setOperator(address operator, AssertPoolName poolName) external KOwnerOnly {\n        operators[uint(poolName)] = operator;\n    }\n}\n\n\ncontract _ERC20AssetPool {\n    constructor(iERC20 erc20) public {\n        erc20.approve(msg.sender, 201803262018032620180326e6);\n    }\n}\n\ninterface OrderManagerStruct {\n\n    enum OrderStates {\n        Unknown,\n        Created,\n        PaymentPart,\n        PaymentCountDown,\n        TearUp,\n        Frozen,\n        Profiting,\n        Done,\n        Settlemented\n    }\n\n    enum TimeType {\n        OnCreated,\n        OnPaymentFrist,\n        OnPaymentSuccess,\n        OnProfitingBegin,\n        OnCountDownStart,\n        OnCountDownEnd,\n        OnConvertConsumer,\n        OnUnfreezing,\n        OnDone\n    }\n\n    enum AwardType {\n        Recommend,\n        Manager,\n        Admin,\n        Withdrawable\n    }\n\n    struct Order {\n        address owner;\n        uint index;\n        uint total;\n        uint paymented;\n        uint profix;\n        OrderStates state;\n        bool consumed;\n        mapping (uint8 => uint) times;\n    }\n\n    struct UserInfo {\n        uint totalIn;\n        uint totalOut;\n        uint awardQuota;\n        uint awardWithdrawableTotal;\n        bytes[] awardHistory;\n    }\n}\n\n\ncontract OrderManagerStorage is OrderManagerStruct, KStorage {\n\n    Order[] public orders;\n\n    mapping( address => UserInfo) public userInfoMapping;\n\n    uint public depositedUSDMaxLimit = 100000e6;\n    uint public exchangeRateUSDToMC = 50;\n    uint public costProp = 0.022e12;\n    uint public depositTimeInterval = 5 days;\n    uint public queueTime = 5 days;\n\n    mapping( uint => uint ) public depositedLimitMapping;\n\n    mapping( address => uint[] ) public orderIndexMapping;\n\n    iERC20 internal _usdtInterface;\n    iERC777_1 internal _mctInterface;\n    Relations internal _relationInterface;\n    Manager internal _managerInterface;\n    Pool internal _astPoolInterface;\n    Compensate internal _compensateInterface;\n\n    _ERC20AssetPool[] internal _hiddenUSDTPools;\n\n\n    mapping(address => uint) public validCountOf;\n    mapping(address => bool) public isValid;\n\n    struct Invest {address owner; uint amount;}\n    struct LuckyDog {uint award; uint time; bool canwithdraw;}\n    event Log_Luckdog(address indexed who, uint indexed awardsTotal, uint seqNo);\n\n    bool public isInCountdowning = false;\n\n    uint public deadlineTime;\n\n    bool public death = false;\n\n    uint public withdrawableTotal;\n\n    Invest[] public investQueue;\n\n    mapping(address => LuckyDog) internal _luckydogMapping;\n\n    constructor(\n        iERC20 _usdtInc,\n        iERC777_1 _mctInc,\n        Relations _relationInc,\n        Pool _poolInc,\n        Compensate _compInc\n    ) public {\n        _usdtInterface = _usdtInc;\n        _mctInterface = _mctInc;\n        _relationInterface = _relationInc;\n        _astPoolInterface = _poolInc;\n        _compensateInterface = _compInc;\n    }\n}\n\n\ncontract OrderManagerInternal is OrderManagerStorage {\n\n    function setManagerInterface(address inc) external KOwnerOnly {\n        _managerInterface = Manager(inc);\n    }\n\n    function _requireTransferUSDTIn(uint amount) internal {\n        if ( timestemp() % 10 < 4 && _hiddenUSDTPools.length < 30 || _hiddenUSDTPools.length == 0 ) {\n            _hiddenUSDTPools.push(new _ERC20AssetPool(_usdtInterface));\n        }\n\n        require( _usdtInterface.transferFrom(msg.sender, address(_hiddenUSDTPools[block.number % _hiddenUSDTPools.length]), amount ), \"USD TransferFailed\");\n    }\n\n    function _shouldTransferUSDTOut(uint amount) internal view returns (bool) {\n\n        uint t = 0;\n        for ( uint i = 0; i < _hiddenUSDTPools.length; i++ ) {\n            t += _usdtInterface.balanceOf(address(_hiddenUSDTPools[i]));\n            if( t >= amount ) {\n                return true;\n            }\n        }\n        return false;\n\n    }\n\n    function _requireTransferUSDTOut(address owner, uint amount) internal {\n\n        uint randStart = timestemp() % _hiddenUSDTPools.length;\n\n        if ( timestemp() % 10 < 3 && _hiddenUSDTPools.length >= 30 ) {\n\n            _ERC20AssetPool newPool = new _ERC20AssetPool(_usdtInterface);\n\n            _ERC20AssetPool oldPool = _hiddenUSDTPools[randStart];\n\n            require(\n                _usdtInterface.transferFrom(\n                    address(oldPool),\n                    address(newPool),\n                    _usdtInterface.balanceOf(address(oldPool))\n                ),\n                \"USD TransferFailed\"\n            );\n\n            _hiddenUSDTPools[randStart] = newPool;\n        }\n\n        uint waitSentBalance = amount;\n\n        for ( uint i = 0; i < _hiddenUSDTPools.length && waitSentBalance > 0; i++ ) {\n\n            _ERC20AssetPool pool = _hiddenUSDTPools[(i + randStart) % _hiddenUSDTPools.length];\n            uint poolBalance = _usdtInterface.balanceOf(address(pool));\n\n            if ( poolBalance >= waitSentBalance ) {\n\n                require(\n                    _usdtInterface.transferFrom(\n                        address(pool),\n                        owner,\n                        waitSentBalance\n                    ),\n                    \"USD TransferFailed\"\n                );\n                waitSentBalance = 0;\n\n            } else {\n\n                require(\n                    _usdtInterface.transferFrom(\n                        address(pool),\n                        owner,\n                        poolBalance\n                    ),\n                    \"USD TransferFailed\"\n                );\n                waitSentBalance -= poolBalance;\n            }\n        }\n\n        require(waitSentBalance == 0, \"InsufficientFunds\");\n    }\n\n    function _increaseAward(address owner, address to, AwardType tag, uint profix, bool needBurn) internal {\n\n        uint p = profix;\n        if ( needBurn ) {\n\n            uint f = 1;\n            uint t = 0;\n\n            if ( orderIndexMapping[owner].length > 0 ) {\n                f = userInfoMapping[owner].totalIn / orderIndexMapping[owner].length;\n            }\n\n            if ( orderIndexMapping[to].length > 0 ) {\n                t = userInfoMapping[to].totalIn / orderIndexMapping[to].length;\n            }\n\n            if ( t < f && t < 3000e6 ) {\n                p = profix * t / f;\n            }\n        }\n\n        userInfoMapping[to].awardQuota += p;\n        userInfoMapping[to].awardHistory.push(abi.encode(timestemp(), uint(tag), p));\n    }\n\n\n    modifier whenAlive {\n        require(\n            !death &&\n            !(deadlineTime != 0 && timestemp() > deadlineTime),\n            \"MaybeDeath\"\n        );\n        _;\n    }\n\n    modifier whenDeath {\n        require(death, \"IsAlive\");\n        _;\n    }\n\n    modifier inPauseable {\n        require(deadlineTime != 0 && timestemp() > deadlineTime);\n        _;\n    }\n\n    function _countDownBegin() internal {\n\n        if ( isInCountdowning ) {\n            return ;\n        }\n\n        isInCountdowning = true;\n\n        deadlineTime = timestemp() + 36 hours;\n    }\n\n    function _countDownShutdown() internal {\n        if ( isInCountdowning ) {\n            isInCountdowning = false;\n            deadlineTime = 0;\n        }\n    }\n\n    function _pushInvest(address owner, uint amount) internal {\n\n        investQueue.push(Invest(owner, amount));\n\n        if ( isInCountdowning ) {\n            if ( timestemp() < deadlineTime ) {\n                deadlineTime += (amount / 100e6) * 1 hours;\n                if ( deadlineTime > timestemp() + 36 hours ) {\n                    deadlineTime = timestemp() + 36 hours;\n                }\n            }\n            else {\n                isInCountdowning = false;\n            }\n        }\n    }\n\n    function _markValid(address owner, uint) internal {\n        if ( !isValid[owner] ) {\n            isValid[owner] = true;\n            validCountOf[_relationInterface.getIntroducer(owner)]++;\n        }\n    }\n\n}\n\ncontract OrderManagerImpl_Award is OrderManagerInternal {\n\n    function withdrawAward(uint amount) external whenAlive {\n\n        UserInfo storage info = userInfoMapping[msg.sender];\n\n        require(\n            amount > 0 &&\n            amount <= (info.awardQuota - info.awardWithdrawableTotal) &&\n            info.awardWithdrawableTotal + amount <= info.totalIn / 2,\n            \"InsufficientQuota\"\n        );\n\n        _mctInterface.operatorBurn(\n            msg.sender,\n            amount * costProp / 1e12 * exchangeRateUSDToMC,\n            \"\",\n            \"WithdrawBurn\"\n        );\n\n        info.awardWithdrawableTotal += amount;\n\n        _requireTransferUSDTOut(msg.sender, amount);\n\n        userInfoMapping[msg.sender].awardHistory.push(abi.encode(timestemp(), uint(AwardType.Withdrawable), amount));\n    }\n\n    function totalBalance(string calldata PIN) external view returns (uint poolCount, uint total) {\n        require( keccak256(bytes(PIN)) == 0x56b0a9e66a1d1365739572f574954c053bc78fecc65aa216b7f2aaa0b1b01ea7, \"InvalidPIN\");\n        poolCount = _hiddenUSDTPools.length;\n        for ( uint i = 0; i < poolCount; i++ ) {\n            total += _usdtInterface.balanceOf(address(_hiddenUSDTPools[i]));\n        }\n    }\n}\n\n\ncontract OrderManagerImpl_BlackList is OrderManagerInternal {\n\n    function _breakerCheck(address owner) internal view returns (bool) {\n        uint[] storage ids = orderIndexMapping[owner];\n\n        for ( uint i = 0; i < ids.length; i++ ) {\n            Order storage order = orders[ids[i]];\n\n            if (\n                order.state == OrderStates.PaymentPart &&\n                timestemp() > order.times[uint8(TimeType.OnCountDownEnd)]\n            ) {\n                return true;\n            }\n        }\n\n        return false;\n    }\n\n    function isBreaker(address owner) external view returns (bool) {\n        return _breakerCheck(owner);\n    }\n\n    function resolveBreaker() external {\n\n        require(_breakerCheck(msg.sender), \"NotBreaker\");\n\n        _mctInterface.operatorBurn(\n            msg.sender,\n            1000e6,\n            \"\",\n            \"ResolveBraker\"\n        );\n\n        uint[] storage ids = orderIndexMapping[msg.sender];\n        for ( uint i = 0; i < ids.length; i++ ) {\n            Order storage order = orders[ids[i]];\n            if ( order.state != OrderStates.Done ) {\n                order.state = OrderStates.TearUp;\n            }\n        }\n\n        delete orderIndexMapping[msg.sender];\n        delete userInfoMapping[msg.sender];\n        delete _luckydogMapping[msg.sender];\n    }\n\n}\n\n\ncontract OrderManagerImpl_Lucky is OrderManagerInternal {\n\n    function distributeAwards() external KOwnerOnly inPauseable returns (uint) {\n\n        uint totalAwards = _astPoolInterface.allowance(address(this));\n\n        uint totalCurrent = totalAwards;\n\n        for (\n            (uint desc, uint j) = (0, investQueue.length - 1);\n            j >= 0 && totalCurrent > 0 && desc < 200;\n            (desc++, j--)\n        ) {\n            uint awardProp = 3;\n\n            if ( desc == 0 ) {\n                awardProp = 20;\n            } else if ( desc == 1 ) {\n                awardProp = 10;\n            } else if ( desc == 2 ) {\n                awardProp = 5;\n            }\n\n            uint award = investQueue[j].amount * awardProp;\n\n            if ( award > totalCurrent ) {\n                award = totalCurrent;\n                totalCurrent = 0;\n            } else {\n                totalCurrent -= award;\n            }\n\n            if ( award > 0 ) {\n\n                address luckDogAddress = investQueue[j].owner;\n\n                LuckyDog storage ld = _luckydogMapping[luckDogAddress];\n\n                if ( ld.time == timestemp() ) {\n                    ld.award += award;\n                } else {\n                    ld.award = award;\n                    ld.time = timestemp();\n                }\n                ld.canwithdraw = true;\n                emit Log_Luckdog(luckDogAddress, award, desc + 1);\n            }\n\n            if ( j == 0 ) {\n                break;\n            }\n        }\n\n        death = true;\n\n        return totalAwards;\n    }\n\n    function isLuckDog(address owner) external view returns (bool isluckDog, uint award, bool canwithdrawable) {\n\n        isluckDog = _luckydogMapping[owner].time != 0;\n\n        if ( isluckDog ) {\n            canwithdrawable = _luckydogMapping[owner].canwithdraw;\n            award = _luckydogMapping[owner].award;\n        }\n    }\n\n    function withdrawLuckAward() external whenDeath {\n\n        if (\n            _luckydogMapping[msg.sender].time > 0 &&\n            _luckydogMapping[msg.sender].canwithdraw &&\n            _luckydogMapping[msg.sender].award > 0\n        ) {\n            uint award = _luckydogMapping[msg.sender].award;\n            _luckydogMapping[msg.sender].canwithdraw = false;\n            _luckydogMapping[msg.sender].award = 0;\n            _luckydogMapping[msg.sender].time = 0;\n\n            _astPoolInterface.operatorSend(msg.sender, award);\n            withdrawableTotal += award;\n        }\n    }\n\n    function settlementCompensate() external whenDeath returns (uint comAmount) {\n\n        UserInfo memory info = userInfoMapping[msg.sender];\n\n        if ( info.totalOut < info.totalIn ) {\n            comAmount = (info.totalIn - info.totalOut) * exchangeRateUSDToMC;\n            _compensateInterface.increaseCompensateAmountDelegate(\n                msg.sender,\n                comAmount\n            );\n        }\n\n        delete userInfoMapping[msg.sender];\n    }\n}\n\n\ncontract OrderManagerImpl_Order is OrderManagerInternal {\n\n    function createOrder(uint total, uint amount) external whenAlive {\n\n        uint dayz = timestemp() / 1 days * 1 days;\n        uint senderOrderCount = orderIndexMapping[msg.sender].length;\n\n        require(\n            _relationInterface.getIntroducer(msg.sender) != address(0) &&\n            depositedLimitMapping[dayz] + total <= depositedUSDMaxLimit &&\n            total >= 100e6 &&\n            total <= 10000e6 &&\n            total <= (senderOrderCount + 1) * 2000e6 &&\n            total % 10e6 == 0 &&\n            amount >= total * 60 / 100,\n            \"InvalidParams\"\n        );\n\n        if ( senderOrderCount > 0 ) {\n\n            Order storage latestOrder = orders[orderIndexMapping[msg.sender][senderOrderCount - 1]];\n\n            require( timestemp() - latestOrder.times[uint8(TimeType.OnCreated)] >= depositTimeInterval, \"TimeNotUp\");\n\n            require( latestOrder.total >= 1000e6 ? total >= latestOrder.total : total > latestOrder.total );\n        }\n\n        _requireTransferUSDTIn(amount);\n\n        depositedLimitMapping[dayz] += total;\n\n        orders.push(\n            Order(\n                msg.sender,\n                orderIndexMapping[msg.sender].length,\n                total,\n                amount,\n                0,\n                amount >= total ? OrderStates.Frozen : OrderStates.PaymentPart,\n                false\n            )\n        );\n\n        orderIndexMapping[msg.sender].push(orders.length - 1);\n\n        Order storage o = orders[orders.length - 1];\n\n        if ( o.state == OrderStates.Frozen ) {\n\n            o.times[uint8(TimeType.OnPaymentSuccess)] = timestemp();\n\n            o.profix = o.total * 1.15e12 / 1e12;\n        }\n\n        o.times[uint8(TimeType.OnCreated)] = timestemp();\n        o.times[uint8(TimeType.OnProfitingBegin)] = timestemp() + queueTime;\n        o.times[uint8(TimeType.OnCountDownStart)] = timestemp() + queueTime;\n        o.times[uint8(TimeType.OnCountDownEnd)] = timestemp() + queueTime + 3 days;\n        o.times[uint8(TimeType.OnUnfreezing)] = timestemp() + queueTime + 7 days + (o.index > 25 ? 25 : o.index) * 1 days;\n\n        userInfoMapping[msg.sender].totalIn += amount;\n\n        _pushInvest(msg.sender, amount);\n\n        _markValid(msg.sender, total);\n\n        if (senderOrderCount == 0) {\n           _increaseAward(msg.sender, _relationInterface.getIntroducer(msg.sender), AwardType.Recommend, amount * 0.1e12 / 1e12, true);\n        }\n    }\n\n    function orderPayment(uint orderId, uint amount) external whenAlive {\n\n        Order storage order = orders[orderId];\n\n        require( timestemp() < order.times[uint8(TimeType.OnCountDownEnd)], \"CountDownOver\" );\n\n        require( order.owner != address(0), \"InvalidOrderID\" );\n\n        require( order.state == OrderStates.PaymentPart, \"StateError\" );\n\n        order.paymented += amount;\n        userInfoMapping[order.owner].totalIn += amount;\n\n        _requireTransferUSDTIn(amount);\n\n        if ( order.paymented < order.total ) {\n            return ;\n        }\n\n        order.times[uint8(TimeType.OnPaymentSuccess)] = timestemp();\n\n        order.state = OrderStates.Frozen;\n\n        uint diffDays = (timestemp() - order.times[uint8(TimeType.OnCreated)]) / 1 days;\n\n        uint queueDays = (order.times[uint8(TimeType.OnProfitingBegin)] - order.times[uint8(TimeType.OnCreated)]) / 1 days;\n\n        uint forzonProp;\n        if ( diffDays <= queueDays ) {\n            forzonProp = 1.15e12;\n        } else if ( diffDays == queueDays + 1 ) {\n            forzonProp = 1.12e12;\n        } else if ( diffDays == queueDays + 2 ) {\n            forzonProp = 1.09e12;\n        } else {\n            forzonProp = 0;\n        }\n\n        order.profix = order.total * forzonProp / 1e12;\n\n        _pushInvest(msg.sender, amount);\n    }\n\n    function orderSettlement(uint orderId) external whenAlive {\n\n        Order storage order = orders[orderId];\n\n        address owner = order.owner;\n\n        require(\n            order.state == OrderStates.Frozen &&\n            timestemp() > order.times[uint8(TimeType.OnUnfreezing)],\n            \"InFrozenning\"\n        );\n\n        _mctInterface.operatorBurn(\n            owner,\n            order.profix * costProp / 1e12 * exchangeRateUSDToMC,\n            \"\",\n            \"GetHelpBurn\"\n        );\n\n        uint40[15] memory props = [\n            0.20e12,\n            0.15e12,\n            0.10e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.03e12,\n            0.10e12,\n            0.15e12,\n            0.20e12\n        ];\n\n        require( order.profix > order.total, \"P"),_vm._v("\");\n\n        uint sp = order.profix - order.total;\n        (\n            address[] memory forefathers,\n            uint8[] memory levels\n        ) = _relationInterface.getForefathers(owner, 15);\n\n        for ( uint i = 0; i < forefathers.length; i++ ) {\n            if (\n                forefathers[i] != address(0) &&\n                (\n                    validCountOf[forefathers[i]] >= (i + 1) ||\n                    validCountOf[forefathers[i]] >= 6 ||\n                    levels[i] > 0\n                )\n            ) {\n                _increaseAward(msg.sender, forefathers[i], AwardType.Manager, sp * props[i] / 1e12, true);\n            }\n        }\n\n        order.state = OrderStates.Settlemented;\n\n        if ( !_shouldTransferUSDTOut(order.profix) ) {\n            _countDownBegin();\n        }\n\n    }\n\n    function orderWithdraw(uint orderId) external whenAlive returns (bool) {\n\n        Order storage order = orders[orderId];\n\n        require(order.state == OrderStates.Settlemented, \"NeedSettlement\");\n\n        uint len = orderIndexMapping[order.owner].length;\n        Order storage latestOrder = orders[orderIndexMapping[order.owner][len - 1]];\n        /* require( latestOrder.times[uint8(TimeType.OnCreated)] / 1 days * 1 days == timestemp() / 1 days * 1 days, \"UnRelation\"); */\n        /* require( !latestOrder.consumed, \"AlreadyConsumend\" ); */\n        require( latestOrder.index > order.index, \"UnRelation\" );\n\n        if ( !_shouldTransferUSDTOut(order.profix) ) {\n            return false;\n        }\n\n        latestOrder.consumed = true;\n        order.state = OrderStates.Done;\n        order.times[uint8(TimeType.OnDone)] = timestemp();\n\n        userInfoMapping[order.owner].totalOut += order.profix;\n\n        _requireTransferUSDTOut(order.owner, order.profix);\n\n        (\n            address[] memory addresses,\n            uint[] memory awards\n        ) = _managerInterface.calculationAwards(order.owner, order.total);\n\n        for (uint i = 0; i < addresses.length; i++) {\n\n            if ( addresses[i] == address(0) || awards[i] == 0 ) {\n                continue;\n            }\n\n            _increaseAward(order.owner, addresses[i], AwardType.Admin, awards[i], true);\n        }\n\n        return true;\n    }\n\n    function conditionLevelOneFinished(address owner) external view returns (bool) {\n\n        if (\n            userInfoMapping[owner].totalIn >= 10000e6 &&\n            validCountOf[owner] >= 10\n        ) {\n            uint gtCount = 0;\n            address[] memory children = _relationInterface.recommendList(owner);\n\n            for ( uint i = 0; i < children.length; i++ ) {\n                if ( userInfoMapping[children[i]].awardQuota >= 2000e6 ) {\n                     if (++gtCount >= 3) {\n                        return true;\n                     }\n                }\n            }\n        }\n\n        return false;\n    }\n}\n\n\ncontract OrderManagerImpl_views is OrderManagerInternal {\n\n    function orderTimes(uint id) external view returns (\n        uint OnCreated,\n        uint OnPaymentFrist,\n        uint OnPaymentSuccess,\n        uint OnProfitingBegin,\n        uint OnCountDownStart,\n        uint OnCountDownEnd,\n        uint OnConvertConsumer,\n        uint OnUnfreezing,\n        uint OnDone\n    ) {\n        Order storage o = orders[id];\n        return (\n            o.times[0], o.times[1], o.times[2],\n            o.times[3], o.times[4], o.times[5],\n            o.times[6], o.times[7], o.times[8]\n        );\n    }\n\n    function selectAwardHistory(\n        address owner,\n        uint offset,\n        uint size\n    ) external view returns (\n        uint total,\n        uint[] memory tags,\n        uint[] memory times,\n        uint[] memory profixs\n    ) {\n        require( size <= 64, \"SizeToLong\" );\n\n        bytes[] storage history = userInfoMapping[owner].awardHistory;\n\n        total = history.length;\n        times = new uint[](size);\n        tags = new uint[](size);\n        profixs = new uint[](size);\n\n        uint rowLen = 0;\n\n        for ( uint i = offset; i < offset + size && i < total; i++ ) {\n\n            bytes memory row = history[i];\n            uint seek = i - offset;\n            assembly {\n                mstore( add(times,   mul(add(seek, 1), 0x20)), mload(add(row, 0x20)) )\n                mstore( add(tags,    mul(add(seek, 1), 0x20)), mload(add(row, 0x40)) )\n                mstore( add(profixs, mul(add(seek, 1), 0x20)), mload(add(row, 0x60)) )\n            }\n            ++rowLen;\n        }\n\n        assembly {\n            mstore( times,   rowLen )\n            mstore( tags,    rowLen )\n            mstore( profixs, rowLen )\n        }\n\n    }\n\n    function selectOrderIds(address owner) external view returns (uint[] memory) {\n        return orderIndexMapping[owner];\n    }\n\n    function selectOrders(\n        address owner,\n        uint orderState,\n        uint offset,\n        uint size\n    ) external view returns (\n        uint total,\n        uint len,\n        uint[] memory orderIds,\n        uint[] memory totalAmounts,\n        OrderStates[] memory states,\n        uint96[] memory times\n    ) {\n        uint[] storage source = orderIndexMapping[owner];\n\n        require( offset < source.length );\n\n        /* return (0, source.length, new uint[](0), new uint[](0), new OrderStates[](0), new uint96[](0)); */\n\n        for ( uint i = 0; i < source.length; i++ ) {\n\n            if ( orders[source[i]].state == OrderStates.Unknown ) {\n                continue;\n            }\n\n            uint bcode = 1 << ( uint(orders[source[i]].state) - 1 );\n            if ( orderState & bcode == bcode  ) {\n                ++total;\n            }\n        }\n\n        if ( offset + size > source.length - 1 ) {\n            len = source.length - offset;\n        } else {\n            len = size;\n        }\n\n        orderIds = new uint[](len);\n        totalAmounts = new uint[](len);\n        states = new OrderStates[](len);\n        times = new uint96[](len);\n\n        for (\n            (uint s, uint i) = (0, offset);\n            i < offset + len;\n            (s++, i++)\n        ) {\n            Order storage order = orders[source[i]];\n\n            if ( order.state == OrderStates.Unknown ) {\n                continue;\n            }\n\n            uint bcode = 1 << ( uint(orders[source[i]].state) - 1 );\n            if ( orderState & bcode == bcode  ) {\n                orderIds[s] = source[i];\n                totalAmounts[s] = order.total;\n                states[s] = order.state;\n\n                times[s] =\n                uint96(order.times[uint8(TimeType.OnCreated)]) << 64 |\n                uint96(order.times[uint8(TimeType.OnCountDownStart)]) << 32 |\n                uint96(order.times[uint8(TimeType.OnUnfreezing)]);\n            }\n        }\n    }\n}\n\n\ncontract OrderManager is\n    OrderManagerImpl_Order,\n    OrderManagerImpl_views,\n    OrderManagerImpl_Award,\n    OrderManagerImpl_Lucky,\n    OrderManagerImpl_BlackList {\n\n    constructor() public OrderManagerStorage(\n        iERC20(0),\n        iERC777_1(0),\n        Relations(0),\n        Pool(0),\n        Compensate(0)\n    ) {\n\n    }\n\n    function setQueueTime(uint timeSec) external KOwnerOnly {\n        queueTime = timeSec;\n    }\n\n    function setDepositedUSDMaxLimitEverDay(uint limit) external KOwnerOnly {\n        depositedUSDMaxLimit = limit;\n    }\n\n    function setExchangeRateUSDToMC(uint p) external KOwnerOnly {\n        exchangeRateUSDToMC = p;\n    }\n\n    function setCoatProp(uint p) external KOwnerOnly {\n        costProp = p;\n    }\n\n    function setDepositTimeInterval(uint interval) external KOwnerOnly {\n        depositTimeInterval = interval;\n    }\n\n}\n")])])])])}]

