import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Index from '../views/index.vue'
import Register from '../views/register'
import Card from '../views/card'
import ExchangeRecord from '../views/exchangeRecord'
import Income from '../views/income'
import IncomeRecord from '../views/income/incomeDetail.vue'
import ProvideHelp from '../views/provideHelp'
import FriendsList from '../views/friendsList'
import Compensate from '../views/compensate'
import CompensateRecord from '../views/compensate/compensateRecord.vue'
import DrawCompensate from '../views/drawCompensate'
import End from '../views/drawCompensate/end.vue'
import LuckPlayer from '../views/drawCompensate/luckPlayer.vue'
import UpLv from '../views/upLv'
import Grade from '../views/upLv/grade.vue'
import AllOrder from '../views/allOrder'
import OrderDetails from '../views/allOrder/orderDetails'
import NodalDividend from '../views/nodalDividend'
import Message from '../views/message'
import MessageCard from '../views/message/messageCard.vue'

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/card',
    name: 'Card',
    component: Card
  },
  {
    path: '/exchangeRecord',
    name: 'ExchangeRecord',
    component: ExchangeRecord
  },
  {
    path: '/income',
    name: 'Income',
    component: Income
  },
  {
    path: '/incomeRecord',
    name: 'IncomeRecord',
    component: IncomeRecord
  },
  {
    path: '/provideHelp',
    name: 'ProvideHelp',
    component: ProvideHelp
  },
  {
    path: '/friendsList',
    name: 'FriendsList',
    component: FriendsList
  },
  {
    path: '/compensate',
    name: 'Compensate',
    component: Compensate
  },
  {
    path: '/compensateRecord',
    name: 'CompensateRecord',
    component: CompensateRecord
  },
  {
    path: '/drawCompensate',
    name: 'DrawCompensate',
    component: DrawCompensate
  },
  {
    path: '/upLv',
    name: 'UpLv',
    component: UpLv
  },
  {
    path: '/grade',
    name: 'Grade',
    component: Grade
  },
  {
    path: '/allOrder',
    name: 'AllOrder',
    component: AllOrder
  },
  {
    path: '/end',
    name: 'End',
    component: End
  },
  {
    path: '/luckPlayer',
    name: 'LuckPlayer',
    component: LuckPlayer
  },
  {
    path: '/orderDetails',
    name: 'OrderDetails',
    component: OrderDetails
  },
  {
    path: '/nodalDividend',
    name: 'NodalDividend',
    component: NodalDividend
  },
  {
    path: '/message',
    name: 'Message',
    component: Message
  },
  {
    path: '/messageCard',
    name: 'MessageCard',
    component: MessageCard
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
