<template>
	<div class="box">
		<div class="navBox">
			<div v-for="(item, index) in list" :key="index">
				<div>
					<span>{{ item.name }}</span>
					<span>{{ item.type }}</span>
				</div>
				<div>
					<span>{{ item.date }}</span>
					<span>{{item.isType3 ? '-' : '+'}}{{ item.amount }} USDT</span>
				</div>
			</div>
			<not-record v-if="!list.length" :text="NoRevenueRecord"></not-record>
		</div>
	</div>
</template>

<script>
	import notRecord from '@/components/pubilc/not-record'
	
	export default {
		components: { notRecord },
		data() {
			return {
				title: this.$t('IncomeDetails'),
				list: [
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' }
				],
				NoRevenueRecord: this.$t('NoRevenueRecord')
			}
		},
		async created() {
			if (!this.$tronWeb) return;
			
			var total = (await this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'selectAwardHistory(address,uint256,uint256)',
				issuerAddress: this.$address,
				parameter: [
					{ type: "address", value: this.$address },
					{ type: "uint256", value: 0 },
					{ type: "uint256", value: 10 },
				]
			}).then()).constant_result[0].decode(
				['uint256','uint256[]','uint256[]','uint256[]'],
				['total','tags','times','profixs']
			)
			
			const maxOffset = total.total.toNumber() <= 10 ? 0 : Number(total.total.toString().substr(0, 1)) * 10;
			const promises = [];
			for (let i = maxOffset; i >= 0; i -= 10) {
				promises.push(this.getList(i));
			}
			
			Promise.all(promises).then(async result => {
				const typeArr = ['推荐奖','管理奖','经理奖','提取收益']
				for (const { tags, times, profixs } of result) {
					for (let i = profixs.length - 1; i >= 0; i--) {
						this.list.push({
							time: times[i].toNumber(),
							amount: profixs[i].toNumber().fromSun(),
							type: typeArr[tags[i].toNumber()],
							date: this.$toTime(times[i].toNumber(),'Y-M-D H:i'),
							name: this.$store.state.name,
							isType3: tags[i].toNumber() === 3 ? true : false
						})
					}
				}
			}).catch(console.log)
		},
		methods:{
			async getList(offset){
				return (await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectAwardHistory(address,uint256,uint256)',
					issuerAddress: this.$address,
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: offset },
						{ type: "uint256", value: 10 },
					]
				}).then()).constant_result[0].decode(
					['uint256','uint256[]','uint256[]','uint256[]'],
					['total','tags','times','profixs']
				)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		background: #fff;
	}
	.navBox {
		width: 100%;
		height: 100%;
		// color: #A1BDE1;
		color: #656363;
		font-size: 28px;
		position: relative;
		padding: 20px 40px 0;
		background-color: #fff;
		overflow-y: scroll;
		&>div {
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #516A89;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				&:first-child {
					margin-bottom: 20px;
				}
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}
</style>
