<template>
	<div class="box">
		<div class="navBox exchangeRecordNavBox">
			<div class="colorA1BDE1">
				<div v-for="(item, index) in list" :key="index">
					<div>
						<span>{{$t('SwapSuccess')}}</span>
						<span>{{ item.date }}</span>
					</div>
					<div>
						<div>
							<span>-{{ item.usdt }} USDT</span>
							<span>+{{ item.dt }} BOC</span>
						</div>
						<!-- <img v-lazy="require('../../../assets/changeRecord_next.png')"> -->
					</div>
				</div>
				<not-record :text="NoRecord" v-if="!list.length"></not-record>
			</div>
		</div>
	</div>
</template>

<script>
	import NotRecord from '@/components/pubilc/not-record.vue'
	
	export default {
		components: { NotRecord },
		data() {
			return {
				title: this.$t('SwapRecords'),
				NoRecord: this.$t('NoRecord'),
				list: [
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' },
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' },
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' }
				]
			}
		},
		async created() {
			if (this.$tronWeb) {
				this.$tronWeb.event({
					contractAddress: this.$contracts.Swap.address,
					method: 'Log_Swaped'
				}).then(events => {
					const length = events.length >= 20 ? 20 : events.length
					for(let i = 0; i < length ; i++){
						// if(window.tronWeb.address.fromHex(events[i].result.owner) !== this.$address) continue;
						this.list.push({
							usdt: events[i].result.inAmount.fromSun(),
							dt: events[i].result.outAmount.fromSun(),
							date: this.$toTime(events[i].result.time,'Y-M-D H:i'),
						})
					}
				})

			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		background: #fff!important;
	}
	.navBox {
		padding: 0 22px 58px;
		&>div {
			width: 100%;
			height: 100%;
			// color: #A1BDE1;
			font-size: 26px;
			border-radius: 10px;
			// background-color: #273446;
			padding: 38px 30px 0;
			overflow-y: auto;
			&>.not_record {
				border: none;
			}
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 22px;
				padding-bottom: 10px;
				border-bottom: 1px solid #17212E;
				&>div {
					display: flex;
					&>div span:first-child {
						margin-bottom: 2px;
					}
					&:first-child {
						flex-direction: column;
						align-items: flex-start;
						&>span:first-child {
							color: #21C17A;
						}
					}
					&:last-child {
						align-items: center;
						&>div {
							display: flex;
							flex-direction: column;
							align-items: flex-end;
							&>span:first-child{
								color: #F76E80;
							}
							&>span:last-child{
								color: #21C17A;
							}
						}
					}
				}
			}
		}
	}
</style>
