import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from './i18n/i18n.js'
import { Toast , Lazyload } from 'vant'
import 'vant/lib/index.css';
import VueClipboard from 'vue-clipboard2'
import PubilcTitle from './components/pubilc/pubilcTitle.vue'

//导入合约config文件
import './assets/js/config.js'

//重置样式
import './assets/css/resets.scss'
//全局样式
import './assets/css/public.scss'

import 'amfe-flexible/index.min.js'

Vue.config.productionTip = false

Vue.directive('ButClickLimit',{
	inserted: function(el){
		el.addEventListener('click',()=>{
			el.disabled = true;
			setTimeout(()=>{
				el.disabled = false;
			},2000)
		})
	}
})

Vue.component('PubilcTitle',PubilcTitle)
Vue.use(Toast).use(Lazyload,{
  lazyComponent: true,
}).use(VueClipboard)

Vue.prototype.$toTime = (times , type) => {
	function add(m){return m<10?'0'+m:m }
	var time = new Date(times * 1000);
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();

	if (type === 'Y/M/D') {
		return y+'.'+add(m)+'.'+add(d);
	} else if (type === 'Y-M-D H:i') {
		return y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'M/D H:i'){
		return add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'Y/M/D H:i'){
		return y+'/'+add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'YMD'){
		return y+add(m)+add(d);
	}else if(type === 'H:i'){
		return add(h)+':'+add(mm);
	}
}

Vue.prototype.PrefixZero = (value , length)=>{
	if(value.indexOf('0x') != -1){
		let result = (value.slice(2) + Array(length).join(0)).slice(0,length);
		return '0x'+result
	}
	return (value + Array(length).join(0)).slice(0,length);
}

new Vue({
	router,
	i18n,
	store,
	render: h => h(App)
}).$mount('#app')
