				}
				// &::before {
				// 	content: '';
				// 	width: 60px;
				// 	height: 60px;
				// 	position: absolute;
				// 	left: -30px;
				// 	border-radius: 30px;
				// 	background-color: #F5F5F5;
				// }
				// &::after {
				// 	content: '';
				// 	width: 60px;
				// 	height: 60px;
				// 	position: absolute;
				// 	right: -30px;
				// 	border-radius: 30px;
				// 	background-color: #F5F5F5;
				// }
			}
			&>.blockInviter{
