<template>
	<div class="box">
		<div class="navBox luckPlayerBox">
			<div class="item" v-for="(item, index) in list" :key="index">
				<div>
					<span>No.{{ item.seqNo }}</span>
					<span>{{ `${item.address.slice(0, 7)}...${item.address.slice(-7)}` }}</span>
				</div>
				<span>{{ item.amount }} <span>USDT</span></span>
			</div>
			<not-record v-if="!list.length" :text="NoLuckyPlayers"></not-record>
		</div>
	</div>
</template>

<script>
	import notRecord from '@/components/pubilc/not-record'
	
	export default {
		components: { notRecord },
		data() {
			return {
				list: [],
				title: this.$t('LuckyPlayer'),
				NoLuckyPlayers: this.$t('NoLuckyPlayers'),
			}
		},
		mounted() {
			this.$tronWeb && this.init()
		},
		methods:{
			init(){
				this.$store.commit('changeSuspensionButtonShow',true)
				
				this.$tronWeb.event({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'Log_Luckdog',
					blockNumber: 0,
					size: 600
				}).then(event => {
					console.log(event)
					if(!event.length) return
					event.forEach(async item=>{
							this.list.unshift({
								seqNo: item.result.seqNo,
								address: window.tronWeb.address.fromHex(item.result.who),
								amount: item.result.awardsTotal.fromSun()
							})
							
							this.list.sort((a,b)=>{
								return a.seqNo - b.seqNo
							})
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		background: #fff;
	}
	.item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		// color: #8FACD2;
		font-size: 32px;
		margin-bottom: 30px;
		padding-bottom: 20px;
		border-bottom: 1px solid #516A89;
		&>span>span {
			font-size: 20px;
		}
		&>div {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			&>span:last-child {
				// color: #7489A5;
				font-size: 28px;
				margin-top: 10px;
			}
		}
	}
	.navBox {
		padding: 38px 20px 0;
		// background-color: #273446;
	}
</style>
