import Addresses from './contracts.json';
// import AssertPool from '../../contracts/AssertPool.json'

export class TronWeb {
    constructor() {
        // this.tronGrid = new TronGrid(window.tronWeb);
        this.address = window.tronWeb.defaultAddress.base58;
    }

    async getBalance(address = this.address) {
        return await window.tronWeb.trx.getBalance(address);
    }

    async sendTransaction(to, amount) {
        return await window.tronWeb.trx.sendTransaction(to, amount);
    }

    async call({ contractAddress, method, issuerAddress = this.address, parameter = [], options = {} }) {
        return await window.tronWeb.transactionBuilder.triggerConstantContract(
            contractAddress,
            method,
            options,
            parameter,
            issuerAddress
        );
    }

    async event({ contractAddress, method, blockNumber , size = 20 }) {
        return await window.tronWeb.getEventResult(
            contractAddress,
            blockNumber ? { eventName: method, blockNumber , size, } : { eventName: method , size, },
        );
    }

    async getBlockTime(blockId = 'latest') {
        return Math.floor(new Date().getTime() / 1000);
    }

    async getBlockNumber(blockId = 'latest') {
        const block = await window.tronWeb.trx.getBlock(blockId);
        return block.block_header.raw_data.number;
    }
}

export const Contracts = {
    init: async function() {
        const keys = Object.keys(Addresses), contracts = {};
        for (const key of keys) {
			contracts[key] = window.tronWeb.contract(Addresses[key].abi, Addresses[key].address);
        }

        return contracts;
    }
}

String.prototype.toSun = function() {
    return window.tronWeb.toSun(Number(this));
}

String.prototype.fromSun = function() {
    return window.tronWeb.fromSun(this);
}

String.prototype.decode = function(types, names) {
    const str = this.slice(0, 2) === '0x' ? this : `0x${this}`;

    if (Array.isArray(names) && names.length > 0) {
        return window.tronWeb.utils.abi.decodeParams(names, types, str);
    } else if (Array.isArray(names)) {
        return window.tronWeb.utils.abi.decodeParams(types, str);
    }
    return window.tronWeb.utils.abi.decodeParams(types, str)[0];
}

String.prototype.toBN = function() {
    return window.tronWeb.toBigNumber(this);
}

String.prototype.toSzabo = function() {
    return '10'.toBN().pow(12).multipliedBy(this);
}

String.prototype.fromSzabo = function() {
    return this.toBN().div('10'.toBN().pow(12)).toNumber();
}

String.prototype.hexToString = function(cleanse = false) {
    const str = window.tronWeb.toUtf8(this);
    return cleanse ? str.replace(/[^\w\u4E00-\u9FA5]/g, '') : str;
}

String.prototype.stringToHex = function(){
    return window.tronWeb.fromUtf8(this)
}

String.prototype.toFixed = function(decimal) {
    const index = this.indexOf('.') + 1
    
    if (index > 0) {
        let suffix = this.slice(index)
        const len = this.slice(index).length

        if (len > decimal || len == decimal) {
            return this.slice(0, index + decimal)
        }
        
        return `${this.slice(0, index)}${suffix}${Array(decimal - len + 1).join(0)}`
    }

    return `${this}.${Array(decimal + 1).join(0)}`
}

Number.prototype.toSun = function() {
    return window.tronWeb.toSun(this);
}

Number.prototype.fromSun = function() {
    return window.tronWeb.fromSun(this);
}

Number.prototype.toBN = function() {
    return window.tronWeb.toBigNumber(this);
}