<template>
	<div class="van-tabbar-page box">
		<!-- <div class="indexTopBg"></div> -->
		<div class="getHelp">
			<div class="getHelpDetails" @click="$router.push('/incomeRecord')">
				<div class="topLeft">
					<img class="lv" v-if="lv == '1'" src="@/assets/img/upLv01.png" />
					<img class="lv" v-if="lv == '2'" src="@/assets/img/upLv02.png" />
					<img class="lv" v-if="lv == '3'" src="@/assets/img/upLv03.png" />
					<img class="lv" v-if="lv == '4'" src="@/assets/img/upLv04.png" />
					<img class="lv" v-if="lv == '5'" src="@/assets/img/upLv05.png" />
					<img class="vip" src="@/assets/img/vip.png" />
				</div>
				<div class="topRight">
					<span>{{$t('IncomeDetails')}}</span>
					<img src="@/assets/img/doubleRight.png" />
				</div>
				<div class="reveiceIncome">
					<span>{{rewardAmount}}</span>
					<div>{{$t('CurrentIncome')}}</div>
				</div>
				<div class="orderInfo colorA1BDE1">
					<div>
						<span>{{$t('Available01')}}</span>
						<span>{{Amount}}</span>
					</div>
					<span></span>
					<div>
						<span>{{$t('TotalRevenue')}}</span>
						<span>{{totalRewardedAmount}}</span>
					</div>
				</div>
				<div class="incomeBg"></div>
			</div>
			
			<div class="income colorA1BDE1_1">
				<div class="title">
					<span>{{$t('extract')}}</span>
				</div>
				<div class="input">
					<div>
						<input type="number" :placeholder="$t('PleaseEnterMultipleOf10')" v-model="value" @input="input">
					</div>
					<span>USDT</span>
				</div>
					<!-- <div class="input">
						<input type="number" placeholder="消耗燃料" v-model="ConsumeDT" disabled>
						<span>DT</span>
					</div> -->
				<div class="Underline ConsumeDT">
					<span>{{$t('FuelConsumption')}}</span>
					<span>{{ConsumeDT}} BOC</span>
				</div>
				<div class="AvailableDT">
					<span>{{$t('Available')}}：</span>
					<span>{{DT}} BOC</span>
				</div>
				<!-- <span>可用 {{DT}} DT</span> -->
				<Button @click="createOrder" v-ButClickLimit>{{$t('Commit')}}</Button>
				<div class="checkbox" @click="rule = true">
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
			</div>
		</div>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div class="colorA1BDE1">
				<div class="ruleTop">
					<div>
						<icon name="fail"></icon>
					</div>
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
				<div class="ruleBottom">
					<p>1、{{$t('ReceivingInstructionsText01')}}；</p>
					<p>2、{{$t('ReceivingInstructionsText02')}}；</p>
					<p>3、{{$t('ReceivingInstructionsText03')}}；</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Button , Overlay } from 'vant'
	import Order from "@/common/Order.json"

	export default {
		components:{ Icon , Button , Overlay },
		data () {
			return {
				active: 0,
				orderList: [],
				times: '',
				teamAchievements: 0,
				TeamMemberTotal: 0,
				dlevel: '',
				rewardAmount: 0,
				lv: '',
				totalRewardedAmount: 0,
				checked: false,
				ConsumeDT: 0,
				DT: 0,
				value: '',
				Amount: 0,
				rule: false,
			}
		},
		methods:{
			init() {
				// this.$contracts.Pool.KAddAuthAddress(this.$contracts.OrderManager.address).send(console.log);
				
				Promise.all([
					this.$tronWeb.call({
						contractAddress: this.$contracts.ERC777_1Token.address,
						method: 'balanceOf(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}),// DT数量
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'userInfoMapping(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}), // 当前可领取收益/累计领取收益
					this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'levelOf(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}) // 等级
				]).then(([ Balance, userInfoMapping, UpgradeDLevel ]) => {
					const { totalIn, totalOut, awardQuota , awardWithdrawableTotal } = userInfoMapping.constant_result[0].decode(
						['uint256', 'uint256', 'uint256', 'uint256'],
						['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal']
					);
					const origin = UpgradeDLevel.constant_result[0].decode(['uint256']);
					
					this.DT = Balance.constant_result[0].decode(['uint256']).toString().fromSun();
					this.lv = origin.toString();
					
					this.Amount = awardQuota.sub(awardWithdrawableTotal).toNumber().fromSun();
					this.totalRewardedAmount = awardWithdrawableTotal.toNumber().fromSun();
					
					const a = Number(totalIn.div(2).sub(awardWithdrawableTotal).toString().fromSun());
					const b = Number(awardQuota.sub(awardWithdrawableTotal).toString().fromSun())
					this.rewardAmount = a < b ? a : b
					
				}).catch(console.log);
			},
			input() {
				if (['', '0'].includes(this.value)) {
					this.value = '';
					this.ConsumeDT = '';
					return false;
				} else {
					this.ConsumeDT = this.value.toSun().toBN()
									.multipliedBy(this.WithdrawCostProp)
									// .div('1'.toSzabo())
									.multipliedBy(this.$store.state.exchangeRateUSDToMC)
									.toString().fromSun();
				}
			},
			createOrder() {
				var value = Number(this.value),
					OrderAmountGranularity = Number(this.OrderAmountGranularity.fromSun());
				
				if(value == '' ||  value == 0){
					return this.$toast.fail(this.$t('PleaseEnterTheWithdrawalIncome'))
				}else if(!this.rewardAmount){
					return this.$toast.fail(this.$t('warningText08')+'0');
				}else if(this.value % 10 != 0){
					return this.$toast.fail(this.$t('PleaseEnterMultipleOf10'));
				}else if(this.rewardAmount < OrderAmountGranularity){
					return this.$toast.fail(this.$t('warningText09')+OrderAmountGranularity);
				}else if(this.value > this.rewardAmount){
					return this.$toast.fail(this.$t('warningText10'));
				}else if(Number(this.ConsumeDT) > Number(this.DT)){
					return this.$toast.fail(this.$t('warningText11'));
				}
				
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'withdrawAward(uint256)',
					issuerAddress: this.$address,
					parameter: [{ type: "uint256", value: this.value.toSun() }]
				}).then(result => {
					this.$contracts.OrderManager.withdrawAward(this.value.toSun()).send({
						feeLimit: this.$store.state.generalFeeLimit.toSun() 
					}).then(res=>{
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.$toast.success(this.$t('success'))
									this.value = '';
									this.ConsumeDT = '';
									this.init();
									clearInterval(interval);
								}
							})
						}, 1000);
					})
				})
			}
		},
		created() {
			this.$tronWeb && this.init();
		},
		destroyed() {
			clearInterval(this.times);
		}
	}
</script>

