<template>
	<div class="box">
		<div class="navBox provideHelpNavBox">
			<div class="provideHelpBox">
				<div class="provideHelpTitle">{{$t('Provide')}}</div>
				<div class="provideHelpInput">
					<input type="number" :placeholder="$t('EnterMultipleOf10')" v-model="number" @input="Input" />
					<div>USDT</div>
				</div>
				<div class="joinInfo">
					<!-- <div>
						<span>最小参与数量</span>
						<div>
							<span>{{minJoin}}</span>
							<span>USDT</span>
						</div>
					</div>
					<div>
						<span>最大参与数量</span>
						<div>
							<span>{{OrderAmountMaxLimit}}</span>
							<span>USDT</span>
						</div>
					</div> -->
					{{$t('range')}}：{{minJoin}} ~ {{OrderAmountMaxLimits}}
				</div>
				<!-- <div class="provideHelpInput">
					<input type="number" placeholder="即时付款≥50%" v-model="multiple" />
					<div>USDT</div>
				</div> -->
				<div class="provideHelpTitle ">{{$t('PrepaidRatio')}}</div>
				<div class="provideHelpSelect">
					<div :class="{active:item.number == active,disable: item.disable}" v-for="(item,index) in selectList" :key="index" @click="multipleFun(item.number,item.disable)">{{item.number}}%</div>
				</div>
				<div class="provideHelpMultiple ">
					<span>{{$t('PrepaymentAmount')}}</span>
					<span>{{multiple}} USDT</span>
				</div>
				<div class="provideHelpBalance">{{$t('Available')}} {{usdt}} USDT</div>
				<Button :disabled='!checked' @click='CreateOrder' v-ButClickLimit>{{$t('Commit')}}</Button>
				<div class="checkbox" @click="checked = !checked">
					<icon name="success" :class="checked ? 'active' : ''"></icon>
					<span>{{$t('IAgree')}}<span @click.stop="joinRules = true">《{{$t('ParticipationRules')}}》</span>{{$t('AndUnderstandTheRisks')}}</span>
				</div>
			</div>
		</div>

		<authorization
		:show="authorizedAccess"
		:click="authorizedAccessFun"
		:needAuthorization='number'
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>

		<overlay :show="joinRules" class="joinRules" @click="joinRules = false">
			<div class="">
				<div class="title">{{$t('ParticipationRulesText01')}}</div>
				<div class="content">
					<p>{{$t('ParticipationRulesText02')}}</p>
					<p>{{$t('ParticipationRulesText03')}}</p>
					<p>{{$t('ParticipationRulesText04')}}</p>
					<p>{{$t('ParticipationRulesText05')}}</p>
					<p>{{$t('ParticipationRulesText06')}}</p>
					<p>{{$t('ParticipationRulesText07')}}</p>
					<p>{{$t('ParticipationRulesText08')}}</p>
					<p>{{$t('ParticipationRulesText09')}}</p>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Button, Icon , Overlay } from 'vant'
	import Authorization from '@/components/pubilc/Authorization.vue'
	import Order from "@/common/Order.json"

	export default {
		components: { Button , Icon , Authorization , Overlay },
		data() {
			return {
				checked: false,
				authorizedAccess: false,
				authorizationEnds: false,
				selectList: [
					// {number: 50 , disable: false},
					{number: 60 , disable: false},
					{number: 70 , disable: false},
					{number: 80 , disable: false},
					{number: 90 , disable: false},
					{number: 100 , disable: false}
				],
				active: '',
				number: '',
				multiple: 0,
				usdt: '',
				OrderAmountMaxLimits: '',
				minJoin: '',
				joinRules: false,
				title: this.$t('ProvideHelp'),
				RemainingDepositedUSDT: '',
				createTimeState: false,
				OrderCreateInterval_self: 0,
			}
		},
		methods:{
			async init(){
				this.authorizedAccess = false;
				
				//当前时间
				var currentTime = new Date().getTime() / 1000
				
				// usdt授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [{ type: "address", value: this.$address },{ type: "address", value: this.$contracts.OrderManager.address }]
				}).then( result => {
					this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toNumber().fromSun()
				})
				
				// usdt
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'balanceOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then( result => {
					this.usdt = result.constant_result[0].decode(['uint256']).toString().fromSun()
				})
				
				// 当天日投入量
				const time = (new Date(new Date(currentTime*1000).toLocaleDateString()).getTime() / 1000) + 8 * 3600
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'depositedLimitMapping(uint256)',
					parameter: [{ type: "uint256", value: time }]
				}).then( async result => {
					this.depositedLimitMapping = result.constant_result[0].decode(['uint256']).toString();
					
					// 日投入上限
					this.DepositedUSDMaxLimit = (await this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'depositedUSDMaxLimit()'
					}).then()).constant_result[0].decode(['uint256']).toString()

					// 剩余可参与的USDT量(日)
					this.RemainingDepositedUSDT = this.DepositedUSDMaxLimit.toBN().minus(this.depositedLimitMapping.toBN()).toString().fromSun()
				})

				// 获取我的推荐码(没有推荐码 =  没有绑定推荐人)
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'addressToShortCode(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result=>{
					this.GetIntroducer = result.constant_result[0].decode(['bytes6'])
				})
				
				// 判断订单间隔时间
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrderIds(address)',
					parameter: [{ type: "address", value: this.$address },],
				}).then(async res=>{
					const { orderIndexMapping } = res.constant_result[0].decode(
						['uint256[]'],
						['orderIndexMapping']
					);
					
					let currentTime = await this.$tronWeb.getBlockTime();
					
					if(orderIndexMapping.length){
						const totalOrderTimes = (await this.$tronWeb.call({
							contractAddress: this.$contracts.OrderManager.address,
							method: 'orderTimes(uint256)',
							issuerAddress: this.$address,
							parameter: [{ type: "uint256", value: orderIndexMapping[orderIndexMapping.length - 1].toNumber() }]
						}).then()).constant_result[0].decode(
							['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
							['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
						)
						
						this.OrderCreateInterval_self = await this.$tronWeb.call({
							contractAddress: this.$contracts.OrderManager.address,
							method: 'depositTimeInterval()'
						}).then(res => res.constant_result[0].decode(['uint256']).toNumber());
						
						if(Math.floor((currentTime - totalOrderTimes.OnCreated.toNumber())) < this.OrderCreateInterval_self){
							this.createTimeState = false
						}else{
							this.createTimeState = true
						}
					}else{
						this.createTimeState = true
					}
				})
				
				// 判断订单参与范围
				try {
					var GetOrder = (await this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'selectOrders(address,uint256,uint256,uint256)',
						parameter: [
							{ type: "address", value: this.$address },
							{ type: "uint256", value: 65535 },
							{ type: "uint256", value: 0 },
							{ type: "uint256", value: 0 }
						]
					}).then()).constant_result[0].decode(
						['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
						['total', 'len', 'id', 'totalAmounts', 'states', 'times']
					)
					
					if(GetOrder.total.toNumber()){
						var id = GetOrder.total.toNumber() - 1;
						var getOrder = (await this.$tronWeb.call({
											contractAddress: this.$contracts.OrderManager.address,
											method: 'selectOrders(address,uint256,uint256,uint256)',
											parameter: [
												{ type: "address", value: this.$address },
												{ type: "uint256", value: 65535 },
												{ type: "uint256", value: id },
												{ type: "uint256", value: 1 }
											]
										}).then()).constant_result[0].decode(
											['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
											['total', 'len', 'id', 'totalAmounts', 'states', 'times']
										)
						
						if (Number(getOrder.totalAmounts[0].toString().fromSun()) >= 1000) {
							this.minJoin = getOrder.totalAmounts[0].toString().fromSun();
						} else {
							this.minJoin = getOrder.totalAmounts[0].toString().toBN().plus('10'.toSun()).toNumber().fromSun()
						}
						
						this.OrderAmountMaxLimits = GetOrder.total.toString().toBN().plus(1).multipliedBy(this.OrderAmountAppendQuota).toString().fromSun();
						if(Number(this.OrderAmountMaxLimits) >= Number(this.OrderAmountMaxLimit.fromSun())){
							this.OrderAmountMaxLimits = Number(this.OrderAmountMaxLimit.fromSun())
						}
					}
				} catch (e) {
					this.minJoin = this.OrderAmountMinLimit.fromSun();
					this.OrderAmountMaxLimits = '2000';
				}
				
			},
			async CreateOrder(){
				var number = Number(this.number),
					multiple = Number(this.multiple),
					minJoin = Number(this.minJoin);
				
				if(!number){
					return this.$toast.fail(this.$t('warningText01'));
				}else if(!multiple){
					return this.$toast.fail(this.$t('warningText02'));
				}else if(this.RemainingDepositedUSDT < 100){
					return this.$toast.fail(this.$t('greatThenDayMaxLimit'));
				}else if(number > this.RemainingDepositedUSDT){
					return this.$toast.fail(this.$t('HelpTodayRemainingBalance')+this.RemainingDepositedUSDT+' USDT');
				}
				else if(!parseInt(this.GetIntroducer,16)){
					this.$toast.fail(this.$t('warningText03'));
					let timeOut = setTimeout(() => {
						clearTimeout(timeOut)
						this.App_Push('/register')
					}, 2000)
					return
				}else if(number % 10 != 0){
					return this.$toast.fail(this.$t('EnterMultipleOf10'));
				}else if(number < minJoin && minJoin < 1000){
					return this.$toast.fail(this.$t('warningText04'));
				}else if(number < minJoin && minJoin >= 1000){
					return this.$toast.fail(this.$t('warningText04'));
				}else if(number > Number(this.OrderAmountMaxLimits)){
					return this.$toast.fail(this.$t('warningText04'));
				}else if(multiple.toBN().multipliedBy(100).div(number).toNumber() < this.OrderPaymentedMinPart){
					// return this.$toast.fail('最小即时付款倍数为'+this.OrderPaymentedMinPart);
					return this.$toast.fail(this.$t('warningText05')+'≥'+this.OrderPaymentedMinPart+'%');
				}else if(multiple > this.usdt){
					return this.$toast.fail(this.$t('warningText06'));
				}else if(!this.createTimeState){
					return this.$toast.fail(`${this.$t('provideHelpWaningText01')}${parseInt(this.OrderCreateInterval_self / 86400)}${this.$t('provideHelpWaningText02')}`);
				}else if(number > Number(this.USDTAuthorizedAccess)){
					return this.authorizedAccess = true
					// return this.App_Push('/authorizationManagement')
				}

				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'createOrder(uint256,uint256)',
					parameter: [
						{ type: "uint256", value: this.number.toSun() },
						{ type: "uint256", value: this.multiple.toSun() }
					]
				}).then(res => {
				// 	const state = res.constant_result[0].decode(['uint8']);
				// 	console.log(state)
				// 	if (state == '0') {
						this.$contracts.OrderManager.createOrder(this.number.toSun(), this.multiple.toSun()).send({ 
							feeLimit: this.$store.state.importantFeeLimit.toSun(),
						}).then(res=>{
							this.$store.commit('loadingShow',true)
							
							const interval = setInterval(() => {
								window.tronWeb.trx.getTransaction(res).then(transaction => {
									if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
										this.$store.commit('loadingShow',false)
										this.$router.push('/');
										clearInterval(interval);
									}
								})
							}, 1000);
						})
						
				// 	} else if(state == '4') {
				// 		return this.$toast.fail(this.$t('warningText07'));
				// 	}
				})
			},
			authorizedAccessFun(value,active){
				if(active === 1){
					var approveValue = this.$store.state.totalSupply
				}else{
					var approveValue = value
				}
				this.$contracts.ERC20Token.approve(this.$contracts.OrderManager.address, approveValue.toSun()).send({
					feeLimit: this.$store.state.generalFeeLimit.toSun()
				}).then(res => {
					this.$store.commit('loadingShow',true)
					
					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(res).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								this.$store.commit('loadingShow',false)
								this.authorizationEnds = true;
								this.authorizedAccess = false;
								
								// usdt授权额度
								this.$tronWeb.call({
									contractAddress: this.$contracts.ERC20Token.address,
									method: 'allowance(address,address)',
									parameter: [{ type: "address", value: this.$address },{ type: "address", value: this.$contracts.OrderManager.address }]
								}).then( result => {
									this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toNumber().fromSun()
									this.CreateOrder()
								})

								clearInterval(interval);
							}
						})
					}, 1000);
				})
			},
			multipleFun(value,disable){
				if(disable) return
				this.active = value;
				this.multiple = this.number * value / 100
			},
			Input(){
				if(this.active){
					this.multiple = this.number * this.active / 100
				}
				if(this.number == ''){
					this.number = ''
					this.multiple = 0
				}
			},
			end(){
				this.authorizedAccess = false;
			},
		},
		created() {
			this.$tronWeb && this.init();
		}
	}
</script>

<style lang="scss" scoped>
	.navBox{
		padding: 40px 28px 0;
	}
	.provideHelpBox{
		width: 100%;
		height: 1016px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(#000, 0.3);
		border-radius: 12px;
		padding: 36px 42px 0;
		position: relative;
		color: #333;
		text-align: center;

		&>.joinInfo{
			display: flex;
			justify-content: space-between;
			margin-bottom: 36px;
			font-size: 28px;
			color: #999999;

			&>div{
				width: 282px;
				height: 112px;
				background-color: #2A405C;
				border-radius: 12px;
				color: #A1BDE1;
				padding-left: 18px;
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: center;
				border: 2px solid #516A89;

				&>span{
					font-size: 26px;
				}

				&>div{
					display: flex;

					&>span:nth-child(1){
						font-size: 32px;
					}

					&>span:nth-child(2){
						font-size: 20px;
						margin: 5px 0 0 5px;
					}
				}
			}
		}

		&>.provideHelpTitle{
			// color: #A1BDE1;
			font-size: 30px;
			text-align: left;
			margin-bottom: 20px;
		}

		&>.provideHelpMultiple{
			font-size: 30px;
			// color: #A1BDE1;
			padding-bottom: 34px;
			border-bottom: 1px solid #516A89;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 40px 0 30px;

			&>span:nth-child(2){
				font-size: 40px;
				color: #EB4948;
			}
		}

		&>.provideHelpInput{
			display: flex;
			width: 100%;
			height: 112px;
			font-size: 36px;
			border: 2px solid #C8C9CC;
			background-color: #F1F1F1;
			border-radius: 10px;
			margin-bottom: 36px;

			&>input{
				width: 432px;
				height: 112px;
				background-color: transparent;
				border: none;
				padding-left: 30px;
				font-size: 26px;
			}

			&>div{
				flex: 1;
				height: 100%;
				border-left: 2px solid #C8C9CC;
				display: flex;
				justify-content: center;
				align-items: center;
				color: #656363;
			}
		}

		&>.provideHelpBalance{
			font-size: 24px;
			text-align: right;
			margin: 16px 0 44px;
			color: #66BAB2;
		}

		&>.provideHelpSelect{
			display: flex;
			justify-content: space-between;

			&>div{
				width: 90px;
				height: 72px;
				border-radius: 12px;
				background-color: #F1F1F1;
				font-size: 24px;
				display: flex;
				align-items: center;
				justify-content: center;
				// border: 2px solid #516A89;
				// color: #516A89;
			}

			&>.disable{
				background-color: rgba($color: gray, $alpha: 0.5);
			}

			&>.active{
				background-color: transparent;
				border: 2px solid #66BAB2;
			}
		}

		&>.checkbox {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 40px;
			font-size: 30px;

			&>i {
				width: 40px;
				height: 40px;
				line-height: 40px;
				color: transparent;
				margin-right: 15px;
				border-radius: 10px;
				border: 2px solid #66BAB2;
				&.active {
					color: #fff;
					background-color: #66BAB2;
