	}

	.joinRules{
		padding: 116px 40px 62px;
		display: flex;
		align-items: center;
		text-align: center;
		
		&>div{
			width: 100%;
			// height: 100%;
			// color: #A1BDE1;
			border-radius: 10px;
			background-color: #fff;

			&>.title{
