<template>
	<div class="compenstate box">
		<div class="navBox compenstateBox">
			<!-- <img src="@/assets/img/record.png" @click="$router.push('/compensateRecord')" /> -->
			<div class="compenstateCircle">
				<div>{{SettlementCompensate ? SettlementCompensate : currentAmout}}</div>
				<span>{{$t('Available02')}}/BOC</span>
			</div>
			<div class="compenstateInfo colorA1BDE1">
				<div>
					<img src="@/assets/img/getReward.png" />
					<span>{{$t('Compensation')}}</span>
					<span>{{total}} BOC</span>
				</div>
				<p></p>
				<div>
					<img src="@/assets/img/wait.png" />
					<span>{{$t('Locked')}}</span>
					<span>{{amout}} BOC</span>
				</div>
			</div>
			<Button @click="send" v-ButClickLimit>{{SettlementCompensate ? $t('Release') : $t('receive')}}</Button>
		</div>
	</div>
</template>

<script>
	import { Button } from 'vant'
	
	export default{
		components:{ Button },
		data(){
			return {
				title: this.$t('ReceiveDT'),
				SettlementCompensate: 0,
				total: 0,
				amout: 0,
				currentAmout: 0,
			}
		},
		methods:{
			async init(){
				this.$store.commit('changeSuspensionButtonShow',true)
				
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'settlementCompensate()'
				}).then(async result => {
					this.SettlementCompensate = Number(result.constant_result[0].decode(['uint256']).toString().fromSun());
					if(!this.SettlementCompensate){
						const { total, currentWithdraw } = (await this.$tronWeb.call({
													contractAddress: this.$contracts.Compensate.address,
													method: 'compensateMapping(address)',
													parameter: [{ type: "address", value: this.$address }]
												}).then()).constant_result[0].decode(['uint256','uint256','uint256'],['total','currentWithdraw','latestWithdrawTime'])
						this.total = total.toNumber().fromSun();
						this.amout = total.sub(currentWithdraw.toString()).toString().fromSun();
						this.currentAmout = (await this.$tronWeb.call({
												contractAddress: this.$contracts.Compensate.address,
												method: 'withdrawCompensate()',
												issuerAddress: this.$address,
											}).then()).constant_result[0].decode(['uint256']).toString().fromSun()
					}
				})
			},
			pushCompensateRecord(){
				this.$router.push('/compensateRecord')
			},
			send(){
				if (this.SettlementCompensate > 0) {
					this.$contracts.OrderManager.settlementCompensate().send({
						feeLimit: this.$store.state.generalFeeLimit.toSun()
					}).then(res => {
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.init();
									clearInterval(interval);
								}
							})
						}, 1000);
					})
				} else {
					if (this.currentAmout <= 0) return this.$toast.fail(this.$t('NoDTAvailable'));

					this.$contracts.Compensate.withdrawCompensate().send({
						feeLimit: this.$store.state.generalFeeLimit.toSun()
					}).then(res => {
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.init();
									clearInterval(interval);
								}
							})
						}, 1000);
					})
				}
			},
		},
		mounted() {
			this.$tronWeb && this.init()
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>
<style>
	.compenstate svg{
		font-size: 45px!important;
		/* color: #A1BDE1; */
	}
</style>
<style lang="scss" scoped>
	.compenstateBox{
		padding: 120px 24px 0;
		text-align: center;
		position: relative;
		
		&>img{
			width: 35px;
			position: absolute;
			top: 40px;
			right: 42px;
		}
		
		.compenstateCircle{
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 28px;
			color: #4E4E4E;
			
			&>div{				
				width: 240px;
				height: 240px;
				border-radius: 50%;
				background: linear-gradient(180deg,#1DE5D2,#22598E);
				font-size: 60px;
				margin-bottom: 28px;
				line-height: 240px;
				color: #FAFAFA;
			}
		}
		
		.compenstateInfo{
			width: 100%;
			height: 272px;
			border-radius: 20px;
			border: 2px solid #1492E6;
			background-color: #fff;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 70px 0 106px;
			color: #656363;
		
			&>p{
				border-right: 2px solid #1492E6;
				height: 60%;
			}
			
			&>div{
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				
				&>img{
					display: block;
					// width: auto;
					height: 60px;
				}
				
				&>span:nth-child(2){
					font-size: 32px;
					margin: 20px 0 15px;
				}
				
				&>span:nth-child(3){
					font-size: 40px;
				}
			}
		}
		
		&>button{
			width: 400px;
			height: 96px;
			border: none;
			border-radius: 10px;
			font-size: 40px;
			color: #fff;
		}
	}
</style>
