<template>
	<div class="home">
		<img src="@/assets/img/logo.png" />
		<img src="@/assets/img/logoName.png" />
		<h2>{{$t('HomeText01')}}</h2>
		<p>{{$t('HomeText02_1')}}</p>
		<p>{{$t('HomeText02_2')}}</p>
		<button @click="isRegister('/provideHelp')">
			<div><span>{{$t('HomeText03')}}</span></div>
		</button>
		<div class="homeDetails" @click="$store.commit('exchangePlayRules',true)">
			<img src="@/assets/img/details.png" />
			<span>{{$t('HomeText04')}}</span>
		</div>
		<div class="userInfo">
			<div class="lv">
				<div @click="$router.push('/upLv')">
					<img v-if="origin == '0'" v-lazy="require('@/assets/img/upLv00.png')">
					<img v-if="origin == '1'" v-lazy="require('@/assets/img/upLv01.png')">
					<img v-if="origin == '2'" v-lazy="require('@/assets/img/upLv02.png')">
					<img v-if="origin == '3'" v-lazy="require('@/assets/img/upLv03.png')">
					<img v-if="origin == '4'" v-lazy="require('@/assets/img/upLv04.png')">
					<img v-if="origin == '5'" v-lazy="require('@/assets/img/upLv05.png')">
				</div>
				<div>
					<div @click.stop="setNameShow = true">
						<span>{{$store.state.name}}</span>
						<img src="@/assets/img/modify.png" />
					</div>
					<div
					@click.stop
					v-clipboard:copy="address"
					v-clipboard:success="onCopySuccess"
					v-clipboard:error="onCopyError">
						<span>{{address ? address.substr(0,5)+'......'+address.substr(-4,4) : ''}}</span>
						<img src="@/assets/img/copy.png" />
					</div>
				</div>
			</div>
		</div>
		<div class="selectLanguage">
			<div class="selectLanguageContent" @click="languageShow = !languageShow">
				<img :src="require(`@/assets/img/${$i18n.locale}.png`)" />
				<icon name="arrow-down"></icon>
			</div>
			<div class="selectLanguageList" v-show="languageShow">
				<span @click="switchLanguage('en')">English</span>
				<span @click="switchLanguage('zh')">中文</span>
			</div>
		</div>
		<div class="message" @click="$router.push('/message')">
			<img src="@/assets/img/message.png" />
		</div>
		<overlay :show="setNameShow" class="set_name" @click="setNameShow = false">
			<div @click.stop>
				<span>{{$t('SetUp')}}</span>
				<input type="text" v-model="nickname" :placeholder="$t('EnterNewNickname')">
				<div>
					<Button @click="setNameShow = false">{{$t('cancel')}}</Button>
					<i></i>
					<Button @click="setName" v-ButClickLimit>{{$t('Commit')}}</Button>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Overlay } from 'vant'
	export default{
		components:{
			Icon , Overlay
		},
		data(){
			return {
				address: '',
				languageShow: false,
				setNameShow: false,
				nickname: '',
				
			}
		},
		props:{
			origin: {
				type: String,
			}
		},
		methods:{
			init(){
				this.address = this.$address
			},
			switchLanguage(type){
				localStorage.setItem('locale',type)
				this.$i18n.locale = type
				
				this.languageShow = !this.languageShow
			},
			isRegister(url){
				if(this.$store.state.name == 'BOBO'){
					return this.$router.push('/register')
				}else{
					return this.$router.push(url)
				}
			},
			onCopySuccess() {
				this.$toast.success(this.$t('CopySuccessful'))
			},
			onCopyError() {
				this.$toast.fail(this.$t('CopyFailed'))
			},
			async setName() {
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/
				
				if (!reg.test(this.nickname) || this.nickname.length > 8) {
					return Toast.fail(this.$t('warningText13'))
				}
	
				this.$contracts.Relations.UpdateNickName(this.PrefixZero(this.nickname.stringToHex(),32))
				.send({ feeLimit: this.$store.state.generalFeeLimit.toSun() }).then(hex => {
					this.$store.commit('loadingShow',true)
	
	                const interval = setInterval(() => {
	                    window.tronWeb.trx.getTransaction(hex).then(transaction => {
	                        if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								this.$store.commit('loadingShow',false)
	                            this.setNameShow = false
								clearInterval(interval);
	                        }
	                    })
	                }, 2000);
				}).catch(console.error);
			},
		},
		mounted() {
			this.$tronWeb && this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.home{
		padding: 180px 0 48px;
		display: flex;
