		align-items: center;
		position: relative;
		z-index: 2;
		
		&>.message{
			position: absolute;
			right: 210px;
			top: 36px;
			display: flex;
			z-index: 2;
			
			&>img{
				width: 48px;
				height: 48px;
			}
		}
		
		&>.userInfo{
			position: absolute;
			top: 0;
			left: 0;
			display: flex;
			justify-content: space-between;
			padding: 30px 20px 0;
			z-index: 2;
			
			&>.lv{
				display: flex;
				align-items: center;
				
				&>div:first-child{
					margin-right: 10px;
					display: flex;
					align-items: center;
					justify-content: center;
					width: 106px;
					height: 92px;
					background-image: url('../assets/img/lvBg.png');
					background-size: 100% 100%;
					
					&>img:last-child{
						width: 50px;
					}
				}
				
				&>div:last-child{
					display: flex;
					flex-direction: column;
					justify-content: flex-start;
					color: #fff;
					
					&>div{
						display: flex;
						align-items: center;
						
						&>img{
							margin-left: 20px;
						}
					}
					
					&>div:first-child{
						font-size: 36px;
						margin-bottom: 20px;
						
						&>img{
							width: 28px;
						}
					}
					
					// &>div{
					// 	display: flex;
					// 	align-items: center;
						
					// 	&>img{
					// 		width: 30px;
					// 		margin-right: 10px;
					// 	}
					// }
					
					&>div:last-child{
						&>span{
							// width: 350px;
							text-align: left;
							font-size: 28px;
							// transform: scale(0.5);
							// margin-left: -45px;
						}
						
						&>img{
							width: 24px;
						}
					}
				}
			}
		}
		
		&:after{
			content: '';
			width: 750px;
			height: 750px;
			border-radius: 50%;
			background-color: rgba($color: #fff, $alpha: 0.2);
			position: absolute;
			top: -310px;
			z-index: 1;
		}
		
		&>img:first-child{
			width: 128px;
			z-index: 2;
		}
		
		&>img:nth-child(2){
			width: 206px;
			margin: 40px 0 38px;
			z-index: 2;
		}
		
		h2{
			font-size: 50px;
			color: #FFFFFF;
			text-shadow: 0px 6px 12px rgba($color: #000000, $alpha: 0.16);
			text-align: center;
		}
		
		p{
			padding: 0 32px;
			font-size: 32px;
			color: #FFDB45;
			text-align: center;
			line-height: 1.4;
			margin-top: 32px;
		}
		
		&>button{
			width: 375px;
			height: 100px;
			border: none;
			background: linear-gradient(180deg, #28BEAF 0%, #3080CE 100%);
			border-radius: 50px;
			margin: 72px 0 40px;
			font-size: 48px;
			color: #fff;
			display: flex;
			justify-content: flex-start;
			box-shadow: 0 4px 16px rgba($color: #000000, $alpha: .17);
			
			&>div{
				width: 100%;
				height: 95px;
				border-radius: 50px;
				background: linear-gradient(90deg, #25DECD -10%, #3472AD 100%);
				box-shadow: 0 2px 4px rgba($color: #000000, $alpha: .17);
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}
		
		&>.homeDetails{
			display: flex;
			align-items: center;
			font-size: 32px;
			color: #fff;
			line-height: 1.5;
			
			&>img{
				width: 36px;
				margin-right: 14px;
			}
		}
	}
	
	.selectLanguage{
		position: absolute;
		top: 30px;
		right: 20px;
		color: #fff;
		z-index: 2;
		
		&>.selectLanguageContent{
			width: 150px;
			height: 60px;
			background-color: rgba($color: #77818B, $alpha: 0.28);
			border-radius: 50px;
			display: flex;
			align-items: center;
