			}
			
			&>i{
				font-size: 30px;
			}
		}
		
		&>.selectLanguageList{
			position: absolute;
			bottom: -104px;
			left: 50%;
			transform: translateX(-50%);
			width: 140px;
			display: flex;
			flex-direction: column;
			
			&>span{
				width: 100%;
				height: 50px;
				background-color: rgba($color: #666, $alpha: 0.11);
