			}
			&>div {
				width: 100%;
				display: flex;
				align-items: center;
				border-top: 2px solid #273446;
				&>button {
					border: none;
					width: calc(50% - 1px);
					height: 103px;
					color: #666;
					font-size: 40px;
					background: transparent;
					display: flex;
					justify-content: center;
					align-items: center;
				}
				&>i {
					width: 2px;
					height: 103px;
					background-color: #273446;
				}
			}
		}
	}
</style>
