<template>
	<div class="myIncome">
		<pubilc-title :text="title" :rightShow="true" :rightText="incomeDetail" :rightClick="()=>{$router.push('/incomeRecord')}" />
		<div class="content">
			<div class="contentBox">
				<div class="myIncomeTop">
					<div class="lineAndNumber">
						<div class="line">
							<div v-for="(item,index) in 5"></div>
						</div>
						<h1>{{rewardAmount}}</h1>
					</div>
					<span>{{$t('CurrentIncome')}}</span>
					<div class="incomeInfo">
						<div>
							<span>{{$t('Available01')}}</span>
							<span>{{Amount}} USDT</span>
						</div>
						<span></span>
						<div>
							<span>{{$t('TotalRevenue')}}</span>
							<span>{{totalRewardedAmount}} USDT</span>
						</div>
					</div>
				</div>
				<div class="myIncomeBottom">
					<button @click="$router.push('/income')">{{$t('extract')}}</button>
					<button @click="isRegister('/card')">{{$t('EarnMore')}}</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PubilcTitle from './pubilc/pubilcTitle.vue'
	export default{
		components:{
			PubilcTitle
		},
		data(){
			return {
				rewardAmount: 0,
				Amount: 0,
				totalRewardedAmount: 0,
				title: this.$t('income01'),
				incomeDetail: this.$t('incomeDetail'),
			}
		},
		methods:{
			init() {
				Promise.all([
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'userInfoMapping(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					})
				]).then(([ userInfoMapping ]) => {
					const { totalIn, totalOut, awardQuota , awardWithdrawableTotal } = userInfoMapping.constant_result[0].decode(
						['uint256', 'uint256', 'uint256', 'uint256'],
						['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal']
					);
					
					this.Amount = awardQuota.sub(awardWithdrawableTotal).toNumber().fromSun();
					this.totalRewardedAmount = awardWithdrawableTotal.toNumber().fromSun();
					
					const a = Number(totalIn.div(2).sub(awardWithdrawableTotal).toString().fromSun());
					const b = Number(awardQuota.sub(awardWithdrawableTotal).toString().fromSun())
					this.rewardAmount = a < b ? a : b
					
					
					// if (totalIn.lt(awardQuota.sub(awardWithdrawableTotal))) {
					// 	this.Amount = this.rewardAmount;
					// } else {
					// 	this.Amount = awardQuota.sub(awardWithdrawableTotal).toString().fromSun();
					// }
				}).catch(console.log);
			},
			isRegister(url){
				if(this.$store.state.name == 'BOBO'){
					return this.$router.push('/register')
				}else{
					return this.$router.push(url)
				}
			}
		},
		mounted() {
			this.$tronWeb && this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.contentBox{
		&>.myIncomeTop{
			padding: 86px 42px 30px;
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 32px;
			color: #666;
			
			&>.lineAndNumber{
				width: 100%;
				font-size: 100px;
				color: #66BAB2;
				position: relative;
				text-align: center;
				z-index: 1;
				margin-bottom: 20px;
				
				&>h1{
					z-index: 2;
				}
				
				&>.line{
					position: absolute;
					width: 100%;
					left: 0;
					z-index: -1;
					top: 50%;
					transform: translateY(-50%);
					opacity: .6;
					
					&>div{
						width: 100%;
						height: 0;
						border-bottom: 1px solid #C7C7C7;
						margin-bottom: 20px;
					}
					
					&>div:last-child{
						margin-bottom: 0;
					}
				}
			}
			
			&>.incomeInfo{
				width: 100%;
				height: 100px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-top: 58px;
				
				&>div{
					display: flex;
					flex-direction: column;
					align-items: center;
					line-height: 1.5;
					flex: 1;
				}
				
				&>span{
					height: 100%;
					border-left: 1px solid #85CFF6;
				}
			}
		}
		
		&>.myIncomeBottom{
			padding: 50px 30px;
			background-color: #E2EDFC;
			display: flex;
			justify-content: space-between;
			
			&>button{
				width: 300px;
				height: 96px;
				font-size: 36px;
				border-radius: 10px;
				color: #fff;
			}
			
			&>button:last-child{
				color: #2CD6CB;
				border: 1px solid #2CD6CB;
				background: transparent;
			}
		}
	}
</style>
