<template>
    <grade-box :origin="origin">
        <div class="upgrade">
            <div class="title">
                <span>{{$t('RecommendAnIdentity')}}</span>
                <!-- <p><span>{{stockInfo}}</span>/50</p> -->
            </div>
            <div class="grade">
                <Button :disabled="origin >= 1" :class="grade === 1 ? 'active' : ''" @click="grade = 1">V1</Button>
                <Button :disabled="origin >= 2" :class="grade === 2 ? 'active' : ''" @click="grade = 2">V2</Button>
                <Button :disabled="origin >= 3" :class="grade === 3 ? 'active' : ''" @click="grade = 3">V3</Button>
            </div>
            <div class="text">
                <p>{{$t('Privileges')}} <span>V{{ grade }}{{$t('Identity')}}</span></p>
                <p>{{$t('BringAssets')}} <span>{{ token }} BOC</span></p>
                <p>{{$t('GiftPrivilege')}} <span v-text="shareLevel"></span></p>
            </div>
            <Button :disabled="origin >= 3" @click="buyGrade">{{$t('payImmediately')}}{{ price }} USDT</Button>
        </div>
        <authorization
		  tokenType="USDT"
		  :show="authorizedAccess" 
		  :click="authorizedAccessFun"
		  :needAuthorization='price' 
		  :authorizationEnds="authorizationEnds"
		  @authorizationEnds="end"
		></authorization>
    </grade-box>
</template>

<script>
    import { Button } from 'vant';

    export default {
        name: 'grade',
        components: {
            Button,
            'grade-box': () => import(/* WebpackChunkName: 'grade' */ '@/components/pubilc/grade-box.vue'),
            'authorization': () => import(/* WebpackChunkName: 'grade' */ '@/components/pubilc/Authorization.vue')
        },
        data() {
            return {
                grade: 0,
                origin: 0,
                v1price: 0,
                v2price: 0,
                v3price: 0,
                usdt: '0',
                authorizedAccess: false,
                authorizationEnds: false,
                USDTAuthorizedAccess: '',
				stockInfoList: [0,0,0],
            }
        },
        computed: {
            shareLevel() {
                switch (this.grade) {
                    case 2:
                        return '3个V1';
                    case 3:
                        return '4个V1+3个V2';
                    case 1:
                    default:
                        return 0;
                }
            },
            token() {
                switch (this.grade) {
                    case 1:
                        return this.v1Token;
                    case 2:
                        return this.v2Token;
                    case 3:
                        return this.v3Token;
                    default:
                        return 0;
                }
            },
            price() {
                switch (this.grade) {
                    case 1:
                        return this.v1price;
                    case 2:
                        return this.v2price;
                    case 3:
                        return this.v3price;
                    default:
                        return 0;
                }
            },
			// stockInfo() {
			//     switch (this.grade) {
			//         case 1:
			//             return 50 - Number(this.stockInfoList[0].toString());
			//         case 2:
			//             return 50 - Number(this.stockInfoList[1].toString());
			//         case 3:
			//             return 50 - Number(this.stockInfoList[2].toString());
			//         default:
			//             return 0;
			//     }
			// }
        },
        methods: {
            buyGrade() {
                if(Number(this.usdt) < Number(this.price)){
					return this.$toast.fail(this.$t('InsufficientBalance'));
				}else if(Number(this.USDTAuthorizedAccess) < Number(this.price)){
					return this.authorizedAccess = true;
                }
				
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'paymentDLevel(uint256)',
                    parameter: [{ type: 'uint256', value: this.grade }]
				}).then(result => {
					
					this.$contracts.Manager.paymentDLevel(this.grade).send({
						feeLimit: this.$store.state.generalFeeLimit.toSun() 
					}).then(res=>{
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.$router.back();
									clearInterval(interval);
								}
							})
						}, 1000);
					})
                })
            },
            authorizedAccessFun(value,active) {
				if(active === 1){
					var approveValue = this.$store.state.totalSupply
				}else{
					var approveValue = value
				}
				this.$contracts.ERC20Token.approve(
					this.$contracts.Manager.address,
					approveValue.toSun()
				).send({feeLimit: this.$store.state.generalFeeLimit.toSun() }).then(res => {
					this.$store.commit('loadingShow',true)
					
					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(res).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								this.$store.commit('loadingShow',false)
								this.$tronWeb.call({
									contractAddress: this.$contracts.ERC20Token.address,
									method: 'allowance(address,address)',
									parameter: [
										{ type: "address", value: this.$address },
										{ type: "address", value: this.$contracts.Manager.address }
									]
								}).then(result => {
									this.authorizedAccess = false;
									this.authorizationEnds = true;
									this.USDTAuthorizedAccess = res.toString().fromSun();
									
									this.buyGrade();
								})
								clearInterval(interval);
							}
						})
					}, 1000);
				}).catch(console.error);
            },
            end(){
				this.authorizedAccess = false;
			},
            init() {
				//查询已购买等级数量
				// this.$tronWeb.call({
				// 	contractAddress: this.$contracts.Manager.address,
				// 	method: 'stockInfoList()',
				// 	issuerAddress: this.$address,
				// }).then(result => {
				// 	const res = result.constant_result[0].decode(['uint256[]'])
				// 	this.stockInfoList = res;
				// })
				
                //查询等级
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'levelOf(address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					const res = result.constant_result[0].decode(['uint256'], ['origin'])
					// 原等级
                    this.origin = res.origin.toNumber();
                    this.grade = this.origin < 3 ? this.origin + 1 : this.origin;
                })
				
                //购买V1需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 1 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);

                    this.v1price = price.toString().fromSun();
                    this.v1Token = sentToken.toString().fromSun();
                })

                //购买V2需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 2 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);
                    
                    this.v2price = price.toString().fromSun();
                    this.v2Token = sentToken.toString().fromSun();
                })

                //购买V3需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 3 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);

                    this.v3price = price.toString().fromSun();
                    this.v3Token = sentToken.toString().fromSun();
                })

                // usdt的额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'balanceOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					const res = result.constant_result[0].decode(['uint256']);
					this.usdt = res.toString().fromSun();
                })
                
                // 授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "address", value: this.$contracts.Manager.address }
					]
				}).then(result => {
					const res = result.constant_result[0].decode(['uint256']);
					this.USDTAuthorizedAccess = res.toString().fromSun();
				})
				
            }
        },
        created() {
            this.$tronWeb && this.init();
        }
    }
</script>

<style lang="scss" scoped>
	.box{
		// width: 100vw;
		// height: 100vh;
		// overflow-y: scroll;
	}
    .upgrade {
        border-radius: 20px;
        margin: 44px 0 62px;
        padding: 46px 22px 18px;
        box-shadow: 0 2px 4px rgba(#000, .3);
        background-color: #fff;
        &>button {
            width: 100%;
            height: 90px;
            color: #fff !important;
            font-size: 44px;
            box-shadow: 0 6px 12px rgba(#000, .16);
            margin-top: 10px;
			border-radius: 10px;
        }
        &>.text {
            color: #656363;
            font-size: 32px;
            text-align: left;
            &>p {
                margin-bottom: 14px;
                &:first-child>span, &:nth-child(2)>span {
                    color: #66BAB2;
                }
            }
        }
        &>.grade {
            display: flex;
            align-items: center;
            justify-content: space-between;
            &>button {
                width: 160px;
                height: 80px;
                border: 2px solid;
                border-color: #CECECE !important;
                border-radius: 8px;
                color: #666666;
                font-size: 44px;
                background: #F1F1F1;
                margin: 34px 0 30px;
                &.active {
                    border-color: #57A39C !important;
                    background-color: #66BAB2;
					color: #fff;
					box-shadow: 0 6px 12px rgba(#000, .16);
                }
            }
        }
        &>.title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #656363;
            font-size: 40px;
            &>p>span {
                color: #FFC41F;
            }
        }
    }
</style>