<template>
	<div class="box nodalDividend">
		<div class="navBox nodalDividendBox">
			<div class="getHelpDetails">
				<div class="reveiceIncome">
					<span>{{ rewardPool }}</span>
					<div>{{$t('receiveDividend')}}</div>
				</div>
				<div class="orderInfo">
					<div>
						<span>{{ $t('totalDividend') }}</span>
						<span>{{ totalReward }}<span>USDT</span></span>
					</div>
					<span></span>
					<div>
						<span>{{ $t('nextDividend') }}</span>
						<span>{{ time }}</span>
					</div>
				</div>
				<div class="incomeBg"></div>
			</div>
			<button @click="withdrawAward">{{$t('receive')}}</button>
			<div class="navBox-record" v-if="list.length > 0">
				<div v-for="(item, index) in list" :key="index">
					<div>
						<span>{{ $t('nodeDividend') }} - V{{item.lv}}</span>
						<span>{{ item.date }}</span>
					</div>
					<div>
						<span>+{{ item.amount }} USDT</span>
					</div>
				</div>
				<not-record v-if="!list.length" :text="NoRevenueRecord"></not-record>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button , Toast } from 'vant';
	
	export default{
		components:{ Button },
		data(){
			return {
				rewardPool: 0,
				totalReward: 0,
				time: '',
				list: [
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' }
				],
				NoRevenueRecord: this.$t('NoRevenueRecord')
			}
		},
		methods:{
			async init() {
				//分红池
				// this.$tronWeb.call({
				// 	contractAddress: this.$contracts.Pool.address,
				// 	method: 'availTotalAmouns(uint256)',
				// 	parameter: [{ type: 'uint256', value: 3 }]
				// }).then(res => {
				// 	this.rewardPool = res.constant_result[0].decode(['uint256']).toString().fromSun();
				// })

				//时间
				this.$contracts.Noder.latestBonusTime().call().then(res => {
					const current = res.toNumber() || new Date().getTime() / 1000;
					let time = new Date((current + (86400 * 7)) * 1000),
						year = time.getFullYear(),
						month = time.getMonth() + 1,
						day = time.getDate();

					this.time = `${year}/${month}/${day}`;
				})

				//累计分红
				this.$contracts.Noder.infomations(this.$address).call().then(res => {
					this.rewardPool = res.quota.toString().fromSun();
					this.totalReward = res.totalWithdrawable.toString().fromSun();
				})

				const block = await this.$tronWeb.getBlockNumber();
				const blockNumber = block < 100000 ? 0 : block - 100000;
				const types = [this.$t('expand'), this.$t('Executives'), this.$t('manager'), this.$t('culture')]

				this.$tronWeb.event({
					contractAddress: this.$contracts.Noder.address,
					method: 'Log_Bounds',
					size: 200,
				}).then(event => {
					if(!event.length) return;

					event.forEach(async item => {
						// if (window.tronWeb.address.fromHex(item.result.owner) === this.$address) {
							let lv = (await this.$tronWeb.call({
								contractAddress: this.$contracts.Relations.address,
								method: 'levelOf(address)',
								issuerAddress: this.$address,
								parameter: [{ type: "address", value: this.$address }]
							}).then()).constant_result[0].decode(['uint256']).toNumber()
							
							if(!(lv < Number(item.result.targetLv))){								
								this.list.push({
									time: item.result.time,
									amount: item.result.amount.fromSun(),
									type: types[Number(item.result.atype)],
									date: this.$toTime(item.result.time,'Y/M/D'),
									lv: item.result.targetLv
								})
								
								this.list.sort((a,b) => {
									return b.time - a.time
								})
							}
							
						// }
					})

					this.list.sort(function(a,b){
						return b.time - a.time;
					})
				})
			},
			withdrawAward(){
				this.$tronWeb.call({
					contractAddress: this.$contracts.Noder.address,
					method: 'withdrawAward()',
					issuerAddress: this.$address,
				}).then(res => {
					this.$contracts.Noder.withdrawAward().send({ 
						feeLimit: this.$store.state.importantFeeLimit.toSun(),
					}).then(res=>{
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.init()
									clearInterval(interval);
								}
							})
						}, 1000);
					})
				})
			},
		},
		created(){
			this.$tronWeb && this.init();
		}
	}
</script>

<style>
	.nodalDividend svg{
		font-size: 45px!important;
		color: #F9C061;
	}
</style>
<style lang="scss" scoped>
	.navBox-record {
		// color: #A1BDE1;
		color: #656363;
		font-size: 28px;
		position: relative;
		padding: 20px 40px 0;
		background-color: #fff;
		&>div {
			display: flex;
			// flex-direction: column;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #516A89;
			line-height: 1.3;
			&>div {
				display: flex;
				flex-direction: column;
				&:first-child {
					align-items: flex-start;
					&>span:first-child{
						color: #21C17A;
					}
				}
				&:last-child{
					font-size: 36px;
					&>span:first-child{
						color: #18A664;
					}
				}
			}
			&:last-child{
				border-bottom: none;
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}

	.nodalDividendBox{
		padding: 0;
		text-align: center;
		
		&>button {
			width: 400px;
			height: 96px;
			font-size: 40px;
			line-height: 96px;
			margin: 62px auto 62px;
			border: none;
			color: #fff;
		}
	}
	.getHelpDetails{
		width: 100%;
		background-image: url(../../assets/img/incomeTopBoxBg.png);
		background-size: 100% 100%;
		padding: 18px 0 152px;
		display: flex;
		flex-direction: column;
		position: relative;
		
		&>.incomeBg{
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
		
		&>.reveiceIncome{
			font-size: 70px;
			display: flex;
			flex-direction: column;
			align-items: center;
			margin: 55px 0 45px;
			color: #fafafa;
	
			&>div{
				font-size: 32px;
				margin-top: 16px;
				line-height: 1;
			}
		}
		
		&>.orderInfo{
			position: absolute;
			bottom: 0;
			width: 100%;
			display: flex;
			align-items: center;
			font-size: 32px;
			border-bottom-left-radius: 25px;
			border-bottom-right-radius: 25px;
			padding: 25px 0;
			color: #656363;
			
			&>div{
				flex: 1;
				display: flex;
				flex-direction: column;
				align-items: center;
				
				&>span:nth-child(1){
					font-size: 24px;
					margin-bottom: 4px;
				}
				
				&>span{
					&>span{
						font-size: 20px;
						margin-left: 5px;
					}
				}
			}
			
			&>span{
				width: auto;
				height: 40px;
				border-right: 2px solid #3F4047;
			}
		}
	}
</style>