import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


//创建VueX对象
const store = new Vuex.Store({
	state: {
		generalFeeLimit: '200',
		importantFeeLimit: '400',
		loading: false,
		authorizedAccess: false,
		exchangeSuccess: false,
		needAuthorization: '',
		suspensionButtonShow: true,
		totalSupply: 0,
		exchangeRateUSDToMC: 0,
		playRules: false,
		name: 'BOBO',
	},
	mutations: {
		changeAuthorizedAccess(state,data){
			state.authorizedAccess = data
		},
		changeNeedAuthorization(state,amount){
			state.needAuthorization = amount
		},
		loadingShow(state,data){
			state.loading = data
		},
		changeExchangeSuccess(state,data){
			state.exchangeSuccess = data
		},
		changeSuspensionButtonShow(state,data){
			state.suspensionButtonShow = data
		},
		changeTotalSupply(state,data){
			state.totalSupply = data
		},
		exchangeRateUSDToMC_Commit(state,data){
			state.exchangeRateUSDToMC = data
		},
		exchangePlayRules(state,data){
			state.playRules = data
		},
		exchangeName(state,data){
			state.name = data
		}
	},
	actions: {
	},
	modules: {
	}
})

export default store

