<template>
	<div class="box">
		<div class="navBox messageBox">
			<div class="content" :class="{width_height_100vwh:!list.length}">
				<div class="line" v-if="list.length"></div>
				<not-record v-else></not-record>
				<div class="list" v-for="(item,index) in list" :key="index">
					<span>{{item.time}}</span>
					<div class="title">{{item.title}}</div>
					<p>{{item.content}}</p>
					<div class="listBottom">
						<span>{{item.from}}</span>
						<div @click="pushMessageCard(item)">
							<img src="@/assets/img/share.png" />
							<!-- <self-icon type="share"></self-icon> -->
							<span>{{$t('Share')}}</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import selfIcon from '@/components/pubilc/self-icon'
	import notRecord from '@/components/pubilc/not-record'
	export default {
		components: { selfIcon , notRecord },
		data(){
			return {
				title: this.$t('News'),
				list: [
					// {
					// 	title: this.$t('title1'),
					// 	content: this.$t('content1'),
					// 	time: '2020/06/06 15:00',
					// 	from: 'DF.R Team'
					// },
					// {
					// 	title: this.$t('title2'),
					// 	content: this.$t('content2'),
					// 	time: '2020/06/01 12:00',
					// 	from: 'DF.R Team'
					// },
				]
			}
		},
		methods:{
			pushMessageCard(item){
				localStorage.setItem('message',JSON.stringify(item))
				this.$router.push('/messageCard')
			}
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		background: #fff;
	}
	.width_height_100vwh{
		width: 100vw;
		height: 100vh;
	}
	.navBox{
		// background-color: #273446!important;
		
		.content{
			padding: 30px 60px 0 70px;
			position: relative;
			
			.line{
				position: absolute;
				width: 0;
				height: 100%;
				left: 36px;
				top: 0;
				border-right: 1px solid #E3E3E3;
			}
			
			.list:last-child{
				margin-bottom: 0!important;
			}
			
			.list{
				font-size: 28px;
				text-align: left;
				margin-bottom: 60px;
				
				&>span{
					color: #999;
					position: relative;
				}
				
				&>span::after{
					content: '';
					width: 22px;
					height: 22px;
					position: absolute;
					border-radius: 50%;
					background-color: #21C17A;
					left: -44px;
					top: 50%;
					transform: translateY(-50%);
				}
				
				&>.title{
					font-size: 32px;
					color: #4e4e4e;
					margin: 18px 0 12px;
				}
				
				&>p{
					color: #666;
				}
				
				&>.listBottom{
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin-top: 32px;
					color: #999;
					
					&>span{
						font-size: 24px;
					}
					
					&>div{
						color: #1492E6;
						display: flex;
						align-items: center;
						line-height: 1;
						// line-height: 1.2;
						
						&>img{
							width: 40px;
							margin-right: 18px;
						}
					}
				}
			}
		}
	}
</style>
