<template>
	<div class="box drawCompensateBox colorA1BDE1">
		<img v-lazy="require('@/assets/img/logo.png')">
		<p>Bobo paradise</p>
		<pre>
	{{$t('CompensationDescription')}}
	   {{$t('CompensationDescriptionText')}}
		</pre>
		<div>
			<div>
				<div class="bgColor273446">
					<div>
						<img v-lazy="require('@/assets/img/draw01.png')">
						<span>{{$t('TotalPH')}}</span>
					</div>
					<span>{{ join }} USDT</span>
				</div>
				<div class="bgColor273446">
					<div>
						<img v-lazy="require('@/assets/img/draw02.png')">
						<span>{{$t('TotalGH')}}</span>
					</div>
					<span>{{ income }} USDT</span>
				</div>
			</div>
			<div class="bgColor273446">
				<div>
					<img v-lazy="require('@/assets/img/draw03.png')">
					<span>{{$t('CompensationAmount')}}</span>
				</div>
				<span>{{ amount }} BOB</span>
			</div>
		</div>
		<Button @click="draw" v-ButClickLimit>{{$t('receive01')}}</Button>
	</div>
</template>

<script>
	import { Button } from 'vant'
	
	export default {
		components: { Button },
		data() {
			return {
				join: '0',
				income: '0',
				amount: '0',
				theme: localStorage.getItem('theme')
			}
		},
		created() {
			this.$store.commit('changeSuspensionButtonShow',false)
			
			this.$tronWeb && this.init();
		},
		methods: {
			init() {
				this.getData();
			},
			getData() {
				Promise.all([
					// this.$contracts.Pool.dataStateVersion().call(),
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'settlementCompensate()',
						issuerAddress: this.$address
					})
				]).then(([ SettlementCompensate ]) => {
					const settlementCompensate = SettlementCompensate.constant_result[0].decode(['uint256']).toString().fromSun();
					if (Number(settlementCompensate) === 0) this.$router.replace('/end');
					this.amount = settlementCompensate;
					
					
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'userInfoMapping(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}).then(res=>{
						const { totalIn, totalOut, awardQuota , awardWithdrawableTotal } = res.constant_result[0].decode(
							['uint256', 'uint256', 'uint256', 'uint256'],
							['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal'],
							
						);
						this.join = totalIn.toString().fromSun();
						this.income = totalOut.toString().fromSun();
					})
				})
			},
			draw() {
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'settlementCompensate()',
					issuerAddress: this.$address
				}).then(SettlementCompensate => {
					this.$contracts.OrderManager.settlementCompensate().send({ 
						feeLimit: this.$store.state.generalFeeLimit.toSun() 
					}).then(res=>{
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.init()
									this.$router.push('/end')
									clearInterval(interval);
								}
							})
						}, 1000);
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		text-align: center;
		padding: 54px 38px 0;
		&>img {
			width: 128px;
		}
		&>pre {
			width: 100%;
			font-size: 28px;
			text-align: left;
			line-height: 1.5;
			padding: 30px 24px 0;
			white-space: pre-wrap;
			border: 2px solid #1492E6;
			border-radius: 15px;
			background-color: #fff;
		}
		&>p{
			color: #fff;
			font-size: 36px;
			margin: 40px 0;
		}
		&>div {
			width: 100%;
			display: flex;
			align-items: center;
			flex-direction: column;
			margin: 40px 0 92px;
			&>div {
				width: 100%;
				&:first-child {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 40px;
					&>div {
						background-color: #fff;
						border: 2px solid #1492E6;
						font-size: 40px;
						width: calc(50% - 17px);
						padding: 34px 0 24px;
						border-radius: 10px;
						// background-color: #273446;
						&>div {
							display: flex;
							align-items: center;
							justify-content: center;
							font-size: 32px;
							margin-bottom: 16px;
							&>img {
								margin-right: 44px;
							}
						}
						&:first-child>div>img {
							width: 50px;
							height: 50px;
						}
						&:last-child>div>img {
							width: 48px;
							height: 50px;
						}
					}
				}
				&:last-child {
					background-color: #fff;
					border: 2px solid #1492E6;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 40px;
					border-radius: 10px;
					// background-color: #273446;
					padding: 38px 0 16px;
					&>div {
						font-size: 32px;
						display: flex;
						align-items: center;
						flex-direction: column;
						margin-right: 136px;
						&>img {
							width: 43px;
							height: 50px;
							margin-bottom: 24px;
						}
					}
				}
			}
		}
		&>button {
			width: 400px;
			height: 96px;
			font-size: 35px;
			line-height: 96px;
			border-radius: 20px;
			border: none;
			color: #fff;
		}
	}
</style>
