<script>
	import Swiper from 'swiper'
	import 'swiper/css/swiper.min.css'
	import 'swiper/js/swiper.min.js'
	export default{
		data(){
			return {
				yubei: 0,
				baowu: 0,
				jiangchi: 0,
				huodong: 0,
				ranshao: 0,
				totalAddresses: 0,
				totalIn: 0,
				title: this.$t('dashBoard')
			}
		},
		methods:{
			async init(){
				Promise.all([
					this.$contracts.Pool.availTotalAmouns(0).call(), //幸运池
					this.$contracts.Pool.availTotalAmouns(1).call(), //探索基金
					this.$contracts.Pool.availTotalAmouns(2).call(), //宝物基金
					// this.$contracts.Relations.totalAddresses().call(), // 地址总量
					this.$tronWeb.call({
						contractAddress: this.$contracts.ERC777_1Token.address,
						method: 'balanceOf(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: window.tronWeb.address.toHex('0x0000000000000000000000000000000000000000') }]
					}),
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'userInfoMapping(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}),
				]).then(async ([ AvailTotal1, AvailTotal2, AvailTotal3, Balance , userInfoMapping ]) => {
					const { totalIn, totalOut, awardQuota , awardWithdrawableTotal } = userInfoMapping.constant_result[0].decode(
						['uint256', 'uint256', 'uint256', 'uint256'],
						['totalIn', 'totalOut', 'awardQuota', 'awardWithdrawableTotal']
					);
					
					this.jiangchi = Math.floor(AvailTotal1.toString().fromSun());
					this.yubei = Math.floor(AvailTotal2.toString().fromSun());
					this.baowu = Math.floor(AvailTotal3.toString().fromSun());
					// this.totalAddresses = Addresses.toString();
					this.ranshao = Math.floor(Balance.constant_result[0].decode(['uint256']).toString().fromSun());
					this.totalIn = totalIn.toString().fromSun()
				}).catch(console.error)
			}
		},
		mounted() {
			var mySwiper = new Swiper ('.swiper-container', {
				direction: 'horizontal', // 垂直切换选项
				//loop: true, // 循环模式选项
				
				// 如果需要分页器
				pagination: {
					el: '.swiper-pagination',
				},
				// slidesPerView : 3,
				spaceBetween: 20,
				
				autoHeight: true, //高度随内容变化
			})
			
			this.$tronWeb && this.init()
		},
	}
</script>

