<template>
	<popup v-model="showFun" @click-overlay='clickOverlay' position="bottom">
		<div class="authorizedAccessBox">
			<!-- <div class="authorizedAccessTop">
				<div><img src="../assets/yellowRing.png"/></div>
				<p>{{$t('Participation')}}</p>
			</div> -->
			<div class="authorizedAccessTop">
				<div><img src="@/assets/img/greenKey.png"/></div>
				<p>{{$t('ContractAuthorization')}}</p>
				<!-- <span>{{authorizedAccessTime}}</span> -->
			</div>
			<!-- <div class="authorizedAccessText">{{$t('ContractAuthorizationText01')}}</div> -->
			<div class="authorizedAccessSelect">
				<div :class="{active: active === 0}" @click="active = 0">
					<div>
						<div>
							<span>{{$t('AuthorizationManagementText11')}}</span>
							<span>{{$t('AuthorizationManagementText12')}}</span>
						</div>
						<span>{{$t('AuthorizationManagementText13')}}</span>
					</div>
					<icon name="success"></icon>
				</div>
				<div :class="{active: active === 1}" @click="active = 1">
					<div>
						<div>
							<span>{{$t('AuthorizationManagementText14')}}</span>
							<span>{{$t('AuthorizationManagementText15')}}</span>
						</div>
						<span>{{$t('AuthorizationManagementText16')}}</span>
					</div>
					<icon name="success"></icon>
				</div>
			</div>
			<!-- <div class="authorizedAccessInput">
				<p>{{$t('ContractAuthorizationText02')}}</p>
				<div>
					<div>{{authorizedAccessNumber}} {{tokenTypeFun}}</div>
				</div>
			</div> -->
			<div class="authorizedAccessButton" @click="click(authorizedAccessNumber,active)" v-ButClickLimit>{{$t('Commit')}}</div>
		</div>
	</popup>
</template>

<script>
	import { Popup , Icon } from 'vant';
	export default {
		components:{ Popup , Icon },
		data () {
			return {
				availableAuthorizationsFun: this.availableAuthorizations,//暂时无用
				showFun: this.show,//显示
				needAuthorizationFun: this.needAuthorization,//数量
				// authorizedAccessTime: '',//时间
				authorizedAccessNumber: '',//输入框的数量
				tokenTypeFun: this.tokenType,//token类型
				active: 0,
			}
		},
		props:{
			show:{
				type: Boolean,
				default: false
			},
			click:{
				type: Function
			},
			needAuthorization: {},
			availableAuthorizations: {},
			tokenType: {
				type: String
			},
			authorizationEnds:{
				type: Boolean,
				default: false
			},
		},
		watch:{
			show(){
				this.showFun = this.show;
				// this.authorizedAccessTime = this.$toTime(new Date().getTime()/1000,'Y/M/D H:i')
				this.needAuthorizationFun = this.needAuthorization
				this.authorizedAccessNumber = this.needAuthorization
			},
			needAuthorization(){
				this.needAuthorizationFun = this.needAuthorization
				this.authorizedAccessNumber = this.needAuthorization
			},
			availableAuthorizations(){
				this.availableAuthorizationsFun = this.availableAuthorizations
			},
			authorizationEnds(){
				if(this.authorizationEnds){
					this.authorizedAccessNumber = '';
				}
			},
			tokenType(){
				this.tokenTypeFun = this.tokenType
			}
		},
		methods:{
			authorizedAccessInput(){
				this.authorizationEnds = false;
			},
			clickOverlay(){
				this.authorizedAccessNumber = ''
				this.$emit('authorizationEnds','end')
			}
		}
	}
</script>

<style scoped lang="scss">
	.van-popup{
		background: transparent;
	}
	.authorizedAccessBox{
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 60px 40px;
		background: #fff;
		border-radius: 50px 50px 0 0;
		color: #080808;
		
		&>div{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		
		.authorizedAccessSelect{
			width: 100%;
			
			&>div{
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 40px;
				
				&>div{
					flex: 1;
					font-size: 28px;
					color: #B4B4B4;
					display: flex;
					justify-content: space-between;
					align-items: center;
					background-color: #F1F1F1;
					padding: 20px 38px 12px 20px;
					border-radius: 12px;
					
					&>div{
						display: flex;
						flex-direction: column;
						align-items: flex-start;
						line-height: 1.3;
						&>span:first-child{
							font-size: 40px;
							color: #4E4E4E;
						}
					}
				}
				
				&>i{
					visibility: hidden;
					font-size: 60px;
					color: #66BAB2;
					margin-left: 32px;
				}
			}
			
			&>.active{
				&>i{
					visibility: visible;
				}
			}
		}
		
		.authorizedAccessTop{
			width: 100%;
			font-size: 32px;
			border-bottom: 1px solid #3F4047;
			// color: #A1BDE1;
			padding-bottom: 28px;
			margin-bottom: 46px;
			
			&>div{
				width: 106px;
				height: 106px;
				border-radius: 50%;
				// border: 5px solid #FFCE00;
				margin-bottom: 30px;
				
				img{
					width: 100px;
					height: 112px;
				}
			}
			
			&>span{
				font-size: 24px;
			}
		}
		
		.authorizedAccessText{
			position: relative;
			font-size: 28px;
			// color: #A1BDE1;
			margin-bottom: 46px;
			text-align: left;
			padding-left: 10px;
		}
		
		.authorizedAccessText:after{
			position: absolute;
			content: '';
			width: 6px;
			height: 32px;
			// background-color: rgba(58,156,255,1);
			left: -15px;
			top: 3px;
		}
		
		.authorizedAccessInput{
			width: 100%;
			font-size: 26px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 56px;
			
			&>div{
				display: flex;
				font-size: 36px;
				width: 100%;
				color: #1492E6;
				// height: 110px;
				// border: 2px solid #516a89;
				border-radius: 10px;
				align-items: center;
				margin: 18px 0 14px;
				background-color: #E3E4E5;
				padding: 10px 20px;
				
				// &>div{
				// 	display: flex;
				// 	align-items: center;
				// 	justify-content: center;
				// 	width: 194px;
				// 	height: 100%;
				// 	font-size: 40px;
				// 	border-left: 2px solid #3A9CFF;
				// }
				
				// &>input{
				// 	flex: 1;
				// 	width: 100%;
				// 	height: 100%;
				// 	background-color: transparent;
				// 	border: none;
				// 	padding-left: 38px;
				// }
				
			}
			
			&>p{
				color: #707070;
				font-size: 36px;
				padding-left: 12px;
			}
			
			&>span{
				color: #A1BDE1;
			}
		}
		
		.authorizedAccessButton{
			width: 390px;
			height: 80px;
			background: linear-gradient(90deg, #28BEAF 0%, #3080CE 100%);
			border-radius: 10px;
			font-size: 32px;
			color: #fff;
		}
	}
</style>