<template>
	<div class="box">
		<div class="navBox registerBox">
			<div>
				<div class="registerTop">
					<img v-lazy="require('@/assets/img/logo.png')">
					<img v-lazy="require('@/assets/img/logoName.png')">
				</div>
				<div class="registerBottom">
					<!-- <span>{{$t('WelcomeToDeFiEra')}}</span> -->
					<span>{{$t('WelcomeToDeFiEra')}}</span>
					<div class="content">		
						<div>
							<span>{{$t('EnterNickname')}}</span>
							<input type="text" v-model="name" :placeholder="$t('EnterYourNickname')" maxlength="6">
						</div>
						<div>
							<span>{{$t('BindInviter')}}</span>
							<input type="text" v-model="fromCode" :placeholder="$t('inviterText01')" maxlength="6" @input="fromCode=fromCode.replace(/[\W]/g,'')">
						</div>
						<div>
							<span>{{$t('inviterText02')}}</span>
							<input type="text" v-model="code" :placeholder="$t('inviterText03')" maxlength="6" @input="code=code.replace(/[\W]/g,'')">
						</div>
					</div>
					<Button @click="bind" v-ButClickLimit>{{$t('Commit')}}</Button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button } from 'vant'
	
	export default {
		components: { Button },
		data() {
			return {
				title: this.$t('registered'),
				code: '',
				name: '',
				fromCode: '',
				theme: localStorage.getItem('theme'),
				balance: 0
			}
		},
		methods: {
			async bind() {
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/

				if (this.fromCode.length !== 6) {
					return this.$toast.fail(this.$t('warningText14'))
				} else if (!reg.test(this.name) || this.name.length > 6) {
					return this.$toast.fail(this.$t('warningText13'))
				} else if (this.code.length !== 6) {
					return this.$toast.fail(this.$t('warningText15'))
				} else if (this.balance <= 0) {
					return this.$toast.fail(this.$t('warningText06'))
				}
				
				let code = this.code.toUpperCase().stringToHex(),
					name = this.name.stringToHex(),
					fromCode = this.fromCode.toUpperCase().stringToHex(),
				    thisAddress = (await this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'shortCodeToAddress(bytes6)',
						issuerAddress: this.$address,
						parameter: [{ type: "bytes6", value: code }]
					}).then()).constant_result[0].decode(['address']),
				    fromAddress = (await this.$tronWeb.call({
						contractAddress: this.$contracts.Relations.address,
						method: 'shortCodeToAddress(bytes6)',
						issuerAddress: this.$address,
						parameter: [{ type: "bytes6", value: fromCode }]
					}).then()).constant_result[0].decode(['address']);
				
				if (!parseInt(fromAddress, 16)) {
				    return this.$toast.fail(this.$t('warningText16'))
				} else if (!parseInt(thisAddress, 16)) {
				    return this.$toast.fail(this.$t('warningText17'))
				}

				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
					method: 'addRelationEx(address,bytes6,bytes16)',
					issuerAddress: this.$address,
					parameter: [
						{ type: "address", value: fromAddress },
						{ type: "bytes6", value: code },
						{ type: "bytes16", value: this.PrefixZero(name,32) }
					]
				}).then(async result => {
					const res = result.constant_result[0].decode(['uint8']);
					const errorMessage = ['', this.$t('registeredWarningText01'), this.$t('registeredWarningText02'), this.$t('registeredWarningText03'), this.$t('registeredWarningText04')];
					console.log(res)
					if (res === 0) {
						this.$store.commit('loadingShow',true)
						this.$contracts.Relations.addRelationEx(
							fromAddress,
							code,
							this.PrefixZero(name,32)
						).send({ shouldPollResponse: true , feeLimit: this.$store.state.generalFeeLimit.toSun() })
						.then(result=>{
							this.$store.commit('loadingShow',false)
							this.$toast.success(this.$t('warningText18'));
							this.$router.back();
						});
					} else {
						return this.$toast.fail(errorMessage[res]);
					}
				})
			},
			GetQueryString(name) {
				return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
			},
			async init() {
				this.$tronWeb.getBalance().then(balance => {
					this.balance = Number(balance.fromSun());
				})

				// this.fromCode = this.GetQueryString('code')
				if(localStorage.getItem('invitationCode') && localStorage.getItem('invitationCode').length == 6){				
					this.fromCode = localStorage.getItem('invitationCode');
					const fromAddress = await this.$contracts.Relations.ShortCodeToAddress(this.fromCode.toUpperCase().stringToHex()).call();
					if (!parseInt(fromAddress, 16)) return this.fromCode = '';
				}
			}
		},
		created() {
			this.$tronWeb && this.init();
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		padding: 120px 40px 0;
		display: flex;
		flex-direction: column;
		
		.registerTop{
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 76px;
			
			&>img:first-child{
				width: 128px;
			}
			
			&>img:last-child{
				width: 206px;
				margin-top: 40px;
			}
		}

		.registerBottom{
			font-size: 36px;
			display: flex;
			flex-direction: column;

			&>span{
				width: 100%;
				text-align: left;
				margin-bottom: 14px;
				color: #fff;
			}
		}
		
		.content{
			width: 100%;
			height: 675px;
			padding: 70px 40px 0;
			border-radius: 0 40px 0 40px;
			background-color: #fff;
			box-shadow: 0 2px 4px rgba(#000, 0.3);
			border-radius: 0 40px 0 40px;
			
			&>div{
				width: 100%;
				font-size: 36px;
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				margin-bottom: 38px;
				color: #666666;
				
				&>input{
					width: 100%;
					padding: 30px 0;
					background-color: transparent;
					border: none;
					border-bottom: 2px solid #C8C9CC;
					font-size: 28px;
				}

				&>input::placeholder{
					color: #AFAFAF;
				}
			}
		}

		button {
			width: 400px;
			height: 96px;
			font-size: 40px;
			line-height: 96px;
			margin: 0 auto;
			border-radius: 50px;
			background: linear-gradient(90deg, #28BEAF 0%, #3080CE 100%);
			border: none;
			color: #fff;
			box-shadow: 0 4px 12px rgba(#000, 0.3);
		}
	}
</style>
