<template>
	<div class="box" id="indexId">
		
		<home :origin="origin"></home>
		<CurrentOrder></CurrentOrder>
		<my-income></my-income>
		<lazy-component>
			<Exchange></Exchange>
			<Status></Status>
			<lucky-down></lucky-down>
		</lazy-component>
		<smart-contract></smart-contract>
		<risk-warning></risk-warning>
		<About></About>
		
		<overlay :show="playRules" class="playRules" @click="$store.commit('exchangePlayRules',false)">
			<div class="">
				<div class="title">{{$t('HomeText04')}}</div>
				<div class="content">
					<p>1、{{$t('InvestmentDescriptionText01')}}；</p>
					<p>2、{{$t('InvestmentDescriptionText02')}}；</p>
					<p>3、{{$t('InvestmentDescriptionText03')}}；</p>
					<p>{{$t('InvestmentDescriptionText04')}}；</p>
					<p>4、{{$t('InvestmentDescriptionText05')}}；</p>
					<p>5、{{$t('InvestmentDescriptionText06')}}；</p>
					<p>6、{{$t('InvestmentDescriptionText07')}}；</p>
					<p>7、{{$t('InvestmentDescriptionText08')}}；</p>
				</div>
			</div>
		</overlay>
		
		<overlay :show="lucky" class="lucky">
			<div @click.stop>
				<img src="@/assets/img/boxYellow.png" />
				<img src="@/assets/img/pauseable.png" />
				<span>{{$t('LuckyPlayer')}}</span>
				<span>{{luckyAmout}} <span>USDT</span></span>
				<div @click="WithdrawLuckAward" v-ButClickLimit>{{$t('GetItNow')}}</div>
			</div>
		</overlay>
		
		<overlay :show="upLv" class="upLv" @click="upLv = false">
			<div @click.stop>
				<img v-if="Number(current) == 1" src="@/assets/img/upLv1.png" />
				<img v-if="Number(current) == 2" src="@/assets/img/upLv2.png" />
				<img v-if="Number(current) == 3" src="@/assets/img/upLv3.png" />
				<img v-if="Number(current) == 4" src="@/assets/img/upLv4.png" />
				<img v-if="Number(current) == 5" src="@/assets/img/upLv5.png" />
				<div>{{$t('Promoted')}}D{{current}}{{$t('level')}}</div>
				<div @click="upLvFun" v-ButClickLimit>{{$t('receive')}}</div>
			</div>
		</overlay>
		
		<authorization
		:show="authorizedAccess"
		:click="authorizedAccessFun"
		:needAuthorization='needAuthorization'
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
	</div>
</template>

<script>
import Home from '@/components/home.vue'
import CurrentOrder from '@/components/currentOrder.vue'
import MyIncome from '@/components/myIncome.vue'
import Exchange from '@/components/exchange.vue'
import LuckyDown from '@/components/luckyDown.vue'
import Status from '@/components/status.vue'
import SmartContract from '@/components/smartContract.vue'
import RiskWarning from '@/components/riskWarning.vue'
import About from '@/components/about.vue'
import { Overlay } from 'vant';
import Authorization from '@/components/pubilc/Authorization.vue'
import { mapState , mapActions } from 'vuex'
export default {
	name: 'index',
	components:{
		Home,CurrentOrder,MyIncome,Exchange,LuckyDown,Status,SmartContract,RiskWarning,About,Overlay,Authorization
	},
	data(){
		return {
			lucky: false,
			luckyAmout: 0,
			authorizationEnds: false,
			name: 'BOBO',
			upLv: false,
			current: '',
			origin: '',
		}
	},
	computed:{
		...mapState(['authorizedAccess']),
		...mapState(['needAuthorization']),
		...mapState(['playRules'])
	},
	mounted(){
		this.$tronWeb && this.init()
	},
	methods:{
		async init(){
			let currentTime = await this.$tronWeb.getBlockTime();
			
			this.$tronWeb.call({
				contractAddress: this.$contracts.Manager.address,
				method: 'upgradeDLevel()',
				issuerAddress: this.$address,
			}).then(res => {
				const { origin , current } = res.constant_result[0].decode(['uint256','uint256'],['origin','current'])
				this.origin = origin.toString()
				
				// 可升等级
				this.current = current.toString();
				// console.log(this.origin,this.current)
				if(this.current != this.origin){
					this.upLv = true;
				}
			})
			
			// 是否需要暂停
			this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'deadlineTime()',
				issuerAddress: this.$address,
				parameter: []
			}).then(res => {
				if(res.constant_result[0].decode(['uint256']).toNumber() && currentTime > res.constant_result[0].decode(['uint256']).toNumber()){
					// 是否需要暂停
					this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'isLuckDog(address)',
						issuerAddress: this.$address,
						parameter: [{ type: "address", value: this.$address }]
					}).then(async res => {
						const IsLuckDog = res.constant_result[0].decode(['bool','uint256','bool'],['isluckDog','award','canwithdrawable'])
						if (IsLuckDog.isluckDog && IsLuckDog.canwithdrawable) {
							this.lucky = true;
							this.luckyAmout = IsLuckDog.award.toString().fromSun();
						} else {
							this.lucky = false;
							const death = await this.$contracts.OrderManager.death().call();
							if(death){
								this.$router.replace('/drawCompensate')
							}
						}
					})
				}
			})
			
		},
		WithdrawLuckAward() {
			this.$tronWeb.call({
				contractAddress: this.$contracts.OrderManager.address,
				method: 'withdrawLuckAward()',
				issuerAddress: this.$address
			}).then(WithdrawLuckAward => {
				this.$contracts.OrderManager.withdrawLuckAward()
				.send({feeLimit: this.$store.state.generalFeeLimit.toSun() }).then(res=>{
					this.$store.commit('loadingShow',true)
					
					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(res).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								this.$store.commit('loadingShow',false)
								this.init()
								this.$router.replace('/drawCompensate')
								clearInterval(interval);
							}
						})
					}, 1000);
				})
			})
		},
		authorizedAccessFun(value,active){
			if(active === 1){
				var approveValue = this.$store.state.totalSupply
			}else{
				var approveValue = value
			}
			this.$contracts.ERC20Token.approve(this.$contracts.Swap.address, approveValue.toSun()).send({
				feeLimit: this.$store.state.generalFeeLimit.toSun()
			}).then(res => {
				this.$store.commit('loadingShow',true)
				
				const interval = setInterval(() => {
					window.tronWeb.trx.getTransaction(res).then(transaction => {
						if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
							this.$store.commit('loadingShow',false)
							this.$store.commit('changeAuthorizedAccess',false)
							this.authorizationEnds = true;
							this.$store.commit('changeExchangeSuccess',true)
							clearInterval(interval);
						}
					})
				}, 1000);
				
			}).catch(console.error);
		},
		end() {
			this.$store.commit('changeAuthorizedAccess',false)
		},
		upLvFun(){
			this.$contracts.Manager.upgradeDLevel().send({
				feeLimit: this.$store.state.generalFeeLimit.toSun()
			}).then(res => {
				this.$store.commit('loadingShow',true)
				
				const interval = setInterval(() => {
					window.tronWeb.trx.getTransaction(res).then(transaction => {
						if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
							this.$store.commit('loadingShow',false)
							this.upLv = false
							this.init()
							clearInterval(interval);
						}
					})
				}, 1000);
				
			}).catch(console.error);
		}
	},
}
</script>

<style lang="scss" scoped>
	.box{
		// width: 100vw;
		// height: auto;
		// height: 6430px;
		// background: linear-gradient(180deg, #4E9FF9 0%, #86D3FF 15%, #BFFDF1 24%, #86D3FF 41%, #BFFDF1 56%, #BFFDF1 67%, #86D3FF 100%);
	}
	
	.upLv{
		// padding: ;
		&>div{
			position: relative;
			&>img{
				width: 100%;
			}
			&>div:nth-child(2){
				position: absolute;
				top: 548px;
				color: #FFEA50;
				font-size: 50px;
				font-weight: bold;
				left: 50%;
				transform: translateX(-50%);
			}			
			&>div:nth-child(3){
				position: absolute;
				top: 672px;
				width: 261px;
				height: 83px;
				color: #FAFAFA;
				border: 2px solid #FFEA50;
				font-size: 40px;
				left: 50%;
				transform: translateX(-50%);
				text-align: center;
				line-height: 83px;
				border-radius: 58px;
			}
		}
	}
	
	.lucky{
		z-index: 3!important;
		display: flex;
		align-items: flex-start;
		padding-top: 100px;
		
		&>div{
			position: relative;
			
			&>img:nth-child(1){
				width: 751px;
				height: 752px;
				animation: myfirst 5s infinite;
				animation-timing-function: linear;
			}
			
			&>img:nth-child(2){
				width: 242px;
				height: 222px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}
			
			@keyframes myfirst{
				100% {transform: rotate(360deg);}
			}
			
			&>span:nth-child(3){
				position: absolute;
				color: #FFEA50;
				font-size: 50px;
				top: 75%;
				left: 50%;
				transform: translate(-50%,-75%);
				white-space:nowrap;
			}
			
			&>span:nth-child(4){
				position: absolute;
				color: #FFEA50;
				font-size: 80px;
				top: 90%;
				left: 50%;
				transform: translate(-50%,-90%);
				
				&>span{
					font-size: 50px;
				}
			}
			
			&>div{
				position: absolute;
				width: 261px;
				height: 83px;
				border-radius: 58px;
				border: 2px solid #FFEA50;
				font-size: 40px;
				text-align: center;
				line-height: 83px;
				bottom: -50px;
				left: 50%;
				transform: translateX(-50%);
				color: #fff;
			}
		}
	}
	
	.playRules{
		padding: 116px 40px 62px;
		display: flex;
		align-items: center;
		text-align: center;
		
		&>div{
			width: 100%;
			// height: 100%;
			// color: #A1BDE1;
			border-radius: 10px;
			background-color: #fff;
	
			&>.title{
				width: 100%;
				height: 110px;
				font-size: 36px;
				line-height: 110px;
				border-bottom: 1px solid #AFAFAF;
			}
	
			&>.content{
				width: 100%;
				// height: calc(100% - 110px);
				// flex: 1;
				padding: 30px 20px 30px 30px;
				font-size: 28px;
				overflow-y: scroll;
				text-align: left;
				line-height: 1.6;
	
				.marginBottom{
					margin-bottom: 20px;
				}
			}
		}
	}
</style>