<template>
	<div v-if="toggleView">		
		<router-view/>
		
		<overlay :show="otherButton" @click="otherButton=!otherButton">
		</overlay>
		
		<div class="suspensionButton" v-if="suspensionButtonShow">
			<div class="button" :class="{indexButton: show}" @click="!show ? $router.back() : otherButton = !otherButton"></div>
			<div class="otherButton" v-show="otherButton">
				<div class="list1" @click="isRegister" v-if="$store.state.name">
					<div>
						<img src="@/assets/img/list1.png" />
					</div>
					<span>{{$store.state.name == 'BOBO' ? $t('registered') : $t('InvitationLink')}}</span>
				</div>
				<div class="list2" @click="$router.push('/friendsList');otherButton = !otherButton">
					<div>
						<img src="@/assets/img/list2.png" />
					</div>
					<span>{{$t('InvitationList')}}</span>
				</div>
				<div class="list3" @click="$router.push('/compensate');otherButton = !otherButton">
					<div>
						<img src="@/assets/img/list3.png" />
					</div>
					<span>{{$t('ReceiveDT')}}</span>
				</div>
				<div class="list4" @click="$router.push('/nodalDividend');otherButton = !otherButton">
					<div>
						<img src="@/assets/img/list4.png" />
					</div>
					<span>{{$t('USDTAuthorizationManagement')}}</span>
				</div>
			</div>
		</div>
		
		
		<overlay :show="IsBreaker" class="IsBreaker">
			<div @click.stop>
				<img src="@/assets/img/logo.png" />
				<img src="@/assets/img/logoName.png" />
				<span>{{$t('AccountLocked')}}</span>
				<p>{{$t('blacklistText01')}}{{CONFIG_ResolveBreakerDTAmount}}BOB！</p>
				<div @click="ResolveBreaker" v-ButClickLimit>{{$t('blacklistText02')}}</div>
				<span @click="$router.push('/')">{{$t('blacklistText03')}}</span>
			</div>
		</overlay>
		
		<overlay :show="pauseable && !death" class="pauseable">
			<div @click.stop>
				<div>
					<img src="@/assets/img/boxYellow.png" />
					<img src="@/assets/img/pauseable1.png" />
					<span>{{$t('SystemDraw')}}<br/>{{$t('PleaseWait')}}......</span>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import Vue from 'vue'
	import { TronWeb, Contracts } from '@/common/init'
	import { Overlay } from 'vant'
	import { mapState , mapActions } from 'vuex'
	export default{
		components:{
			Overlay
		},
		data(){
			return{
				show: false,
				toggleView: true,
				otherButton: false,
				name: true,
				IsBreakerShow: false,
				IsBreaker: false,
				DT: 0,
				pauseable: false,
				death: true,
				CONFIG_ResolveBreakerDTAmount: '',
			}
		},
		computed:{
			...mapState(['loading']),
			...mapState(['suspensionButtonShow'])
		},
		watch:{
			$route(){
				if(this.IsBreaker){
					if(this.$route.name == 'Index'){
						this.IsBreakerShow = false
						this.show = true
					}else{
						this.IsBreakerShow = true;
						this.show = false
					}
				}else{
					if(this.$route.name == 'Index'){
						this.show = true
					}else{
						this.show = false
					}
				}
			},
			loading(){
				if(this.loading){
					this.$toast.loading({
						message: this.$t('inTransaction'),
						forbidClick: true,
						duration: 0,
					});
				}else{
					this.$toast.clear()
				}
			}
		},
		mounted() {
			if(this.$route.name == 'Index'){
				this.show = true
			}else{
				this.show = false;
			}
			
			window.onload = () => {
				const interval = setInterval(() => {
					if (window.tronWeb.ready) {
						this.init();
						clearInterval(interval);
					}
				});
			}
		},
		methods:{
			init() {
				let locale = localStorage.getItem('locale')
				if (locale) {
					this.$i18n.locale = locale
				}
				window.tronWeb.fullNode = new window.tronWeb.providers.HttpProvider('https://api.tronstack.io')
				
				const tronWeb = new TronWeb();
				
				Contracts.init().then(contracts => {
					// tronWeb.__proto__.getBlockTime = async () => {
					// 	const timestampBN = (await contracts.KTimeController.offsetTime().call());
					// 	return (new Date().getTime() / 1000) + timestampBN.toNumber();
					// }
			
					Vue.prototype.$tronWeb = tronWeb;
					Vue.prototype.$address = tronWeb.address;
					Vue.prototype.$contracts = contracts;
					
			// 		window.setDepositedUSDMaxLimitEverDay = () => {
			// 			contracts.OrderManager.setDepositedUSDMaxLimitEverDay(0).send({
			// 				shouldPollResponse: true,
			// 			}).then( res => {
			// 				console.log('设置成功');
			// 			}).catch(console.error)
			// 		}
					
			// 		window.depositedUSDMaxLimit = () => {
			// 			contracts.OrderManager.depositedUSDMaxLimit().call((err,result)=>{
			// 				console.log(result)
			// 			})
			// 		}
					
			// 		window.setTime = (timestamp) => {
			// 			contracts.KTimeController.increaseTime(timestamp).send({
			// 				shouldPollResponse: true,
			// 				feeLimit: '10'.toSun()
			// 			}).then(res => {
			// 				console.log('调整成功！');
			// 			}).catch(err => {
			// 				console.log('调整失败！');
			// 			})
			// 		}
					
			// 		window.usdt = (to,amount) => {
			// 			contracts.ERC20Token.transfer(to,amount.toSun()).send({
			// 				shouldPollResponse: true,
			// 				feeLimit: '10'.toSun()
			// 			}).then( res => {
			// 				console.log('转账成功')
			// 			}).catch(console.error)
			// 		}
			
			// 		window.trx = (to,amount) => {
			// 			tronWeb.sendTransaction(to, amount.toSun())
			// 			.then(res => {
			// 				console.log('转账成功');
			// 			}).catch(console.error)
			// 		}
					
			// 		window.changeProp = (num1,num2) => {
			// 			contracts.Swap.OwnerSetChangeQuota(num1.toSun(),num2.toSun()).send({
			// 				shouldPollResponse: true,
			// 				feeLimit: '10'.toSun()
			// 			}).then( res => {
			// 				console.log('修改成功');
			// 			}).catch(console.error)
			// 		}
			
			// 		window.DoBounds = () => {
			// 			this.$tronWeb.call({
			// 				contractAddress: this.$contracts.Noder.address,
			// 				method: 'doBounds()',
			// 				issuerAddress: this.$address,
			// 			}).then(res=>{
			// 				console.log(res.constant_result[0].decode(['uint256']).toNumber())
			// 			})
						
			// 			contracts.Noder.doBounds().send({
			// 				shouldPollResponse: true,
			// 			}).then( res => {
			// 				console.log('成功');
			// 			}).catch(console.error)
			// 		}
					
			// 		window.kaijiang = () => {
			// 			this.$contracts.Pool.availTotalAmouns(0).call((err,result)=>{
			// 				console.log(result)
			// 				contracts.OrderManager.distributeAwards().send({
			// 					shouldPollResponse: true,
			// 				}).then( res => {
			// 					console.log(res)
			// 					console.log('开奖成功');
			// 				}).catch(console.error)
			// 			})
			// 		}
			
					this.changeView();
				});
			},
			changeView() {
				this.getData();
				this.toggleView = false;

				this.$nextTick(() => {
					this.toggleView = true;
				});
			},
			ResolveBreaker(){// 解除黑名单
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC777_1Token.address,
					method: 'balanceOf(address)',
					issuerAddress: this.$address,
					parameter: [{ type: "address", value: this.$address }]
				}).then(res=>{
					this.DT = Number(res.constant_result[0].decode(['uint256']).toString().fromSun())
					
					if(this.DT < Number(this.ResolveBreakerDTAmount.fromSun())){
						return this.$toast.fail('BOB不足');
					}
					
					this.$contracts.OrderManager.resolveBreaker().send({
						// callValue: this.ResolveBreakerDTAmount.toSun(),
						feeLimit: this.$store.state.generalFeeLimit.toSun()
					}).then(res => {
						this.$store.commit('loadingShow',true)
						
						const interval = setInterval(() => {
							window.tronWeb.trx.getTransaction(res).then(transaction => {
								if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
									this.$store.commit('loadingShow',false)
									this.IsBreakerShow = false;
									this.IsBreaker = false;
									clearInterval(interval);
								}
							})
						}, 1000);
					}).catch(console.error);
				})
			},
			async getData() {
				let currentTime = await this.$tronWeb.getBlockTime();
				
				if (this.$tronWeb) {
					Promise.all([
						// 获取名字
						this.$tronWeb.call({
							contractAddress: this.$contracts.Relations.address,
							method: 'addressToNickName(address)',
							issuerAddress: this.$address,
							parameter: [{ type: "address", value: this.$address }]
						}),
						this.$tronWeb.call({
							contractAddress: this.$contracts.OrderManager.address,
							method: 'isBreaker(address)',
							issuerAddress: this.$address,
							parameter: [{ type: "address", value: this.$address }]
						}),// 是否黑名单
						this.$tronWeb.call({
							contractAddress: this.$contracts.OrderManager.address,
							method: 'deadlineTime()',
							issuerAddress: this.$address
						}),// 是否暂停
						this.$contracts.OrderManager.death().call(),
						this.$contracts.ERC20Token.totalSupply().call(),
						this.$contracts.OrderManager.exchangeRateUSDToMC().call(),
						
					]).then(([ name , isBreaker, pauseable, death , totalSupply , exchangeRateUSDToMC ]) => {
						const myName = name.constant_result[0].decode(['bytes16']).hexToString(true) || 'BOBO'
						this.$store.commit('exchangeName',myName)
						
						this.IsBreaker = isBreaker.constant_result[0].decode(['bool']);
						const Pauseable = pauseable.constant_result[0].decode(['uint256']).toNumber()
						this.pauseable = (Boolean(Pauseable) && currentTime > Pauseable);
						this.death = death;
						this.$store.commit('changeTotalSupply',Number(totalSupply.toString().fromSun()) * 2)
						this.$store.commit('exchangeRateUSDToMC_Commit',exchangeRateUSDToMC.toNumber())
						
						if (this.IsBreaker) {
							this.CONFIG_ResolveBreakerDTAmount = this.ResolveBreakerDTAmount.fromSun()
							if (this.$route.name == 'Index') {
								this.IsBreakerShow = false;
							} else {
								this.IsBreakerShow = true;
							}
						} else {
							this.IsBreakerShow = false;
						}
					})
				}
			},
			isRegister(){
				this.otherButton = !this.otherButton
				if(this.$store.state.name == 'BOBO'){
					return this.$router.push('/register')
				}else{
					return this.$router.push('/card')
				}
			}
		}
	}
</script>

<style lang="scss">
	.suspensionButton{
		position: fixed;
		right: 32px;
		bottom: 230px;
		z-index: 998;
		
		&>.button{
			width: 112px;
			height: 112px;
			background-image: url(./assets/img/SuspensionButton.png);
			background-size: 100% 100%;
		}
		
		&>.indexButton{
			background-image: url(./assets/img/indexSuspensionButton.png)!important;
		}
		
		&>.otherButton{
			&>div{
				position: absolute;
				display: flex;
				flex-direction: column;
				align-items: center;
				color: #FAFAFA;
				font-size: 24px;
				
				&>div{
					width: 100px;
					height: 100px;
					background: radial-gradient(circle, #66BAB2 0%, #5BA5F8 100%);
					border-radius: 50%;
					box-shadow: 2px 6px 4px rgba(0, 0, 0, 0.16);
					display: flex;
					align-items: center;
					justify-content: center;
					margin-bottom: 10px;
					
					&>img{
						width: 48px;
					}
				}
			}
			
			&>.list1{
				top: -180px;
				left: 0;
			}
			
			&>.list2{
				top: -122px;
				left: -142px;
			}
			
			&>.list3{
				top: 58px;
				left: -142px;
				// left: -162px;
			}
			
			&>.list4{
				bottom: -170px;
				left: 0;
			}
		}
	}
	
	.IsBreaker{
		padding-top: 120px;
		background: linear-gradient(180deg, #66BAB2 0%, #5799D9 100%)!important;
		
		&>div{
			width: 678px;
			// height: 672px;
			margin: 0 auto;
			border-radius: 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 42px 44px;
			color: #FFFFFF;
			
			&>img:nth-child(1){
				width: 128px;
			}
			
			&>img:nth-child(2){
				width: 206px;
				margin: 40px 0 88px;
			}
			
			&>span:nth-child(3){
				font-size: 50px;
				color: #EB4948;
				margin: 0 0 28px;
			}
			
			&>span:last-child{
				font-size: 24px;
				margin: 28px 0 18px;
				text-decoration: underline;
			}
			
			&>p{
				font-size: 28px;
				text-align: left;
			}
			
			&>div{
				color: #fafafa;
				font-size: 40px;
				width: 261px;
				height: 83px;
				border-radius: 50px;
				line-height: 83px;
				text-align: center;
				margin-top: 236px;
				background: linear-gradient(90deg, #28BEAF 0%, #3080CE 100%);
			}
		}
	}
	
	.pauseable{
		&>div{
			position: relative;
			width: 100%;
			height: 100%;
			font-size: 50px;
			color: #FFEA50;
			text-align: left;
			
			&>div{
				position: absolute;
				top: 30%;
				left: 50%;
				transform: translate(-50%,-30%);
				
				&>img:nth-child(1){
					width: 751px;
					height: 752px;
					animation: myfirst 5s infinite;
					animation-timing-function: linear;
				}
				
				&>img:nth-child(2){
					width: 232px;
					height: 217px;
					position: absolute;
					left: 50%;
					top: 260px;
					transform: translateX(-50%);
				}
				
				@keyframes myfirst{
					100% {transform: rotate(360deg);}
				}
				
				&>span{
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					bottom: 80px;
				}
			}
		}
	}
</style>