<template>
	<div class="exchange">
		<pubilc-title :text="title" :rightShow="true" :rightText="SwapRecords" :rightClick="()=>{$router.push('exchangeRecord')}"></pubilc-title>
		<div class="content">
			<div class="contentBox">
				<div class="exchangeTitle">{{$t('SwapPeriod')}}：{{roundID}}</div>
				<div class="exchangeProp">
					<span>1:{{prop}}</span>
					<span>{{Remaining}} BOC</span>
				</div>
				<div class="exchangeSchedule">
					<div :style="'width:'+RemainingProp+'%'">
						<div>{{RemainingProp}}%</div>
					</div>
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Send')" v-model="value" @input="input" />
					<div>
						<img src="../assets/img/usdtLogo.png" />
						<span>USDT</span>
					</div>
				</div>
				<div class="exchangeBalance">
					{{$t('Available')}} {{usdt}} USDT
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Received')" disabled="disabled" v-model="exchangeDT" />
					<div>
						<img src="../assets/img/bbtLogo.png" />
						<span>BOC</span>
					</div>
				</div>
				<button @click="exchange" v-ButClickLimit>{{$t('LightningExchange')}}</button>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState , mapActions } from 'vuex'
	export default{
		data() {
			return {
				// show: false
				value: '',
				exchangeDT: '',
				info: '',
				prop: '',
				usdt: 0,
				total: '',
				current: '',
				RemainingProp: 0,
				list: [],
				Remaining: 0,
				roundID: '',
				NoRecord: this.$t('NoRecord'),
				exchangeLimit: 0,
				title: this.$t('swap'),
				SwapRecords: this.$t('SwapRecords'),
				exchangeRules: false,
				USDTAuthorizedAccess: 0,
			}
		},
		computed: {
			...mapState(['exchangeSuccess']),
		},
		methods:{
			async init(){
				this.Log_Swaped()
				
				// usdt
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'balanceOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					this.usdt = result.constant_result[0].decode(['uint256']).toString().fromSun();
				})
				
				// usdt授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [{ type: "address", value: this.$address },{ type: "address", value: this.$contracts.Swap.address }]
				}).then( result => {
					this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toNumber().fromSun()
				})
				
				this.info = (await this.$tronWeb.call({
					contractAddress: this.$contracts.Swap.address,
					method: 'swapInfo()',
				}).then()).constant_result[0].decode(['uint256','uint256','uint256','uint256'],['roundID','total','current','prop'])
		
				if(Number(this.info.roundID) < 10){
					this.roundID = "00"+this.info.roundID
				}else if(Number(this.info.roundID) < 100){
					this.roundID = "0"+this.info.roundID
				}else{
					this.roundID = this.info.roundID
				}
				
				// this.prop = this.info.prop.toString().fromSun()
				this.prop = this.info.prop.mul('1'.toSun()).toString().fromSun()
				this.total = this.info.total.toString().fromSun()
				this.current = this.info.current.toString().fromSun()
				this.Remaining = this.info.total.sub(this.info.current).toString().fromSun()
				this.RemainingProp = this.Remaining.toBN().multipliedBy(100).div(this.total).toString().toFixed(2)
				
				// console.log(this.info.total.sub(this.info.current).div(this.info.total).toNumber())
				// this.prop = this.$fromWei(this.$BN(this.info.prop).mul(this.$BN(this.$toWei('1','Mwei'))).toString())
				// this.total = this.$fromWei(this.info.total)
				// this.current = this.$fromWei(this.info.current)
				// this.Remaining = this.$BN(this.total).sub(this.$BN(this.current)).toNumber()
				// this.RemainingProp = this.$BN(this.Remaining * 100).div(this.$BN(this.total)).toNumber()
				
				this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrders(address,uint256,uint256,uint256)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: 65535 },
						{ type: "uint256", value: 0 },
						{ type: "uint256", value: 0 }
					]
				}).then( res => {
					let result = res.constant_result[0].decode(
						['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
						['total', 'len', 'orderIds', 'totalAmounts', 'states', 'times']
					);
					
					if(result.total.toNumber()){
						// 兑换上限
						this.$tronWeb.call({
							contractAddress: this.$contracts.Swap.address,
							method: 'exchangeLimit(address)',
							parameter: [{ type: "address", value: this.$address }]
						}).then(result => {
							this.exchangeLimit = result.constant_result[0].decode(['uint256']).toString().fromSun()
							this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
						})
					}else{
						this.$contracts.swap.nomalAddressQuota().call((err,result)=>{
							this.exchangeLimit = result.toNumber().fromSun()
							this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
						})
					}
				}).catch(err=>{
					this.$contracts.Swap.nomalAddressQuota().call((err,result)=>{
						this.exchangeLimit = result.toNumber().fromSun()
						this.exchangeLimit = Number(this.exchangeLimit) * Number(this.prop)
					})
				})
			},
			input() {
				if(this.value == '' || this.value == 0){
					this.exchangeDT = ''
					return 
				}
				const value = this.value.toString();
				this.exchangeDT = value * this.prop;
			},
			async exchange() {
				this.$store.commit('changeExchangeSuccess',false)
				
				var value = Number(this.value)
				
				if(value == '' || value == 0){
					return this.$toast.fail(this.$t('PleaseEnterTheTransferAmount'))
				}else if(value > this.usdt){
					return this.$toast.fail(this.$t('InsufficientBalance'))
				}else if(this.exchangeDT < 1){
					return this.$toast.fail(this.$t('LessThanTheMinimumLimit'))
				}else if(this.exchangeDT > this.exchangeLimit){
					return this.$toast.fail(this.$t('RedeemableLimitExceeded'))
				}else if(this.exchangeDT > this.Remaining){
					return this.$toast.fail(this.$t('ExcessRemaining'))
				}else if(value > this.USDTAuthorizedAccess){
					this.$store.commit('changeNeedAuthorization',value)
					return this.$store.commit('changeAuthorizedAccess',true)
				}
				
				// var code = (await this.$tronWeb.call({
				// 	contractAddress: this.$contracts.Swap.address,
				// 	method: 'doswaping(uint256)',
				// 	issuerAddress: this.$address,
				// 	parameter: [{ type: "uint256", value: this.value.toSun() }]
				// }).then())
				// if (code.DoswapingError == '1') {
				// 	this.$store.commit('changeNeedAuthorization',{
				// 		value: value
				// 	})
				// 	return this.$store.commit('changeAuthorizedAccess')
				// }
		
				this.$contracts.Swap.doswaping(this.value.toSun()).send({
					feeLimit: this.$store.state.generalFeeLimit.toSun()
				}).then(res => {
					this.$store.commit('loadingShow',true)
					
					const interval = setInterval(() => {
						window.tronWeb.trx.getTransaction(res).then(transaction => {
							if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
								this.$store.commit('loadingShow',false)
								this.init();
								this.$toast.success(this.$t('SwapSuccess'));
								this.value = '';
								this.exchangeDT = '';
								clearInterval(interval);
							}
						})
					}, 1000);
				}).catch(console.error);
			},
			Log_Swaped(){
				this.list = [];
		
				// this.$tronWeb.event({
				// 	contractAddress: this.$contracts.swap.address,
				// 	method: 'Log_Swaped'
				// }).then(res => {
				// 	for(let i = 0; i < res.length ; i++){
				// 		if(window.tronWeb.address.fromHex(res[i].result.owner) != this.$address) continue
				// 		this.list.unshift({
				// 			inAmount: res[i].result.inAmount.fromSun(),
				// 			outAmount: res[i].result.outAmount.fromSun(),
				// 			time: this.$toTime(res[i].result.time,'Y-M-D H:i'),
				// 		})
				// 	}
				// })
			}
		},
		mounted() {
			// this.$tronWeb && this.exchangeData && this.init()
			this.$tronWeb && this.init()
			// var fromBlock = (await this.$web3.eth.getBlock('latest').then()).number;
			// if(fromBlock - 7200 < 0){
			// 	fromBlock = 0
			// }else{
			// 	fromBlock -= 7200
			// }
			// var address = this.$address
			// this.$contract.getData('swapWs').events.Log_Swaped({
			// 	fromBlock: fromBlock,
			// 	filter: {owner: address}
			// }, (error, event)=>{ })
			// .on('data', async event => {	
			// 	this.list.unshift({
			// 		inAmount: this.$fromWei(event.returnValues.inAmount,'','Mwei'),
			// 		outAmount: this.$fromWei(event.returnValues.outAmount),
			// 		time: this.$toTime(event.returnValues.time,'Y-M-D H:i'),
			// 	})
			// })
			// .on('changed', function(event){
			// })
			// .on('error', console.error);
		},
		watch:{
			exchangeSuccess(){
				if(this.exchangeSuccess){					
					this.$tronWeb.call({
						contractAddress: this.$contracts.ERC20Token.address,
						method: 'allowance(address,address)',
						parameter: [{ type: "address", value: this.$address },{ type: "address", value: this.$contracts.Swap.address }]
					}).then( result => {
						this.USDTAuthorizedAccess = result.constant_result[0].decode(['uint256']).toNumber().fromSun()
						this.exchange()
					})
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.contentBox{
		padding: 32px 44px 48px;
		text-align: center;
		color: #666;
		
		.exchangeTitle{
			font-size: 30px;
			margin-bottom: 22px;
			padding-bottom: 32px;
			border-bottom: 2px solid #AFAFAF;
			text-align: left;
		}
		
		.exchangeProp{
			font-size: 32px;
			display: flex;
			justify-content: space-between;
		}
		
		.exchangeSchedule{
			width: 100%;
			height: 36px;
			border-radius: 10px;
			background-color: #E8E8E8;
			position: relative;
			margin: 10px 0 36px;
			
			&>div{
				width: 0%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background: linear-gradient(90deg, #28BEAF 0%, #3080CE 100%);
				border-radius: 10px;
				
				&>div{
					position: absolute;
					color: #fff;
					font-size: 24px;
					left: 10px;
					top: 50%;
					transform: translateY(-50%);
					text-align: left;
				}
			}
		}
		
		.exchangeInput{
			display: flex;
			width: 100%;
			height: 112px;
			font-size: 36px;
			background-color: #E8E8E8;
			// border: 2px solid #4d6784;
			border-radius: 10px;
			
			&>input{
				width: 381px;
				height: 112px;
				background-color: transparent;
				border: none;
				padding-left: 30px;
			}
			
			&>div{
				flex: 1;
				height: 108px;
				border-left: 2px solid #dededf;
				display: flex;
				align-items: center;
				padding-left: 20px;
				
				&>img{
					width: 43px;
					margin-right: 34px;
				}
			}
		}
		
		.exchangeBalance{
			font-size: 30px;
			text-align: left;
			margin: 30px 0;
		}
		
		&>button{
			width: 400px;
			height: 96px;
			border-radius: 10px;
			font-size: 40px;
			color: #fff;
			border: none;
			margin-top: 60px;
			box-shadow: 0 4px 12px rgba(#000, 0.16);
			background: linear-gradient(90deg, #28BEAF 0%, #3080CE 100%);
		}
	}
</style>
