<script>
	import { Rate , Popup , Icon , Overlay } from 'vant';
	import Order from "@/common/Order.json"

	export default {
		components: { Icon },
		data(){
			return {
				title: this.$t('HistoricalOrder'),
				active: 0,
				ProvideHelpList: [],
				orderTotalNumber: 0,
			}
		},
		methods:{
			async init(){
				// 循环出所有type为0的订单
				this.getOrderTotal().then(result=>{
					this.orderTotal = result
					for(var i = this.orderTotal;i >= 0;i-=10){
						this.cretaOrder(i)
					}
				});
				
				// 最新块的时间
				var currentTime = await this.$tronWeb.getBlockTime();
				this.times = setInterval(()=>{
					currentTime++;
					this.ProvideHelpList.forEach((item,index)=>{
						if(item.state == '2'){
							if(currentTime == 0 || currentTime > item.OnCountDownEnd){
								this.$set(this.ProvideHelpList[index],'timesDown','00:00:00:00')
								clearInterval(this.times)
								return 
							}else{
								var timesDown = this.timeDown(item.OnCountDownEnd,currentTime);
								this.$set(this.ProvideHelpList[index],'timesDown',timesDown)
							}
						}
					})
				},1000)
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
			async getOrderTotal(type){ //获取订单最高偏移量
				// 订单总量
				// var orderTotal = (await this.$contract.getData('controller').methods.QueryOrders(this.$address,type,255,0,10).call()).total;				
				const order = (await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrders(address,uint256,uint256,uint256)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: 65535 },
						{ type: "uint256", value: 0 },
						{ type: "uint256", value: 0 }
					],
				})).constant_result[0].decode(
					['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
					['total', 'len', 'id', 'totalAmounts', 'states', 'times']
				);
				
				const orderTotal = order.total.toNumber()
				this.orderTotalNumber += Number(orderTotal)
				if(Number(orderTotal) <= 10){
					var maxOffset = 0
				}else if(Number(orderTotal) % 10 == 0){
					var maxOffset = (Number(orderTotal.toString().substr(0,1)) - 1) * 10
				}else{
					var maxOffset = Number(orderTotal.toString().substr(0,1)) * 10
				}
				return maxOffset
			},
			async cretaOrder(offset){
				// 订单
				const QueryOrders = (await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrders(address,uint256,uint256,uint256)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: 65535 },
						{ type: "uint256", value: offset },
						{ type: "uint256", value: 10 }
					],
				})).constant_result[0].decode(
					['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
					['total', 'len', 'id', 'totalAmounts', 'states', 'times']
				);
				
				for(var key = (QueryOrders.len.toNumber() - 1); key >= 0 ; key--){
					var orderState = QueryOrders.states[key]
					
					const orderTimes = (await this.$tronWeb.call({
						contractAddress: this.$contracts.OrderManager.address,
						method: 'orderTimes(uint256)',
						issuerAddress: this.$address,
						parameter: [{ type: "uint256", value: QueryOrders.id[key].toString() }]
					}).then()).constant_result[0].decode(
						['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
						['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
					)
					
					const OnCreated = orderTimes.OnCreated.toNumber()
					const OnCountDownStrat = orderTimes.OnCountDownStart.toNumber()
					const OnCountDownEnd = orderTimes.OnCountDownEnd.toNumber()
					const CurrentProfitInfoTime = orderTimes.OnUnfreezing.toNumber()
					
					// usdt
					// var usdt = (await this.$contracts.ERC20Token.balanceOf(QueryOrders.orders[key]).call()).toString().fromSun();
					// 最新块的时间
					var currentTime = await this.$tronWeb.getBlockTime();
					
					// 是否完成排队
					const CurrentProfitInfo = (CurrentProfitInfoTime && currentTime >= CurrentProfitInfoTime)
					
					var totalAmount = QueryOrders.totalAmounts[key].toString().fromSun()
					
					if(currentTime > OnCountDownStrat && orderState == 2){
						var OrderCountdownStarts = true;
					}else{
						var OrderCountdownStarts = false;
					}
					
					if(orderState == 2 || orderState == 4 || orderState == 6){
						var isComplete = false
					}else{
						var isComplete = true
					}
					
					if(orderState == 6 || orderState == 7 || (orderState == 5 && CurrentProfitInfo)){
						var type = 2
					}else if(orderState == 2 || (orderState == 5 && !CurrentProfitInfo)){
						var type = 1
					}
					
					this.ProvideHelpList.push({
						order: QueryOrders.id[key],
						totalAmount: totalAmount,
						state: orderState,
						// currentScale: currentScale,
						cretaTime: OnCreated,
						timesDown: '00:00:00:00',
						OnCountDownEnd: OnCountDownEnd,
						CurrentProfitInfo: CurrentProfitInfo,
						OrderCountdownStarts: OrderCountdownStarts,
						type: type,
						isComplete: isComplete,
						totalOrder: QueryOrders.total.toNumber()
					})

					this.ProvideHelpList.sort(function(a,b){
						return b.cretaTime - a.cretaTime
					})
				}
					
			},
		},
		mounted() {
			this.$tronWeb && this.init()
		},
	}
</script>

