<template>
	<div class="content">
		<pubilc-title :text="title"></pubilc-title>
		<div class="contentBox">
			<!-- <p>{{ isInCountdowning ? $t('LuckyPrizeCountdown') : $t('Unopened') }}</p> -->
			<div class="luckyContent">
				<div>{{deadlineTimes.substr(0,1)}}</div>
				<div>{{deadlineTimes.substr(1,1)}}</div>
				<p>:</p>
				<div>{{deadlineTimes.substr(3,1)}}</div>
				<div>{{deadlineTimes.substr(4,1)}}</div>
				<p>:</p>
				<div>{{deadlineTimes.substr(6,1)}}</div>
				<div>{{deadlineTimes.substr(7,1)}}</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				isInCountdowning: false,
				deadlineTimes: '00:00:00',
				title: '',
			}
		},
		mounted() {
			this.$tronWeb && this.init()
		},
		methods:{
			async init(){
				// 是否开启倒计时
				this.$contracts.OrderManager.isInCountdowning().call((err,result)=>{
					this.isInCountdowning = result
					this.isInCountdowning ? this.title = this.$t('LuckyPrizeCountdown') : this.title = this.$t('Unopened')
				})
				
				let currentTime = await this.$tronWeb.getBlockTime();
				
				this.$contracts.OrderManager.deadlineTime()
				.call((err, deadlineTime) => {
					if (!err) {
						this.deadlineTime = deadlineTime.toNumber();
						
						if(currentTime < this.deadlineTime){
							var deadlineTime = setInterval(()=>{
								currentTime++;
								this.deadlineTimes = this.timeDown(
									this.deadlineTime,
									currentTime
								);
							}, 1000);
						}
					}
				})
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
		}
	}
</script>

<style lang="scss" scoped>
	.contentBox{
		width: 100%;
		height: 158px;
		margin: 36px 0 50px;
		box-shadow: 0px 2px 4px rgba(#000, 0.16);
		border-radius: 20px;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 34px 0 48px;
		
		&>.luckyContent{
			border-radius: 10px;
			font-size: 68px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-weight: bold;
			
			&>div{
				width: 66px;
				height: 104px;
				background-color: #66BAB2;
				border-radius: 10px 7px 10px 7px;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 10px;
			}
			
			&>p{
				color: #66BAB2;
				margin-right: 10px;
			}
		}
		
		&>p{
			font-size: 32px;
		}
	}
</style>
