<template>
	<div class="currentOrder">
		<pubilc-title :text="title" :rightShow="true" :rightText="More" :rightClick="()=>{$router.push('/allOrder')}"></pubilc-title>
		<div class="content">
			<div class="MyHelpListBox">
				<div class="noCurrentOrder"  v-if="!ProvideHelpList.length">
					<img src="@/assets/img/noCurrentOrder.png" />
					<span>{{$t('CurrentlyNoOrders')}}</span>
				</div>
				<div class="MyHelpList" v-for="(item,index) in ProvideHelpList" :key="index" @click="$router.push({path:'/orderDetails',query:{order: item.order,total: item.total}})">
					<div class="MyHelpListBoxContentRight" :class="{
						red: (item.state == '2' || (item.state == '5' && !item.CurrentProfitInfo)) || item.state == '4',
						bule: (item.type == 3 && item.state == '6') || (item.state == '5' && item.CurrentProfitInfo) || item.state == '8',
					}">
						<div class="MyHelpListBoxContentRightRate">
							<div>
								<div class="left">
									<img v-if="(item.state == '2' || (item.state == '5' && !item.CurrentProfitInfo))" src="@/assets/img/orderState01.png"/>
									<img v-else-if="(item.state == '5' && item.CurrentProfitInfo) || item.state == '8'" src="@/assets/img/orderState02.png"/>
			
									<span v-if="item.state == '2' || (item.state == '5' && !item.CurrentProfitInfo)">{{$t('ProvideHelp')}}</span>
									<span v-else-if="(item.state == '5' && item.CurrentProfitInfo) || item.state == '8'">{{$t('IncomeCollection')}}</span>
								</div>
								
								<div class="right" v-if="item.OrderCountdownStarts">
									<span>{{$t('PerformCountdown')}}</span>
									<div>
										<div>{{item.timesDown.substr(0,1)}}</div>
										<div>{{item.timesDown.substr(1,1)}}</div>
										<p>:</p>
										<div>{{item.timesDown.substr(3,1)}}</div>
										<div>{{item.timesDown.substr(4,1)}}</div>
										<p>:</p>
										<div>{{item.timesDown.substr(6,1)}}</div>
										<div>{{item.timesDown.substr(7,1)}}</div>
									</div>
								</div>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightContent">
							<div>
								<div class="MyHelpListBoxContentRightCircle">
									<icon name="success" class="active"></icon>
									<div :style="'width:'+item.currentScale+'%'" :class="{yellowBackground:(item.state == '5' && item.CurrentProfitInfo) || item.state == '8'}"></div>
									<div :class="{yellowBorder:(item.state == '5' && item.CurrentProfitInfo) || item.state == '8'}"></div>
								</div>
								<div class="MyHelpListBoxContentRightTotal">
									{{item.totalAmount}}
									<span>USDT</span>
								</div>
							</div>
							<div class="MyHelpListBoxContentRightToFrom colorA1BDE1">
								<span>{{$store.state.name}}</span>
								<img :class="{rotate180:(item.state == '5' && item.CurrentProfitInfo)}" src="@/assets/img/send.png" />
								<span>Contract</span>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightButton">
							<div>
								<span>{{$toTime(item.cretaTime,'Y/M/D H:i')}}</span>
							</div>
							<div class="green">{{$t('Details')}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Order from "@/common/Order.json"
	import { Icon } from 'vant'
	export default{
		components:{
			Icon
		},
		data(){
			return {
				ProvideHelpList: [],
				title: this.$t('CurrentOrder'),
				More: this.$t('More')
			}
		},
		methods:{
			init(){				
				this.QueryOrders(0, 255, 0, 10).then(({ total }) => {
					const totalNum = total.toNumber();
					if (totalNum !== this.ProvideHelpList.length) {
						this.ProvideHelpList = [];
						const maxOffset = totalNum <= 10 ? 0 : totalNum % 10 == 0 ? (Number(total.toString().substr(0, 1)) - 1 * 10) : Number(total.toString().substr(0, 1)) * 10;
						const promises = [];
						for (let i = maxOffset; i >= 0; i -= 10) {
							promises.push(this.QueryOrders(0, 255, i, 10));
						}
						Promise.all(promises).then(async QueryOrders => {
							for (const { len, states, totalAmounts, times , id , total } of QueryOrders) {
								let currentTime = 0;
								for (let i = len.toNumber() - 1; i >= 0; i--) {
									const orderState = states[i];
									
									if ([2,5,8].includes(orderState)) {
										const totalAmount = totalAmounts[i].toString().fromSun();
										
										if([2].includes(orderState)){
											var currentScale = 50
										}else{
											var currentScale = 100;
										}
										
										currentTime = await this.$tronWeb.getBlockTime();
										const orderTimes = (await this.$tronWeb.call({
											contractAddress: this.$contracts.OrderManager.address,
											method: 'orderTimes(uint256)',
											issuerAddress: this.$address,
											parameter: [{ type: "uint256", value: id[i] }]
										}).then()).constant_result[0].decode(
											['uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256','uint256'],
											['OnCreated','OnPaymentFrist','OnPaymentSuccess','OnProfitingBegin','OnCountDownStart','OnCountDownEnd','OnConvertConsumer','OnUnfreezing','OnDone']
										)

										const OnCreated = orderTimes.OnCreated.toNumber()
										const OnCountDownStrat = orderTimes.OnCountDownStart.toNumber()
										const OnCountDownEnd = orderTimes.OnCountDownEnd.toNumber()
										const CurrentProfitInfoTime = orderTimes.OnUnfreezing.toNumber()
										
										if(currentTime > OnCountDownStrat && orderState == 2){
											var OrderCountdownStarts = true;
										}else{
											var OrderCountdownStarts = false;
										}
										
										this.ProvideHelpList.push({
											order: id[i],
											totalAmount: totalAmount,
											state: orderState,
											cretaTime: OnCreated,
											timesDown: '00:00:00:00',
											OnCountDownEnd: OnCountDownEnd,
											CurrentProfitInfo: (CurrentProfitInfoTime && currentTime >= CurrentProfitInfoTime),
											OrderCountdownStarts: OrderCountdownStarts,
											total: total.toNumber(),
											currentScale: currentScale,
										});
									}
								}
				
								this.times = setInterval(()=>{
									currentTime++;
									this.ProvideHelpList.forEach((item,index)=>{
										if(item.state == '2'){
											if(currentTime == 0 || currentTime > item.OnCountDownEnd){
												this.$set(this.ProvideHelpList[index],'timesDown','00:00:00:00')
												clearInterval(this.times)
												return 
											}else{
												var timesDown = this.timeDown(item.OnCountDownEnd,currentTime);
												this.$set(this.ProvideHelpList[index],'timesDown',timesDown)
											}
										}
									})
								},1000)
							}
						}).catch(console.log)
					}
				}).catch(console.log)
			},
			async QueryOrders(orderType, orderState, offset, size, address = this.$address) {
				const order = await this.$tronWeb.call({
					contractAddress: this.$contracts.OrderManager.address,
					method: 'selectOrders(address,uint256,uint256,uint256)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "uint256", value: orderState },
						{ type: "uint256", value: offset },
						{ type: "uint256", value: size }
					],
				});
			
				return order.constant_result[0].decode(
					['uint256', 'uint256', 'uint256[]', 'uint256[]', 'uint8[]', 'uint96[]'],
					['total', 'len', 'id', 'totalAmounts', 'states', 'times']
				);
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
		},
		mounted() {
			this.$tronWeb && this.init()
		}
	}
</script>

